import { CalculatedChartStateType, ChartStateType, isIndicatorGraph } from "../Types/CChartStateTypes";
import { DataSeries, Dataset, isCandleChartDataset, isIndicatorDataset } from "../Types/CChartTypes";
import { getDecimals, isNullish } from "../utils";

export const pureYToPix = (y: number, pixYBottom: number, decimals: number, translatedY: number, pixPerPt: number) => {
  return pixYBottom - y * Math.pow(10, decimals) * pixPerPt + translatedY;
};
export const purePixToY = (
  pixY: number,
  pixYBottom: number,
  decimals: number,
  translatedY: number,
  pixPerPt: number
) => {
  return ((pixYBottom + translatedY - pixY) / pixPerPt) * Math.pow(10, -decimals);
};

export const snapPixYToPrices = (
  pixY: number,
  dataset: Dataset,
  subcharts: ChartStateType["subCharts"] | null,
  subchartIdx: number,
  yaxisIdx: number,
  snapTolerance?: number
) => {
  const snapTol = !!snapTolerance ? snapTolerance : 10;
  if (isNullish(pixY) || isNullish(subcharts) || isNullish(subchartIdx) || isNullish(yaxisIdx) || isNullish(dataset))
    return null;
  const prices = [];
  if (isIndicatorDataset(dataset)) {
    if (!!dataset.prices[0]) prices.push(dataset.prices[0]);
  } else {
    if (isCandleChartDataset(dataset)) prices.push(dataset.open, dataset.high, dataset.low, dataset.close);
    else prices.push(dataset.close);
  }
  if (prices.length === 0) return null;

  let snapRes: { y: string; pixY: number }[] = [];
  prices.forEach((price, priceIdx) => {
    const yPixPrice = pureYToPix(
      price,
      subcharts[subchartIdx].bottom,
      subcharts[subchartIdx].yaxis[yaxisIdx].decimals,
      subcharts[subchartIdx].yaxis[yaxisIdx].translatedY,
      subcharts[subchartIdx].yaxis[yaxisIdx].heightPerPt
    );
    if (yPixPrice >= pixY - snapTol && yPixPrice <= pixY + snapTol) {
      snapRes.push({ y: price.toFixed(getDecimals(price)), pixY: yPixPrice });
    }
  });

  if (!!snapRes)
    return snapRes.sort((a, b) => {
      if (Math.abs(a.pixY - pixY) < Math.abs(b.pixY - pixY)) return -1;
      else if (Math.abs(a.pixY - pixY) > Math.abs(b.pixY - pixY)) return 1;
      return 0;
    });
  return null;
  // for (let i = 0; i < prices.length; i++) {
  //   const price = prices[i];
  //   const yPixPrice = pureYToPix(
  //     price,
  //     subcharts[subchartIdx].bottom,
  //     subcharts[subchartIdx].yaxis[yaxisIdx].decimals,
  //     subcharts[subchartIdx].yaxis[yaxisIdx].translatedY,
  //     subcharts[subchartIdx].yaxis[yaxisIdx].heightPerPt
  //   );
  //   if (yPixPrice >= pixY - snapTol && yPixPrice <= pixY + snapTol) {
  //     snapRes.push({ y: price.toFixed(getDecimals(price)), pixY: yPixPrice });
  //   }
  // }
  // if (snapRes.length > 0) {
  // }
};

export function GetYOptInterval(max: number, min: number, dec: number, targetSteps: number) {
  let initIntervalStep = 1 / Math.pow(10, dec);
  let curIntervalStep = initIntervalStep;
  let optIntervalStep = curIntervalStep;
  const optimizationFunction = (steps: number) => Math.abs((max - min) / steps - targetSteps);
  let optimizer = optimizationFunction(curIntervalStep); // - targetSteps);

  let i = 0;
  while (curIntervalStep < max - min) {
    for (let j = 0; j <= 2; j++) {
      const factor = j === 0 ? 1 : j === 1 ? 2.5 : 5.0;
      let curOptimizerRes = optimizationFunction(curIntervalStep * factor);
      if (curOptimizerRes < optimizer) {
        optimizer = curOptimizerRes;
        optIntervalStep = curIntervalStep * factor;
      }
    }
    i++;
    curIntervalStep = initIntervalStep * Math.pow(10, i);
  }
  return parseFloat(optIntervalStep.toFixed(dec));
}

export const calculateCurrentGraphs = (
  subcharts: ChartStateType["subCharts"] | undefined,
  widthPerTick: number,
  xStart: number,
  xEnd: number,
  pixXstart: number,
  pixXend: number
): CalculatedChartStateType["subCharts"] | null => {
  if (!subcharts) return null;
  if (subcharts.length === 0) return null;

  const calculatedSubcharts = subcharts;

  calculatedSubcharts.forEach((subchart, subchartIdx) => {
    const mainYaxis: { pixY: number; label: string }[] = [];
    subchart.yaxis.forEach((yaxis, yaxisIdx) => {
      let yMinExactAllGraphs: number | null = null;
      let yMaxExactAllGraphs: number | null = null;
      let maxDecimals: number = 0;
      yaxis.graphs.forEach((graphM, graphIdx) => {
        const graph = calculatedSubcharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx];
        const data = graph.data;
        graph.datasets.last = {
          data: data[xEnd],
          x: xEnd,
          pixX: pixXend,
          dateString: "",
        };
        graph.datasets.first = {
          data: data[xStart],
          x: xStart,
          pixX: pixXstart,
          dateString: "",
        };
        const curData = graph.data.filter((val, idx) => idx >= xStart && idx <= xEnd) as DataSeries;
        graph.curData = curData;
        graph.yMaxExact =
          isIndicatorGraph(graph) && !!graph?.indicator?.default?.fixedYScale
            ? graph?.indicator?.default?.fixedYScale[1]
            : Math.max(
                ...curData.map((dataset) => {
                  if (isIndicatorDataset(dataset)) {
                    const max = Math.max(...(dataset.prices.filter((price) => price !== null) as number[]));
                    if (!max && max !== 0) return 0;
                    return max;
                  }
                  if (isCandleChartDataset(dataset)) return dataset.high;
                  return dataset.close;
                })
              );
        graph.yMinExact =
          isIndicatorGraph(graph) && !!graph?.indicator?.default?.fixedYScale
            ? graph?.indicator?.default?.fixedYScale[0]
            : Math.min(
                ...curData.map((dataset) => {
                  if (isIndicatorDataset(dataset)) {
                    const min = Math.min(...(dataset.prices.filter((price) => price !== null) as number[]));
                    if (!min && min !== 0) return 0;
                    return min;
                  }
                  if (isCandleChartDataset(dataset)) return dataset.low;
                  return dataset.close;
                })
              );
        if (yMinExactAllGraphs === null || graph.yMinExact < yMinExactAllGraphs) yMinExactAllGraphs = graph.yMinExact;
        if (yMaxExactAllGraphs === null || graph.yMaxExact > yMaxExactAllGraphs) yMaxExactAllGraphs = graph.yMaxExact;
        if (graph.decimals > maxDecimals) maxDecimals = graph.decimals;
      });

      // target scale interval should be 75px
      const intervalsTarget = Math.max(Math.round((subchart.bottom - subchart.top) / 75), 3);
      yMaxExactAllGraphs = yMaxExactAllGraphs || 0;
      yMinExactAllGraphs = yMinExactAllGraphs || 0;
      const decimals = maxDecimals;

      const optIntervalY = GetYOptInterval(yMaxExactAllGraphs, yMinExactAllGraphs, decimals, intervalsTarget);
      yaxis.optIntervalY = optIntervalY;

      let yMaxAllGraphs: null | number = null;
      let yMinAllGraphs: null | number = null;
      yaxis.graphs.forEach((graph, graphIdx) => {
        graph.yMax = Math.ceil(graph.yMaxExact / optIntervalY) * optIntervalY;
        graph.yMin = Math.floor(graph.yMinExact / optIntervalY) * optIntervalY;
        if (yMaxAllGraphs === null || graph.yMax > yMaxAllGraphs) yMaxAllGraphs = graph.yMax;
        if (yMinAllGraphs === null || graph.yMin < yMinAllGraphs) yMinAllGraphs = graph.yMin;
      });

      const heightPerPt =
        (subchart.bottom - subchart.top) / ((yMaxAllGraphs || 0) - (yMinAllGraphs || 0)) / Math.pow(10, decimals);
      yaxis.heightPerPt = heightPerPt;
      yaxis.translatedY = -pureYToPix(yMinAllGraphs || 0, subchart.bottom, decimals, 0, heightPerPt) + subchart.bottom;
      let y_bottom = purePixToY(subchart.bottom, subchart.bottom, decimals, yaxis.translatedY, yaxis.heightPerPt);
      let i = pureYToPix(
        Math.ceil(y_bottom / optIntervalY) * optIntervalY,
        subchart.bottom,
        decimals,
        yaxis.translatedY,
        heightPerPt 
      );
      const iStart = i; 
      while (i >= subchart.top) {
        let yi = purePixToY(i, subchart.bottom, yaxis.decimals, yaxis.translatedY, yaxis.heightPerPt);
        let yvalstr = (Math.round(yi * Math.pow(10, decimals)) / Math.pow(10, decimals)).toString();
        if (i>subchart.top+10 && i <subchart.bottom-10)
        mainYaxis.push({ pixY: i, label: yvalstr });
        i -= (heightPerPt * optIntervalY) / Math.pow(10, -decimals);
      }
      yaxis.drawTicks = mainYaxis;
    });
  });
  return calculatedSubcharts;
};
