import { Button, Typography } from "@material-ui/core";
import { mdiChartBellCurve, mdiChartLine } from "@mdi/js";
import Icon from "@mdi/react";
import React from "react";
import { ChartDataset, Dataset, isCandleChartDataset, isIndicatorDataset } from "./Types/CChartTypes";

export const CChartLabel = (props: {
  name: string;
  dataset: Dataset;
  decimals?: number;
  onClick?: (name?: string) => any;
  // top: number;
}) => {
  const { dataset, name, decimals, onClick } = props;
  if (!dataset) return null;

  return (
    <div >
      <Button
        style={{textTransform: "none", textAlign: "left"}}
        onClick={() => {
          onClick?.(name);
        }}
        startIcon={<Icon path={isIndicatorDataset(dataset) ? mdiChartBellCurve : mdiChartLine} size={1.0} color="#333" />}
      >
      {isIndicatorDataset(dataset) ? (
        <Typography>{`${name}:${dataset.prices.map((price) => price?.toFixed(decimals)).join(", ") || ""}`}</Typography>
      ) : (
        <Typography variant="body1">
          {isCandleChartDataset(dataset)
            ? `${name}
                O:${dataset.open.toFixed(decimals)}
                H:${dataset.high.toFixed(decimals)}
                L:${dataset.low.toFixed(decimals)}
                C:${dataset.close.toFixed(decimals)}`
            : `${name}
                C:${dataset.close.toFixed(decimals)}`}
        </Typography>
      )}
      </Button>
    </div>
  );
};
