const useStyles = makeStyles({
  popper: {
    zIndex: 1800,
  },
  tooltip: {
    background: "rgba(205, 205, 205, 0.95)",
    //   filter: "blur(1px)",
  },
});

export async function parseCsvFileObj(data: File) {
  const res: any = await data.text().then((dataString) => {
    // console.log(dataString);
    const rowDelimiter = [
      { chars: "\n", amt: dataString.match(/\n/gm)?.length ?? 0 },
      { chars: "\r", amt: dataString.match(/\r/gm)?.length ?? 0 },
      { chars: "\r\n", amt: dataString.match(/\r\n/gm)?.length ?? 0 },
    ];
    const rowDelimiterAmts = rowDelimiter.map((val) => val.amt);
    const maxAmtRowDelimiterIdx = rowDelimiterAmts.indexOf(Math.max(...rowDelimiterAmts));
    const guessedRowDelimiter = rowDelimiter[maxAmtRowDelimiterIdx].chars;
    const rows = dataString.split(guessedRowDelimiter);

    // only first line is checked for delimiter
    const delimiter = [
      { chars: ",", amt: rows[0].match(/,/gm)?.length ?? 0 },
      { chars: ";", amt: rows[0].match(/;/gm)?.length ?? 0 },
    ];
    const delimiterAmts = delimiter.map((del) => del.amt);
    const maxAmtDelimiterIdx = delimiterAmts.indexOf(Math.max(...delimiterAmts));
    const guessedDelimiter = delimiter[maxAmtDelimiterIdx].chars;

    // last delimiter is likely decimal delimiter
    let amtDecDel = { dots: 0, commas: 0 };
    rows.forEach((row) => {
      row.split(guessedDelimiter).forEach((val) => {
        const lastDot = val.lastIndexOf(".");
        const lastComma = val.lastIndexOf(",");
        if (lastDot !== -1 && lastComma !== -1) {
          if (lastDot > lastComma) amtDecDel.dots++;
          else amtDecDel.commas++;
        } else if (lastDot !== -1 && lastComma === -1) amtDecDel.dots++;
        else if (lastDot === -1 && lastComma !== -1) amtDecDel.commas++;
      });
    });
    const isCommaDecDel = amtDecDel.dots < amtDecDel.commas;
    const isCommaDigitSeparator = amtDecDel.dots > amtDecDel.commas && amtDecDel.commas > 0;

    const newDataString = isCommaDecDel
      ? dataString.replaceAll(".", "").replaceAll(",", ".")
      : isCommaDigitSeparator
      ? dataString.replaceAll(",", "")
      : dataString;

    // console.log(isCommaDecDel, isCommaDigitSeparator);
    // const parseNumbers = (val: string) => {
    //   if (typeof val !== "string") return val;
    //   if (isCommaDecDel) return parseFloat(val.replace(".", "").replace(",", "."));
    //   else if (isCommaDigitSeparator) return parseFloat(val.replace(",", ""));
    //   return parseFloat(val);
    // };

    let parseRes = parse(newDataString, {
      delimiter: guessedDelimiter,
      record_delimiter: guessedRowDelimiter,
      cast: true,
      trim: true,
      cast_date: true,
    }).map((dataset: any[]) => ({
      date: dataset[0],
      open: dataset[1],
      high: dataset[2],
      low: dataset[3],
      close: dataset[4],
      volume: dataset[5],
    }));
    parseRes.shift();
    return parseRes;
  });
  return res;
}

export const ChartMenuChart = (props: {
  ChartState: CalculatedChartStateType;
  //   onNavigate: (target: CChartMenuStateType) => void;
  ChartStateDispatch: ChartStateHookType[1];
}) => {
  const { ChartState, ChartStateDispatch } = props;
  const classes = useStyles();
  const [IsError, setIsError] = React.useState<string | null>(null);

  const theme = useTheme();
  const HiddenInputRef: React.RefObject<HTMLInputElement> = React.useRef(null);
  const [ShowUrlPopup, setShowUrlPopup] = React.useState(false);
  const [CsvUrl, setCsvUrl] = React.useState("");

  async function handleInputFileChange(evt: React.ChangeEvent<HTMLInputElement>) {
    const files = evt.target.files;
    if (!files) return;
    if (files.length > 0) {
      try {
        const res = await parseCsvFileObj(files[0]);
        ChartState.methods.addSubchart(res, "some chart", undefined, true);
      } catch (err) {
        console.log("Error - could not parse provided local file", err);
        setIsError("Error - could not parse provided local file");
      }
    }
  }

  // evt handler for KeyUp evt of Input/Textfield -> Callback onEnter
  const HandleOnKeyUp = (e: React.KeyboardEvent<HTMLInputElement>): void => {
    if (e.key === "Enter" && e.type === "keyup") {
      fetchCsvFromURL();
    }
  };
  async function fetchCsvFromURL() {
    try {
      const resp = await fetch(CsvUrl);
      if (!resp) throw Error("cant download file from url");
      let file = resp as unknown as File;
      const res = await parseCsvFileObj(file);
      ChartState.methods.addSubchart(res, "some chart", undefined, true);
    } catch (err) {
      console.log("Error - could not download file from URL or File could not be parsed", err);
      setIsError("Error - could not download file from URL or File could not be parsed");
    }
    // const data = fetch(CsvUrl).then((response) => {
    //   return await parseCsvFileObj(response);
    // });

    setShowUrlPopup(false);
  }

  return (
    <React.Fragment>
      <CSnackBar open={!!IsError} onClose={() => setIsError(null)} content={IsError ? IsError : ""}></CSnackBar>
      <input
        type="file"
        style={{ visibility: "hidden", position: "absolute", top: 0, height: 0, width: 0 }}
        ref={HiddenInputRef}
        onChange={handleInputFileChange}
      />
      <List>
        <ListItem
          button
          onClick={() => {
            HiddenInputRef?.current?.click?.();
          }}
        >
          <ListItemIcon
            style={{
              border: "1px solid #666",
              borderRadius: 5,
              marginRight: 10,
              background: theme.palette.secondary.main,
              minWidth: 48,
              height: 48,
            }}
          >
            <Icon path={mdiFileDelimitedOutline} size={2} color={theme.palette.secondary.contrastText} />
          </ListItemIcon>
          <ListItemText primary="CSV from device" primaryTypographyProps={{ variant: "h6", color: "textSecondary" }} />
        </ListItem>

        <Tooltip
          PopperProps={{ disablePortal: true }}
          classes={{ popper: classes.popper, tooltip: classes.tooltip }}
          style={{ zIndex: 1500 }}
          open={ShowUrlPopup}
          //   onClose={() => setShowUrlPopup(false)}
          title={
            <div>
              <TextField
                helperText="Enter/Paste URL"
                // style={{ width: 320 }}
                fullWidth
                onKeyUp={HandleOnKeyUp}
                value={CsvUrl}
                onChange={(e) => setCsvUrl(e.target.value)}
                InputProps={{
                  endAdornment: (
                    <React.Fragment>
                      <IconButton onClick={fetchCsvFromURL} size="small">
                        <Icon path={mdiArrowRightThick} size={1} />
                      </IconButton>
                      <IconButton onClick={() => setShowUrlPopup(false)}>
                        <Icon path={mdiClose} size={1} />
                      </IconButton>
                    </React.Fragment>
                  ),
                }}
              />
            </div>
          }
          interactive
          arrow
          placement="bottom"
          //   open={true}
          //   onClose={() => { }}
        >
          <ListItem
            button
            onClick={() => {
              // onNavigate?.("indicators");
              // HiddenInputRef?.current?.click?.();
              setShowUrlPopup(true);
            }}
          >
            <ListItemIcon
              style={{
                border: "1px solid #666",
                borderRadius: 5,
                marginRight: 10,
                background: theme.palette.secondary.main,
                minWidth: 48,
                height: 48,
              }}
            >
              <Icon path={mdiWeb} size={2} color={theme.palette.secondary.contrastText} />
            </ListItemIcon>
            <ListItemText primary="CSV from URL" primaryTypographyProps={{ variant: "h6", color: "textSecondary" }} />
          </ListItem>
        </Tooltip>
      </List>
    </React.Fragment>
  );
};
