import { Vector2 } from "react-use-gesture/dist/types";
import { getDateString } from "../Time/CChartDateTime";
import { getOptimalPeriod, isNewPeriod } from "../Time/CChartPeriod";
import {
  CalculatedChartStateType,
  ChartGraphStateType,
  ChartStateType,
  DrawingTool,
  InteractivePointerType,
  InteractiveXaxisType,
  isChartGraph,
  SubchartStateType,
} from "../Types/CChartStateTypes";
import { ChartPeriodType } from "../Types/CChartTimeTypes";
import { purePixToX, pureXToPix } from "./CChartStateXaxis";
import { purePixToY, pureYToPix, snapPixYToPrices } from "./CChartStateYaxis";

export const getSubchartIdxByVector = (
  pixXy: [number, number] | undefined,
  subcharts: CalculatedChartStateType["subCharts"]
): number | null => {
  if (!pixXy || !subcharts) return null;
  for (let i = 0; i < subcharts.length; i++) {
    const subchart = subcharts[i];
    if (pixXy[1] >= subchart.top && pixXy[1] < subchart.bottom) return i;
  }
  return null;
};

export const getSubchartIdxByBottom = (
  pixXy: [number, number] | undefined,
  subcharts: CalculatedChartStateType["subCharts"]
): number | null => {
  if (!pixXy) return null;
  for (let i = 0; i < subcharts.length; i++) {
    const subchart = subcharts[i];
    if (pixXy[1] >= subchart.bottom - 10 && pixXy[1] < subchart.bottom + 10) return i;
  }
  return null;
};

const getToolsByXy = (
  pixXy: [number, number] | undefined,
  subcharts: ChartStateType["subCharts"],
  xaxis: InteractiveXaxisType
): [number, number, number, number][] => {
  if (!pixXy || !subcharts || !xaxis) return [];
  const subchartIdx = getSubchartIdxByVector(pixXy, subcharts);
  if (subchartIdx === null) return [];

  let toolsIdx: [number, number, number, number][] = [];
  const translatedX = xaxis.totalTranslatedX;
  const widthPerTick = xaxis.scaledWidthPerTick;
  subcharts[subchartIdx].yaxis.forEach((singleYaxis, singleYaxisIdx) => {
    const translatedY = singleYaxis.translatedY;
    const heightPerPt = singleYaxis.heightPerPt;
    singleYaxis.tools.forEach((tool, toolIdx) => {
      tool.xy.forEach((anchorXy, anchorIdx) => {
        if (
          pixXy[0] >= pureXToPix(anchorXy[0], translatedX, widthPerTick) - 10 &&
          pixXy[0] <= pureXToPix(anchorXy[0], translatedX, widthPerTick) + 10 &&
          pixXy[1] >=
            pureYToPix(
              anchorXy[1],
              subcharts[subchartIdx].bottom,
              subcharts[subchartIdx].yaxis[singleYaxisIdx].decimals,
              translatedY,
              heightPerPt
            ) -
              10 &&
          pixXy[1] <=
            pureYToPix(
              anchorXy[1],
              subcharts[subchartIdx].bottom,
              subcharts[subchartIdx].yaxis[singleYaxisIdx].decimals,
              translatedY,
              heightPerPt
            ) +
              10
        ) {
          toolsIdx.push([subchartIdx, singleYaxisIdx, toolIdx, anchorIdx]);
        }
      });
    });
  });
  return toolsIdx;
};

export const getDragActionType = (
  dragInitXy: [number, number] | undefined,
  subcharts: SubchartStateType[],
  xaxis: InteractiveXaxisType,
  isEditing: boolean,
  isDrawing: boolean,
  canvasWidth: number,
  canvasHeight: number
):
  | null
  | { type: "translate" | "scale" }
  | { type: "resizeSubchart"; res: number; initBottom: number }
  | { type: "drawTool"; init: [number, number] }
  | { type: "editTool"; res: [number, number, number, number][]; init: [number, number] } => {
  if (!dragInitXy) return null;
  if (isEditing) return null;

  if (isDrawing) {
    // const editToolPath = getToolsByXy(dragInitXy, subcharts, xaxis); //only initially -> tool xy is changed by editing but dragInitXy isnt adjusted!
    return {
      type: "drawTool",
      // res: editToolPath,
      init: dragInitXy,
    };
  }

  const editToolPath = getToolsByXy(dragInitXy, subcharts, xaxis); //only initially -> tool xy is changed by editing but dragInitXy isnt adjusted!
  if (editToolPath.length > 0)
    return {
      type: "editTool",
      res: editToolPath,
      init: subcharts[editToolPath[0][0]].yaxis[editToolPath[0][1]].tools[editToolPath[0][2]].xy[editToolPath[0][3]],
    };
  const resizeSubchartIdx = getSubchartIdxByBottom(dragInitXy, subcharts); // same as above - only initially
  if (resizeSubchartIdx !== null && resizeSubchartIdx !== subcharts.length-1)
    return { type: "resizeSubchart", res: resizeSubchartIdx, initBottom: subcharts[resizeSubchartIdx].bottom };

  const clickedChart =
    dragInitXy[0] >= 0 && dragInitXy[0] < 0 + canvasWidth && dragInitXy[1] >= 0 && dragInitXy[1] < 0 + canvasHeight;
  if (clickedChart) return { type: "translate" };
  const clickedXaxis = dragInitXy[0] >= 0 && dragInitXy[0] < 0 + canvasWidth && dragInitXy[1] >= 0 + canvasHeight;
  if (clickedXaxis) return { type: "scale" };
  return null;
};

export const editToolPosition = (
  dragActionType: { type: "editTool"; res: [number, number, number, number][]; init: [number, number] },
  subcharts: ChartStateType["subCharts"],
  xaxis: InteractiveXaxisType,
  pointer: ChartStateType["pointer"]
): ChartStateType["subCharts"] => {
  if (dragActionType.type !== "editTool" || dragActionType.res.length === 0 || !dragActionType.init) return subcharts;
  // if (drag.isDragging) {
  const curPixX = !!pointer.chartPointer.move.pixXSnap ? pointer.chartPointer.move.pixXSnap : pointer.drag.xy[0];
  const curPixY =
    pointer.chartPointer.move.snapDatasets.length > 0 && !!pointer.chartPointer.move.snapDatasets[0].pixYSnap
      ? pointer.chartPointer.move.snapDatasets[0].pixYSnap
      : pointer.drag.xy[1];
  const initX = pureXToPix(
    Math.round(purePixToX(pointer.drag.initial[0], xaxis.totalTranslatedX, xaxis.scaledWidthPerTick)),
    xaxis.totalTranslatedX,
    xaxis.scaledWidthPerTick
  );
  const subIdx = dragActionType.res[0][0];
  const yaxisIdx = dragActionType.res[0][1];

  let datasets: {
    y: number;
    pixY: number;
  }[] = [];
  let yAdjusted = { y: dragActionType.init[1], pixY: pointer.drag.initial[1] };
  subcharts[subIdx].yaxis[yaxisIdx].graphs.forEach((graph, graphIdx) => {
    const localInitY = snapPixYToPrices(pointer.drag.initial[1], graph.data[initX], subcharts, subIdx, yaxisIdx);
    if (!!localInitY && localInitY.length > 0) datasets.push({ ...localInitY[0], y: parseFloat(localInitY[0].y) });
  });
  if (datasets.length > 0) {
    yAdjusted = { y: datasets[0].y, pixY: datasets[0].pixY };
  }

  const newPixX =
    pureXToPix(dragActionType.init[0], xaxis.totalTranslatedX, xaxis.scaledWidthPerTick) + curPixX - initX;
  const newPixY =
    pureYToPix(
      dragActionType.init[1],
      subcharts[subIdx].bottom,
      subcharts[subIdx].yaxis[yaxisIdx].decimals,
      subcharts[subIdx].yaxis[yaxisIdx].translatedY,
      subcharts[subIdx].yaxis[yaxisIdx].heightPerPt
    ) +
    curPixY -
    yAdjusted.pixY;
  const newX = purePixToX(newPixX, xaxis.totalTranslatedX, xaxis.scaledWidthPerTick);
  const newY = purePixToY(
    newPixY,
    subcharts[subIdx].bottom,
    subcharts[subIdx].yaxis[yaxisIdx].decimals,
    subcharts[subIdx].yaxis[yaxisIdx].translatedY,
    subcharts[subIdx].yaxis[yaxisIdx].heightPerPt
  );
  // console.log(curPixX, curPixY, newPixX, newPixY, newX, newY);
  // console.log(
  //   "2nd:",
  //   pureXToPix(
  //     Math.round(purePixToX(pointer.drag.initial[0], xaxis.totalTranslatedX, xaxis.scaledWidthPerTick)),
  //     xaxis.totalTranslatedX,
  //     xaxis.scaledWidthPerTick
  //   ),
  //   purePixToX(pointer.drag.initial[0], 0, 0),
  //   pointer.drag.initial[0]
  // );
  const [toolsSubIdx, toolsYaxisIdx, toolIdx, anchorIdx] = dragActionType.res[0];
  const editedSubcharts: ChartStateType["subCharts"] = [
    ...subcharts.slice(0, toolsSubIdx),
    {
      ...subcharts[toolsSubIdx],
      yaxis: [
        ...subcharts[toolsSubIdx].yaxis.slice(0, toolsYaxisIdx),
        {
          ...subcharts[toolsSubIdx].yaxis[toolsYaxisIdx],
          tools: [
            ...subcharts[toolsSubIdx].yaxis[toolsYaxisIdx].tools.slice(0, toolIdx),
            {
              ...subcharts[toolsSubIdx].yaxis[toolsYaxisIdx].tools[toolIdx],
              xy: [
                ...subcharts[toolsSubIdx].yaxis[toolsYaxisIdx].tools[toolIdx].xy.slice(0, anchorIdx),
                [newX, newY],
                ...subcharts[toolsSubIdx].yaxis[toolsYaxisIdx].tools[toolIdx].xy.slice(anchorIdx + 1),
              ],
            },
            ...subcharts[toolsSubIdx].yaxis[toolsYaxisIdx].tools.slice(toolIdx + 1),
          ],
        },
        ...subcharts[toolsSubIdx].yaxis.slice(toolsYaxisIdx + 1),
      ],
    },
    ...subcharts.slice(toolsSubIdx + 1),
  ];
  return editedSubcharts;
  // }
  // return subcharts;
};

export const resizeSubchart = (
  dragActionType: { type: "resizeSubchart"; res: number; initBottom: number },
  subcharts: ChartStateType["subCharts"],
  drag: ChartStateType["pointer"]["drag"]
): ChartStateType["subCharts"] => {
  if (dragActionType.type !== "resizeSubchart" || dragActionType === null) return subcharts;
  // if (drag.isDragging) {
  const subchartIdx = dragActionType.res;
  const newBottom = Math.min(
    Math.max(dragActionType.initBottom + drag.xy[1] - drag.initial[1], subcharts[subchartIdx].top + 10),
    subcharts[subchartIdx + 1].bottom - 10
  );
  // console.log(newBottom);
  return [
    ...subcharts.slice(0, subchartIdx),
    { ...subcharts[subchartIdx], bottom: newBottom },
    { ...subcharts[subchartIdx + 1], top: newBottom },
    ...subcharts.slice(subchartIdx + 2),
  ];
  // }
  // return subcharts;
};

export const addToolAnchor = (
  // dragActionType: { type: "drawTool"},
  drawToolType: "hline" | "vline" | "trendline" | undefined,
  // subcharts: ChartStateType["subCharts"],
  draw: ChartStateType["draw"],
  pointer: ChartStateType["pointer"]
): ChartStateType["draw"] => {
  const defaultDrawState: ChartStateType["draw"] = { isDrawing: false, pixXy: [] };

  const curPixX = !!pointer.chartPointer.move.pixXSnap ? pointer.chartPointer.move.pixXSnap : pointer.drag.xy[0];
  const curPixY =
    pointer.chartPointer.move.snapDatasets.length > 0 && !!pointer.chartPointer.move.snapDatasets[0].pixYSnap
      ? pointer.chartPointer.move.snapDatasets[0].pixYSnap
      : pointer.drag.xy[1];
  const dragpixXy: [number, number] = [curPixX, curPixY];
  if (!drawToolType || !draw || !dragpixXy) return defaultDrawState;
  // add temporary anchor point
  return { ...draw, pixXy: [...draw.pixXy, dragpixXy] };
};

export const addTool = (
  subcharts: ChartStateType["subCharts"],
  xaxis: InteractiveXaxisType,
  draw: ChartStateType["draw"],
  drag: ChartStateType["pointer"]["drag"]
): ChartStateType["subCharts"] => {
  const subchartIdx = getSubchartIdxByVector(drag.initial, subcharts);
  // console.log(subchartIdx, xaxis, draw, draw.type);
  if (subchartIdx === null || !xaxis || !draw || !drag || !draw.type) return subcharts;
  const pixXyArr = draw.pixXy;
  const xyArr = pixXyArr.map((pixXy, pixXyIdx) => [
    purePixToX(pixXy[0], xaxis.totalTranslatedX, xaxis.scaledWidthPerTick),
    purePixToY(
      pixXy[1],
      subcharts[subchartIdx].bottom,
      subcharts[subchartIdx].yaxis[0].decimals,
      subcharts[subchartIdx].yaxis[0].translatedY,
      subcharts[subchartIdx].yaxis[0].heightPerPt
    ),
  ]);

  return [
    ...subcharts.slice(0, subchartIdx),
    {
      ...subcharts[subchartIdx],
      yaxis: [
        {
          ...subcharts[subchartIdx].yaxis[0],
          tools: [
            ...subcharts[subchartIdx].yaxis[0].tools,
            { xy: xyArr, type: draw.type, style: { strokeColor: "blue", anchorColor: "#333" } } as DrawingTool,
          ],
        },
        ...subcharts[subchartIdx].yaxis.slice(1),
      ],
    },
    ...subcharts.slice(subchartIdx + 1),
  ];
};

export const calculateXPointer = (
  widthPerTick: number,
  translatedPixX: number,
  xaxis: InteractiveXaxisType,
  mainGraph: ChartGraphStateType,
  onMove?: ChartStateType["pointer"]["move"]
) => {
  const defaultXpointer = null;

  if (!mainGraph || !onMove) return defaultXpointer;
  const xIdx = Math.round(purePixToX(onMove?.xy[0] as any, translatedPixX, widthPerTick));
  if (xIdx < 0 || xIdx > mainGraph.data.length - 1) return { xPointerUnlimited: xIdx };
  const xPointer = Math.abs(Math.round(xIdx));
  let xPointerDateStr = "";
  const optPeriodRes = getOptimalPeriod(
    mainGraph.data,
    xaxis.xStart,
    xaxis.xEnd,
    mainGraph.meta,
    xaxis.noDataPeriodPortion,
    Object.assign({}, mainGraph.dateStat)
  );
  // console.log(mainGraph, onMove, optPeriodRes, mainGraph.meta.chartPeriod);
  if (!optPeriodRes || !mainGraph.meta.chartPeriod) return defaultXpointer;

  const periodToDraw = isNewPeriod(mainGraph.data[xPointer].date, mainGraph.dateStat, mainGraph.meta.chartPeriod, {
    ...(optPeriodRes as ChartPeriodType),
  });
  xPointerDateStr = getDateString(
    mainGraph.data[xPointer].date,
    !!periodToDraw ? periodToDraw : mainGraph.meta.chartPeriod.chartPeriodName
  );
  const pixXPointer = pureXToPix(xPointer, translatedPixX, widthPerTick);
  return { xPointer, xPointerDateStr, pixXPointer, xPointerUnlimited: xIdx };
};

export const calculateCurrentPointerDataset = (
  subcharts: CalculatedChartStateType["subCharts"] | null,
  xpointer: number | null,
  xpointerUnlimited: number,
  xPointerDateString: string,
  pixXPointer: number,
  onMove: CalculatedChartStateType["pointer"]["move"],
  onDragEnd: CalculatedChartStateType["pointer"]["dragPointerUp"]
): InteractivePointerType["chartPointer"] | undefined => {
  const xy = onMove?.xy;
  if (!onMove || !subcharts || !xy) return;

  const getSubchartIdx = (xy: Vector2) => {
    let subchartIdx: number = -1;
    for (let i = 0; i < subcharts.length; i++) {
      const subchart = subcharts[i];
      if (xy[1] >= subchart.top && xy[1] < subchart.bottom) subchartIdx = i;
    }
    return subchartIdx;
  };
  const subchartIdx = getSubchartIdx(xy);
  if (subchartIdx === -1) return;
  const clickedSubchartIdx = !!onDragEnd && !!onDragEnd.xy ? getSubchartIdx(onDragEnd.xy) : null;

  const snapDatasets: InteractivePointerType["chartPointer"]["move"]["snapDatasets"] = [];

  if (xpointer !== null)
    subcharts[subchartIdx].yaxis.forEach((singleYaxis, singleYaxisIdx) => {
      singleYaxis.graphs.forEach((graph, graphIdx) => {
        let ySnap: string | null = null;
        let pixYSnap: number | null = null;

        const snapPriceRes = snapPixYToPrices(
          onMove?.xy[1],
          graph.data[xpointer],
          subcharts,
          subchartIdx,
          singleYaxisIdx
        );
        if (!!snapPriceRes && snapPriceRes.length > 0) {
          pixYSnap = snapPriceRes[0].pixY;
          ySnap = snapPriceRes[0].y;
        }

        snapDatasets.push({
          yaxisIdx: singleYaxisIdx,
          graphIdx: graphIdx,
          data: graph.data[xpointer],
          y: purePixToY(
            onMove?.xy[1],
            subcharts[subchartIdx].bottom,
            singleYaxis.decimals,
            singleYaxis.translatedY,
            singleYaxis.heightPerPt
          ),
          ySnap: ySnap,
          pixYSnap: pixYSnap,
          dateString: xPointerDateString,
        });
      });
    });

  const calcPointerDataset = {
    move: {
      pixX: onMove?.xy[0],
      pixXSnap: pixXPointer,
      pixY: onMove?.xy[1],
      x: xpointer,
      xUnlimited: xpointerUnlimited,
      subchartIdx, //onMove
      snapDatasets: snapDatasets,
      xDateString: xPointerDateString,
    },
    click: {
      clickedSubchartIdx: clickedSubchartIdx, //onDragEnd
    },
  };
  return calcPointerDataset;
};
