import React, { RefObject } from "react";
import { Stage, Layer, Rect, Line, Shape } from "react-konva";
import { CChartProps, Dataset } from "./Types/CChartTypes";
import { isIndicatorGraph } from "./Types/CChartStateTypes";
import Ajv from "ajv";
import { chartDataSchema } from "./CChartDataSchema";
import { CChartLabel } from "./CChartLabel";
import { CChartCrosshair } from "./CChartCrosshair";

import { CCandleChart } from "./Charts/CCandleChart";
import { CLineChart } from "./Charts/CLineChart";
import { CBarChart } from "./Charts/CBarChart";
import { ReducerActionType } from "./ChartState/CChartStateReducer";
import { useChartState } from "./ChartState/CChartState";
import { CTrendline } from "./Tools/CTrendline";
import { CVline } from "./Tools/CVLine";
import { CHline } from "./Tools/CHLine";
import { Button, ThemeProvider, Typography, useTheme } from "@material-ui/core";

import { muiTheme } from "./MuiTheme";
import { CChartMenu2, CChartMenuStateType } from "./Menu2/ChartMenu";
import Icon from "@mdi/react";
import { mdiMenu } from "@mdi/js";
const ajv = new Ajv();

export const CChart = (props: CChartProps) => {
  // get props + -> deviceRatio (does konva need that anymore?!)
  const { width, height, chartData, style, viewMode } = props;
  // responsive container and pointer events
  let widthPerTick = 20;
  const ContainerRef: RefObject<HTMLDivElement> = React.useRef(null);

  const [ChartMenu2Open, setChartMenu2Open] = React.useState<CChartMenuStateType>({
    location: null,
    expandedSetting: [],
  });
  const [ChartState, ChartStateDispatch] = useChartState(ContainerRef, { width, height, viewMode });
  widthPerTick = ChartState.xaxis.scaledWidthPerTick;
  const { widthYAxis, widthTickmarkLines } = ChartState.yaxis;
  const { heightXAxis, heightTickMarkLines } = ChartState.xaxis;
  const containerWidth = ChartState.containerSize.width - 1;
  const containerHeight = ChartState.containerSize.height - 1;
  const isContainerInit = ChartState.containerSize.init;
  // const theme = useTheme();

  const handleToggleExpanded = (id: string) => {
    if (ChartMenu2Open.expandedSetting.includes(id)) {
      setChartMenu2Open((current) => ({
        ...current,
        expandedSetting: ChartMenu2Open.expandedSetting.filter((val) => val !== id),
      }));
      // setExpanded(Expanded.filter((val) => val !== id));
    } else setChartMenu2Open((current) => ({ ...current, expandedSetting: [...ChartMenu2Open.expandedSetting, id] }));
    //setExpanded([...Expanded, id]);
  };

  // check chartData schema every time chartData is modified
  React.useEffect(() => {
    if (!isContainerInit) return;
    const validate = ajv.compile(chartDataSchema);
    const valid = validate(chartData);
    if (!valid) {
      alert("ERROR - chartData prop is not valid, check console");
      console.log("ERROR - chartData prop is not valid -> ", "data:", chartData, "errors:", validate.errors);
    } else if (ChartState.subCharts.length === 0) {
      const action: ReducerActionType<"addSubchart"> = {
        task: "addSubchart",
        params: { chartSeries: chartData[0].data, chartName: chartData[0].name, containerHeight },
      };
      (ChartStateDispatch as React.Dispatch<ReducerActionType<"addSubchart">>)(action);
      // addSubchart(chartData[0].data, chartData[0].name, { indicatorFn: ATR, params: [14] });
      // addSubchart(chartData[0].data, chartData[0].name, { indicatorFn: RSI, params: [14] });
      // addGraph(chartData[0].data, chartData[0].name, 0, { indicatorFn: SMA, params: [10] });
    }
    // eslint-disable-next-line
  }, [chartData, ChartState.subCharts.length, containerHeight, isContainerInit, heightXAxis]);
  const fullState = ChartState;

  console.log(fullState);
  const xAxis = fullState.xaxis.xAxisTicks;
  return (
    // <div style={{ width: "100%", height: "100%", position: "fixed", top: 0 }}>
    <React.Fragment>
      <ThemeProvider theme={muiTheme}>
        <div //responsive container (above is not necessary)
          ref={ContainerRef}
          style={{
            boxSizing: "border-box",
            // width: !!width ? width : "100%",
            // height: !!height ? height : "100%",
            // width: "100%",
            // height: "100%",
            // position: "fixed",
            border: "1px solid red",
            background: fullState.backgroundColor,
            // ...addContainerStyles,
          }}
        >
          <Stage //konva container
            width={ChartState.containerSize.width}
            height={ChartState.containerSize.height}
            listening={false}

            // style={{ boxSizing: "border-box" }}
          >
            {/*  Canvas Layer 3 - chart and example */}
            <Layer>
              {/* <Rect
              x={20 + ChartState.pointer.totalTranslatedX}
              y={50 + ChartState.pointer.totalTranslatedY}
              width={100}
              height={100}
              fill="red"
              shadowBlur={10}
            /> */}
              {0 in fullState.subCharts ? <CCandleChart fullState={fullState} /> : null}
              {fullState.subCharts.map((subchart, subchartIdx) =>
                subchart.yaxis.map((singleYaxis, singleYaxisIdx) =>
                  singleYaxis.graphs.map((graph, graphIdx) => {
                    const stateGraph = fullState.subCharts[subchartIdx].yaxis[0].graphs[graphIdx];
                    // const type = stateGraph.type;
                    const indicatorType = isIndicatorGraph(stateGraph) ? stateGraph.indicator.type : null;
                    if (isIndicatorGraph(stateGraph) && stateGraph.data.length > 0) {
                      const indicatorLines = stateGraph.data[stateGraph.data.length - 1].prices;

                      if (indicatorType === "line") {
                        return indicatorLines.map((indicatorDataSeries, indDataSeriesIdx) => (
                          <CLineChart
                            fullState={fullState}
                            subchartIdx={subchartIdx}
                            yaxisIdx={0}
                            graphIdx={graphIdx}
                            key={`${subchartIdx}-${graphIdx}-${indDataSeriesIdx}`}
                            indSeriesIdx={indDataSeriesIdx}
                          />
                        ));
                      } else if (indicatorType === "bars") {
                        return (
                          <CBarChart
                            fullState={fullState}
                            subchartIdx={subchartIdx}
                            yaxisIdx={0}
                            graphIdx={graphIdx}
                            key={`${subchartIdx}-${graphIdx}`}
                          />
                        );
                      }
                    }
                    return null;
                  })
                )
              )}
              {/* NEW TLINE!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */}
              {!!fullState.pointer.move.isMoving && fullState.draw.pixXy.length >= 1 ? (
                <Line
                  x={0}
                  y={0}
                  points={[
                    fullState.draw.pixXy[0][0],
                    fullState.draw.pixXy[0][1],
                    !!fullState.pointer?.chartPointer.move.x
                      ? fullState.methods.xToPix(fullState.pointer.chartPointer.move.x)
                      : fullState.pointer.move.xy[0],
                    !!fullState.pointer?.chartPointer?.move.snapDatasets?.[0]?.pixYSnap
                      ? fullState.pointer?.chartPointer?.move.snapDatasets[0].pixYSnap
                      : fullState.pointer.move.xy[1],
                  ]}
                  stroke={"red"}
                  strokeWidth={1}
                />
              ) : null}
              {/* EXISTING TLINES!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */}
              {0 in fullState.subCharts
                ? fullState.subCharts[0].yaxis[0].tools.map((tool, toolIdx) => {
                    if (tool.type === "trendline" && tool.xy.length === 2)
                      return (
                        <CTrendline
                          mode="trendline"
                          subchartTop={fullState.subCharts[0].top}
                          subchartBottom={fullState.subCharts[0].bottom}
                          pixXy={[
                            [fullState.methods.xToPix(tool.xy[0][0]), fullState.methods.yToPix(tool.xy[0][1], 0, 0)],
                            [fullState.methods.xToPix(tool.xy[1][0]), fullState.methods.yToPix(tool.xy[1][1], 0, 0)],
                          ]}
                          style={{ anchorColor: tool.style.anchorColor, strokeColor: tool.style.strokeColor }}
                        />
                      );
                    else if (tool.type === "vline" && tool.xy.length === 1)
                      return (
                        <CVline
                          mode="extendUpDown"
                          subchartTop={fullState.subCharts[0].top}
                          subchartBottom={fullState.subCharts[0].bottom}
                          pixXy={[
                            [fullState.methods.xToPix(tool.xy[0][0]), fullState.methods.yToPix(tool.xy[0][1], 0, 0)],
                          ]}
                          style={{ anchorColor: tool.style.anchorColor, strokeColor: tool.style.strokeColor }}
                        />
                      );
                    else if (tool.type === "hline" && tool.xy.length === 1)
                      return (
                        <CHline
                          mode="extendLeftRight"
                          pixXstart={0}
                          pixXend={fullState.containerSize.width - fullState.yaxis.widthYAxis}
                          pixXy={[
                            [fullState.methods.xToPix(tool.xy[0][0]), fullState.methods.yToPix(tool.xy[0][1], 0, 0)],
                          ]}
                          style={{ anchorColor: tool.style.anchorColor, strokeColor: tool.style.strokeColor }}
                        />
                      );
                    else return null;
                  })
                : null}
            </Layer>

            {/*  Canvas Layer 5 (before 0) - axis bg rects + future chartbgcolor? */}
            <Layer>
              <Rect
                x={0 + 0.5}
                y={containerHeight - heightXAxis + 0.5}
                width={containerWidth}
                height={heightXAxis}
                fill={ChartState.xaxis.style.fillColor}
                stroke={ChartState.xaxis.style.strokeColor}
                strokeWidth={1}
              />
              {fullState.subCharts.map((subchart, subchartIdx) => (
                <React.Fragment key={subchartIdx}>
                  <Rect
                    // key={`subchartPrimaryYaxis-${subchartIdx}`}
                    x={containerWidth - widthYAxis + 0.5}
                    y={subchart.top + 0.5}
                    width={widthYAxis}
                    height={subchart.bottom - subchart.top}
                    // fill={ChartState.xaxis.style.fillColor}
                    stroke={ChartState.xaxis.style.strokeColor}
                    strokeWidth={1}
                  />
                  <Rect
                    // key={`subchartBorder-${subchartIdx}`}
                    x={0 + 0.5}
                    y={subchart.top + 0.5}
                    width={containerWidth - widthYAxis}
                    height={subchart.bottom - subchart.top}
                    stroke={ChartState.xaxis.style.strokeColor}
                    strokeWidth={1}
                  />
                </React.Fragment>
              ))}
            </Layer>
            {/*  Canvas Layer 6 before 1 - x axis */}
            <Layer>
              {xAxis.map((xaxisTick, xaxisTickIdx) => {
                return (
                  <React.Fragment key={xaxisTickIdx}>
                    <Line
                      x={xaxisTick.x}
                      y={ChartState.containerSize.height - 1 - heightXAxis + 0.5}
                      points={[0, 0, 0, heightTickMarkLines]}
                      stroke={ChartState.xaxis.style.strokeColor}
                      strokeWidth={1}
                    />
                    {!!ChartState.grid.useGridX ? (
                      <Line
                        x={xaxisTick.x}
                        y={0 + 0.5}
                        points={[0, 0, 0, ChartState.containerSize.height - 1 - heightXAxis + 0.5]}
                        stroke={ChartState.grid.style.strokeColor}
                        strokeWidth={1}
                      />
                    ) : null}
                    <Shape
                      sceneFunc={(context, shape) => {
                        // context.fillS
                        const ctx = context._context;
                        ctx.font = ChartState.xaxis.style.fontSize + "px " + ChartState.xaxis.style.fontName;
                        ctx.fillStyle = ChartState.xaxis.style.fontColor;
                        ctx.textAlign = "center";
                        ctx.textBaseline = "top";
                        ctx.fillText(
                          xaxisTick.dateString,
                          xaxisTick.x,
                          ChartState.containerSize.height - 1 - heightXAxis + 0.5 + 20 + 5 // additional 5px distance to end of tickmark
                        );
                        // (!) Konva specific method, it is very important
                        context.fillStrokeShape(shape);
                      }}
                      // fill="#333"
                      // stroke={ChartState.options.xAxis.style.fontColor}
                      // strokeWidth={1}
                    />
                  </React.Fragment>
                );
              })}
            </Layer>
            {/*  Canvas Layer 7 before 2 - y axis */}
            <Layer>
              {fullState.subCharts.map((subchart, subchartIdx) => {
                return subchart.yaxis.map((singleYaxis, singleYaxisIdx) => {
                  const yAxisTicks = singleYaxis.drawTicks;

                  // const yAxisTicks =
                  //   subchartIdx === 0 ? subchart.yaxis[0] : subchart.yaxis[0].slice(0, subchart.yaxis[0].length - 1);
                  return yAxisTicks.map((yaxisTick, yaxisTickIdx) => {
                    return (
                      <React.Fragment key={yaxisTick.label}>
                        <Line
                          x={ChartState.containerSize.width - 1 - widthYAxis + 0.5}
                          y={yaxisTick.pixY + 0.5}
                          points={[0, 0, widthTickmarkLines, 0]}
                          stroke={ChartState.xaxis.style.strokeColor}
                          strokeWidth={1}
                        />
                        {!!ChartState.grid.useGridY ? (
                          <Line
                            x={0 + 0.5}
                            y={yaxisTick.pixY + 0.5}
                            points={[0, 0, ChartState.containerSize.width - 1 + 0.5, 0]}
                            stroke={ChartState.grid.style.strokeColor}
                            strokeWidth={1}
                          />
                        ) : null}
                        <Shape
                          sceneFunc={(context, shape) => {
                            // context.fillS
                            const ctx = context._context;
                            ctx.font = ChartState.yaxis.style.fontSize + "px " + ChartState.yaxis.style.fontName;
                            ctx.fillStyle = ChartState.yaxis.style.fontColor;
                            ctx.textAlign = "left";
                            ctx.textBaseline = "middle";
                            ctx.fillText(
                              yaxisTick.label,
                              ChartState.containerSize.width - 1 - widthYAxis + widthTickmarkLines + 5 + 0.5,
                              yaxisTick.pixY // additional 5px distance to end of tickmark
                            );
                            // (!) Konva specific method, it is very important
                            context.fillStrokeShape(shape);
                          }}
                        />
                      </React.Fragment>
                    );
                  });
                });
              })}
            </Layer>
            {/*  Canvas Layer 4 - crosshair */}
            <Layer>
              {!!ChartState.pointer.move?.xy && !!ChartState.pointer.isHovering ? (
                <CChartCrosshair fullState={fullState} />
              ) : null}
            </Layer>
          </Stage>
        </div>

        {!!fullState.xaxis.xEnd && xAxis.length > 0
          ? fullState.subCharts.map((subchart, subchartIdx) => (
              <div style={{ position: "absolute", top: subchart.top + 2 }}>
                {subchart.yaxis[0].graphs.map((graph, graphIdx) => {
                  const name =
                    isIndicatorGraph(graph) && graph.indicator.params.length > 0
                      ? `${graph.indicator.name}(${graph.indicator.params[0].val})`
                      : isIndicatorGraph(graph) && graph.indicator.params.length === 0
                      ? `${graph.indicator.name}`
                      : graph.name;
                  if (!graph.datasets.last) return null;

                  const tmpDataset: Dataset =
                    (fullState.pointer.isHovering &&
                    subchartIdx === fullState.pointer?.chartPointer?.move.subchartIdx &&
                    fullState.pointer?.chartPointer?.move.snapDatasets?.[0]?.graphIdx === graphIdx
                      ? fullState.pointer?.chartPointer?.move?.snapDatasets?.[0]?.data
                      : fullState.pointer.isHovering &&
                        // subchartIdx === fullState.pointer?.chartPointer?.move.subchartIdx &&
                        fullState.pointer.chartPointer.move.x !== null
                      ? graph.data[fullState.pointer.chartPointer.move.x]
                      : graph.datasets.last.data) || graph.datasets.last.data;

                  return (
                    <CChartLabel
                      key={`${subchartIdx}-${graphIdx}`}
                      onClick={() =>
                        setChartMenu2Open((current) => ({
                          location: "settings",
                          expandedSetting: [`graph-sub-${subchartIdx}-y-0-graph-${graphIdx}`, `sub-${subchartIdx}`],
                        }))
                      }
                      name={name}
                      dataset={tmpDataset}
                      decimals={
                        isIndicatorGraph(graph) && graph.indicator.default.decimals !== undefined
                          ? graph.indicator.default.decimals
                          : graph.decimals
                      }
                      // top={subchart.top + graphIdx * 30} // get height automatically!
                    />
                  );
                })}
              </div>
            ))
          : null}
        <div style={{ position: "absolute", top: (fullState.subCharts?.[0]?.bottom ?? 0) - 64 - 10, left: 10 }}>
          <Button
            color="primary"
            variant="contained"
            style={{ width: 64, height: 64, textTransform: "none", borderRadius: 5, opacity: 0.8 }}
            onClick={() => setChartMenu2Open((current) => ({ ...current, location: "menu" }))}
          >
            <div style={{ position: "relative", top: 0, height: 64 }}>
              <Icon path={mdiMenu} size="64px" color="#fff" />
            </div>
          </Button>
        </div>

        <CChartMenu2
          ChartMenuState={ChartMenu2Open}
          onClose={() => {
            setChartMenu2Open((current) => ({ ...current, location: null }));
          }}
          onNavigate={(target) => setChartMenu2Open((current) => ({ ...current, location: target }))}
          ChartState={fullState}
          ChartStateDispatch={ChartStateDispatch}
          onSettingsExpand={handleToggleExpanded}
        />
      </ThemeProvider>
    </React.Fragment>
    // </div>
  );
};
