export function getData() {
  const promiseMSFT = fetch("https://raw.githubusercontent.com/carmnk/resources/main/data/csv/BAYN.DE.csv")
    .then((response) => {
      return response.text();
    })
    .then((data) => {
      //   console.log(data);
      let res = parse(data, { delimiter: ",", cast: true, trim: true, cast_date: true })
        .map((dataset: any[]) => ({
          date: dataset[0],
          open: dataset[1],
          high: dataset[2],
          low: dataset[3],
          close: dataset[4],
          volume: dataset[5],
        }));
      res.shift();
      return res
    })
    
  return promiseMSFT;
}

