import { createMuiTheme, responsiveFontSizes } from "@material-ui/core/styles";
export const muiTheme = (() => {
  let localTheme = createMuiTheme({
    palette: {
      primary: {
        main: "#009688",
      },
      secondary: {
        main: "#f50057",
      },
      type: "light",
    },
  });

  localTheme.typography.h1 = {
    fontFamily: "'Open Sans', sans-serif, Arial",
    fontWeight: 600,
    fontSize: "3rem",
    lineHeight: 1.167,
  };
  localTheme.typography.h2 = {
    fontFamily: "'Open Sans', sans-serif, Arial",
    fontWeight: 600,
    fontSize: "2.5rem",
    lineHeight: 1.167,
  };
  localTheme.typography.h3 = {
    fontFamily: "'Open Sans', sans-serif, Arial",
    fontWeight: 600,
    fontSize: "2rem",
    lineHeight: 1.167,
  };
  localTheme.typography.h4 = {
    fontFamily: "'Open Sans', sans-serif, Arial",
    fontWeight: 600,
    fontSize: "1.88rem",
    lineHeight: 1.167,
  };
  localTheme.typography.h5 = {
    fontFamily: "'Open Sans', sans-serif, Arial",
    fontWeight: 600,
    fontSize: "1.44rem",
    lineHeight: 1.167,
  };
  localTheme.typography.h6 = {
    fontFamily: "'Open Sans', sans-serif, Arial",
    fontWeight: 600,
    fontSize: "1.25rem",
    lineHeight: 1.167,
  };
  localTheme.typography.fontFamily = "'Quattrocento Sans', sans-serif";
  localTheme = responsiveFontSizes(localTheme);
  return localTheme;
})();
