import { PeriodsConstType } from "../Types/CChartTimeTypes";
import { format, getDate, getHours, getISOWeek, getMinutes, getMonth, getYear } from "date-fns";

export const getDateString = (date: Date, period: keyof PeriodsConstType): string => {
  switch (period) {
    case "minutes":
      return format(date, "mm");
    case "hours":
      return format(date, "HH");
    case "days":
      return format(date, "dd");
    case "weeks":
      return format(date, "dd");
    case "months":
      return format(date, "MMM");
    case "years":
      return format(date, "yyyy");
    default:
      return "|";
  }
}; 

export const getDateSubValue = (date: Date, period: keyof PeriodsConstType): number => {
  switch (period) {
    case "minutes":
      return getMinutes(date);
    case "hours":
      return getHours(date);
    case "days":
      return getDate(date);
    case "weeks":
      return getISOWeek(date);
    case "months":
      return getMonth(date);
    case "years":
      return getYear(date);
    default:
      return 0;
  }
};
export const getDateObject = (date: Date) => ({
  minute: getDateSubValue(date, "minutes"),
  hour: getDateSubValue(date, "hours"),
  day: getDateSubValue(date, "days"),
  week: getDateSubValue(date, "weeks"),
  month: getDateSubValue(date, "months"),
  year: getDateSubValue(date, "years"),
});
