import { makeStyles, Theme, createStyles, Typography, TypographyProps } from "@material-ui/core";
import { Variant } from "@material-ui/core/styles/createTypography";
import { TreeItemProps, TreeItem } from "@material-ui/lab";

const useTreeItemStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      color: theme.palette.text.secondary,
      "&:hover > $content": {
        backgroundColor: theme.palette.action.hover,
      },
      "&:focus > $content, &$selected > $content": {
        backgroundColor: `var(--tree-view-bg-color, ${theme.palette.grey[400]})`,
        color: "var(--tree-view-color)",
      },
      "&:focus > $content $label, &:hover > $content $label, &$selected > $content $label": {
        backgroundColor: "transparent",
      },
    },
    content: {
      color: theme.palette.text.secondary,
      borderTopRightRadius: theme.spacing(2),
      borderBottomRightRadius: theme.spacing(2),
      paddingRight: theme.spacing(1),
      fontWeight: theme.typography.fontWeightMedium,
      "$expanded > &": {
        fontWeight: theme.typography.fontWeightRegular,
      },
      width: "auto",
    },
    group: {
      marginLeft: 0,
      "& $content": {
        paddingLeft: theme.spacing(2),
      },
    },
    expanded: {},
    selected: {},
    label: {
      fontWeight: "inherit",
      color: "inherit",
    },
    labelRoot: {
      display: "flex",
      alignItems: "center",
      padding: theme.spacing(1, 0),
    },
    labelRootBtn: {
      // display: "flex",
      // alignItems: "left",
      // padding: theme.spacing(0.5, 0),
      width: "max-content",
    },
    btnLabel: {
      padding: 0,
    },
    labelIcon: {
      marginRight: theme.spacing(1),
    },
    labelText: {
      fontWeight: "inherit",
      flexGrow: 1,
    },
  })
);

declare module "csstype" {
  interface Properties {
    "--tree-view-color"?: string;
    "--tree-view-bg-color"?: string;
  }
}
export type CTreeItemProps = TreeItemProps & {
  bgColor?: string;
  color?: string;
  labelIcon?: JSX.Element;
  labelInfo?: JSX.Element | string;
  labelText: string | JSX.Element;
  onLabelClick?: TreeItemProps["onLabelClick"];
  onIconClick?: TreeItemProps["onIconClick"];
  labelTypographyVariant?: Variant;
  infoTypographyVariant?: Variant;
  labelTypographyColor?: TypographyProps["color"];
  infoTypographyColor?: TypographyProps["color"];
};

export const CTreeItem = (props: CTreeItemProps) => {
  const classes = useTreeItemStyles();
  const {
    labelText,
    labelIcon: LabelIcon,
    labelInfo,
    color,
    bgColor,
    onLabelClick,
    onIconClick,
    labelTypographyVariant,
    infoTypographyVariant,
    labelTypographyColor,
    infoTypographyColor,
    ...other
  } = props;

  return (
    <TreeItem
      onLabelClick={onLabelClick}
      onIconClick={onIconClick}
      label={
        <div className={classes.labelRoot}>
          {LabelIcon}
          <Typography
            variant={labelTypographyVariant ?? "body1"}
            className={classes.labelText}
            color={labelTypographyColor ?? "initial"}
          >
            {labelText}
          </Typography>
          <Typography variant={infoTypographyVariant ?? "caption"} color={infoTypographyColor ?? "initial"}>
            {labelInfo}
          </Typography>
        </div>
      }
      style={{
        "--tree-view-color": color,
        "--tree-view-bg-color": bgColor,
        // boxSizing: "content-box",
      }}
      classes={{
        root: classes.root,
        content: classes.content,
        expanded: classes.expanded,
        selected: classes.selected,
        group: classes.group,
        label: classes.label,
      }}
      {...other}
    />
  );
};
