import { Icon } from "@mdi/react";
import { ToggleButton, ToggleButtonGroup, TreeView } from "@material-ui/lab";
import {
  mdiMenuDown,
  mdiMenuRight,
  mdiDiameterVariant,
  mdiChartBellCurve,
  mdiArrowExpandVertical,
  mdiPoll,
  mdiHome,
  mdiFilter,
  mdiMagnify,
  mdiAlphabetical,
  mdiSortAlphabeticalAscending,
  mdiDivingScubaTank,
  mdiBorderColor,
  mdiClose,
  mdiIframeVariableOutline,
} from "@mdi/js";
import React from "react";
import { iSMA, iEMA, iRSI, iMACD, iATR, iVolume, iOBV } from "../CIndicator";
import {
  AdditionalIndicatorGraphStateType,
  CalculatedChartStateType,
  ChartStateHookType,
  isChartGraph,
  isIndicatorGraph,
} from "../Types/CChartStateTypes";
import { ChartDataSeries } from "../Types/CChartTypes";
import { Grid, IconButton, TextField, Typography, useTheme } from "@material-ui/core";
import { CTreeItem } from "../CTreeItem";
import { CChartMenuStateType } from "./ChartMenu";
import { ReducerActionType } from "../ChartState/CChartStateReducer";
import { Colorpicker } from "../Colorpicker";

const onIndicatorLabelClick = (
  indi: AdditionalIndicatorGraphStateType["indicator"],
  ChartState: CalculatedChartStateType
) => {
  if (indi.default.newSubchart)
    return () => {
      ChartState.methods.addSubchart(
        ChartState.subCharts[0].yaxis[0].graphs[0].data as ChartDataSeries,
        ChartState.subCharts[0].yaxis[0].graphs[0].name,
        indi
      );
    };
  else
    return () => {
      ChartState.methods.addGraph(
        ChartState.subCharts[0].yaxis[0].graphs[0].data as ChartDataSeries,
        ChartState.subCharts[0].yaxis[0].graphs[0].name,
        0,
        indi
      );
    };
};

export const CIndicatorGraphTreeItem = (props: {
  subchartIdx: number;
  yaxisIdx: number;
  graphIdx: number;
  ChartState: CalculatedChartStateType;
  ChartStateDispatch: ChartStateHookType[1];
  handleToggleExpanded?: (id: string) => void;
}) => {
  const { subchartIdx, yaxisIdx, graphIdx, ChartState, ChartStateDispatch, handleToggleExpanded } = props;
  const graphGeneric = ChartState.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx];
  const graph = isIndicatorGraph(graphGeneric) ? graphGeneric : null;
  const theme = useTheme();

  if (!graph) return null;

  return (
    <CTreeItem
      key={`graph-sub-${subchartIdx}-y-${yaxisIdx}-graph-${graphIdx}`}
      nodeId={`graph-sub-${subchartIdx}-y-${yaxisIdx}-graph-${graphIdx}`}
      labelText={graph.name}
      bgColor="#eee"
      onLabelClick={() => {
        const id = `graph-sub-${subchartIdx}-y-${yaxisIdx}-graph-${graphIdx}`;
        handleToggleExpanded?.(id);
      }}
      labelIcon={
        <div
          style={{
            background: theme.palette.primary.main,
            width: 32,
            height: 32,
            border: "1px solid #666",
            borderRadius: 5,
            marginRight: 10,
          }}
        >
          <Icon path={mdiChartBellCurve} size={"32px"} color={theme.palette.secondary.contrastText} />
        </div>
      }
      labelInfo={
        subchartIdx !== 0 || graphIdx !== 0 ? (
          <IconButton
            size="small"
            onClick={(e) => {
              e.preventDefault();
              const action: ReducerActionType<"removeGraph"> = {
                task: "removeGraph",
                params: { subchartIdx, yaxisIdx: 0, graphIdx },
              };
              (ChartStateDispatch as React.Dispatch<ReducerActionType<"removeGraph">>)(action);
            }}
          >
            <Icon path={mdiClose} size={1} color="#333" />
          </IconButton>
        ) : undefined
      }
    >
      <CTreeItem
        key={subchartIdx * 1000 + yaxisIdx * 100 + graphIdx}
        nodeId={(subchartIdx * 1000 + yaxisIdx * 100 + graphIdx).toString()}
        labelText="line stroke color"
        labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
        labelInfo={
          <Colorpicker
            color={ChartState.subCharts[subchartIdx].yaxis[0].graphs[graphIdx].style?.strokeColor?.[0]}
            onColorSelected={(color) => {
              const action: ReducerActionType<"setLineChartStrokeColor"> = {
                task: "setLineChartStrokeColor",
                params: { color: color, subchartIdx, yaxisIdx: 0, graphIdx },
              };
              (ChartStateDispatch as React.Dispatch<ReducerActionType<"setLineChartStrokeColor">>)(action);
            }}
          />
        }
      />
      {graph.indicator.params.map((param, paramIdx) => (
        <CTreeItem
          nodeId={`${2 + subchartIdx}00-${graphIdx}-${paramIdx}`}
          labelText={param.name}
          labelIcon={<Icon path={mdiIframeVariableOutline} size={1} color="#333" />}
          labelInfo={
            <TextField
              variant="outlined"
              margin="none"
              size="small"
              inputProps={{ style: { padding: 5, width: 50 } }}
              defaultValue={param.val}
              onChange={(e) => {
                const val = parseFloat(e.target.value);

                if (isNaN(val)) return;
                const action: ReducerActionType<"setIndicatorParam"> = {
                  task: "setIndicatorParam",
                  params: { val, subchartIdx, yaxisIdx: 0, graphIdx, paramIdx },
                };
                (ChartStateDispatch as React.Dispatch<ReducerActionType<"setIndicatorParam">>)(action);
              }}
            />
          }
        />
      ))}
    </CTreeItem>
  );
};

export type IndicatorCategoryType = "Average" | "Oszillator" | "Volatility" | "Volume";

export const ChartMenuIndicators = (props: {
  ChartState: CalculatedChartStateType;
  onNavigate: (target: CChartMenuStateType["location"]) => void;
  open: CChartMenuStateType["location"];
  ChartStateDispatch: ChartStateHookType[1];
}) => {
  const { ChartState, onNavigate, open, ChartStateDispatch } = props;
  const theme = useTheme();
  const [FilterCat, setFilterCat] = React.useState(true);

  const categorys: { name: IndicatorCategoryType; icon: string }[] = [
    { name: "Average", icon: mdiDiameterVariant },
    { name: "Oszillator", icon: mdiChartBellCurve },
    { name: "Volatility", icon: mdiArrowExpandVertical },
    { name: "Volume", icon: mdiPoll },
  ];
  const indicators: {
    name: string;
    indicator: AdditionalIndicatorGraphStateType["indicator"];
    category: IndicatorCategoryType;
  }[] = [
    { name: "SMA", indicator: iSMA, category: "Average" },
    { name: "EMA", indicator: iEMA, category: "Average" },
    { name: "RSI", indicator: iRSI, category: "Oszillator" },
    { name: "MACD", indicator: iMACD, category: "Oszillator" },
    { name: "ATR", indicator: iATR, category: "Volatility" },
    { name: "Volume", indicator: iVolume, category: "Volume" },
    { name: "OBV", indicator: iOBV, category: "Volume" },
  ];

  const amtIndicators = ChartState.subCharts
    .map((subchart, subIdx) =>
      subchart.yaxis.map((yaxis, yaxixsIdx) => yaxis.graphs.filter((fgraph) => fgraph.type === "indicator").length)
    )
    .flat()
    .reduce((acc, cur) => acc + cur);

  return (
    <React.Fragment>
      <div
        style={{
          position: "absolute",
          right: 24, //see padding title bar
          zIndex: 2,

          // padding
        }}
      >
        {amtIndicators > 0 ? (
          <div style={{ paddingBottom: 5 }}>
            <ToggleButtonGroup value={"web"} exclusive style={{ borderRadius: 500 }} size="small">
              <ToggleButton
                // value="web"
                style={{
                  borderBottomLeftRadius: 50,
                  textTransform: "none",
                  borderTopLeftRadius: 50,
                  background: open === "indicators" ? theme.palette.secondary.light : "#fff",
                }}
                onClick={() => {
                  onNavigate?.("indicators");
                }}
              >
                <Typography> Add</Typography>
              </ToggleButton>
              <ToggleButton
                value="android"
                style={{
                  borderBottomRightRadius: 50,
                  borderTopRightRadius: 50,
                  background: open === "editIndicator" ? theme.palette.secondary.light : "#fff",
                }}
                onClick={() => {
                  onNavigate?.("editIndicator");
                }}
              >
                Edit
              </ToggleButton>
            </ToggleButtonGroup>
          </div>
        ) : null}
        <div
          style={{
            background: theme.palette.secondary.light,
            border: "1px solid #666",
            borderRadius: 50,
            padding: 5,
          }}
        >
          <IconButton
            size="small"
            style={{ border: "1px solid #fff", margin: "0px 5px" }}
            onClick={() => alert("upcoming")}
          >
            <Icon path={mdiMagnify} size={1} color={theme.palette.primary.contrastText} />
          </IconButton>
          <IconButton
            size="small"
            style={{ border: "1px solid #fff", margin: "0px 5px 0px 0px" }}
            onClick={() => setFilterCat((current) => !current)}
          >
            <Icon
              path={FilterCat ? mdiSortAlphabeticalAscending : mdiFilter}
              size={1}
              color={theme.palette.primary.contrastText}
            />
          </IconButton>
        </div>
      </div>

      <TreeView
        //   className={classes.root}
        //   defaultExpanded={["3"]}
        defaultCollapseIcon={<Icon path={mdiMenuDown} size={1} color="#333" />}
        defaultExpandIcon={<Icon path={mdiMenuRight} size={1} color="#333" />}
        defaultEndIcon={<div style={{ width: 24 }} />}
        //   selected="1"
        //   onNodeSelect={() => {}}
        disableSelection
        // style={{height: 100, overflowY: "scroll"}}
      >
        {open === "editIndicator"
          ? ChartState.subCharts.map((subchart, subchartIdx) =>
              subchart.yaxis.map((yaxis, yaxisIdx) =>
                yaxis.graphs.map((graph, graphIdx) =>
                  isIndicatorGraph(graph) ? (
                    <CIndicatorGraphTreeItem
                      key={`editIndicator-sub-${subchartIdx}-yaxis-${yaxisIdx}-graph-${graphIdx}`}
                      ChartState={ChartState}
                      ChartStateDispatch={ChartStateDispatch}
                      subchartIdx={subchartIdx}
                      yaxisIdx={yaxisIdx}
                      graphIdx={graphIdx}
                      // handleToggleExpanded={handleT}
                    />
                  ) : null
                )
              )
            )
          : !!FilterCat
          ? categorys.map((cat, catIdx) => (
              <CTreeItem
                key={`cat-${catIdx}`}
                nodeId={catIdx.toString()}
                labelText={cat.name}
                labelIcon={
                  <div
                    style={{
                      border: "1px solid #666",
                      borderRadius: 5,
                      marginRight: 10,
                      background: theme.palette.secondary.main,
                      minWidth: 48,
                      height: 48,
                    }}
                  >
                    <Icon path={cat.icon} size={2} color={theme.palette.secondary.contrastText} />
                  </div>
                }
                bgColor={theme.palette.primary.light}
              >
                {indicators
                  .filter((indi) => indi.category === cat.name)
                  .sort((a, b) => (a.name > b.name ? 1 : -1))
                  .map((indi, indiIdx) => (
                    <CTreeItem
                      key={`cat-${catIdx}-indi-${indiIdx}`}
                      nodeId={((catIdx + 1) * 100 + (indiIdx + 1)).toString()}
                      labelText={indi.name}
                      //   labelIcon={<Icon path={mdiFileChartOutline} size={1} color="#333" />}
                      bgColor={theme.palette.primary.light}
                      onLabelClick={onIndicatorLabelClick(indi.indicator, ChartState)}
                    />
                  ))}
              </CTreeItem>
            ))
          : indicators
              .sort((a, b) => (a.name > b.name ? 1 : -1))
              .map((indi, indiIdx) => (
                <CTreeItem
                  key={`cat-indi-${indiIdx}`}
                  nodeId={(indiIdx + 1).toString()}
                  labelText={indi.name}
                  //   labelIcon={<Icon path={mdiFileChartOutline} size={1} color="#333" />}
                  bgColor={theme.palette.primary.light}
                  onLabelClick={onIndicatorLabelClick(indi.indicator, ChartState)}
                />
              ))}
      </TreeView>
    </React.Fragment>
  );
};
