import React from "react";
import { Line, Rect, Shape } from "react-konva";
import { chartStateReducer } from "./ChartState/CChartStateReducer";
import { isIndicatorGraph, CalculatedChartStateType } from "./Types/CChartStateTypes";
import { getDecimals } from "./utils";

export const CChartCrosshair = (props: { fullState: CalculatedChartStateType }) => {
  const { fullState } = props;
  const containerWidth = fullState.containerSize.width - 1;
  const containerHeight = fullState.containerSize.height - 1;
  const { widthYAxis } = fullState.yaxis;
  const { heightXAxis, heightTickMarkLines } = fullState.xaxis;

  let pixYSnap: number | null = null;
  let ySnap: string | null = null;
  if (!!fullState.pointer.move) {
    pixYSnap = fullState.pointer.move.xy[1];
  }
  for (let i = 0; i < fullState.subCharts.length; i++) {
    const subchart = fullState.subCharts[i];
    for (let j = 0; j < subchart.yaxis[0].graphs.length; j++) {
      const graph = subchart.yaxis[0].graphs[j];
      if (!!fullState.pointer?.chartPointer?.move?.snapDatasets?.[0])
        if (fullState.pointer.chartPointer.move?.snapDatasets[0].pixYSnap !== null) {
          pixYSnap = fullState.pointer.chartPointer.move.snapDatasets[0].pixYSnap;
          ySnap = fullState.pointer.chartPointer.move.snapDatasets[0].ySnap;
        }
    }
  }
  let subchartIdx = -1;
  if (!!fullState.pointer.move) {
    const xy = fullState.pointer.move.xy[1];
    subchartIdx = fullState.subCharts.findIndex((val) => val.bottom >= xy && val.top <= xy);
  }
  if (!ySnap && subchartIdx !== -1)
    ySnap = fullState.methods
      .pixToY(fullState.pointer.move?.xy[1] as number, subchartIdx, 0)
      .toFixed(fullState.subCharts[subchartIdx].yaxis[0].decimals);
  if (!!ySnap && subchartIdx !== -1) {
    const curgraph = fullState.subCharts[subchartIdx].yaxis[0].graphs[0];
    if (isIndicatorGraph(curgraph)) {
      if (!!curgraph.indicator.default.decimals)
        ySnap = (
          Math.round(parseFloat(ySnap) * Math.pow(10, curgraph.indicator.default.decimals)) /
          Math.pow(10, curgraph.indicator.default.decimals)
        ).toString();
    }
  }

  // console.log(fullState.pointer?.chartPointer?.move?.xUnlimited, fullState.xaxis.xEnd, fullState.xaxis.xStart);
  // console.log(fullState.pointer.move.xy[1], fullState.subCharts[fullState.subCharts.length - 1].bottom);

  return !!fullState.pointer.move && !!pixYSnap ? (
    <React.Fragment>
      <Line // crosshair x
        x={0}
        y={0}
        points={[
          fullState.methods.xToPix(Math.round(fullState.methods.pixToX(fullState.pointer.move.xy[0]))),
          0,
          fullState.methods.xToPix(Math.round(fullState.methods.pixToX(fullState.pointer.move.xy[0]))),
          containerHeight - heightXAxis + heightTickMarkLines,
        ]}
        stroke="black"
        strokeWidth={1}
      />

      {fullState.pointer.move.xy[1] <= fullState.subCharts[fullState.subCharts.length - 1].bottom ? (
        <React.Fragment>
          <Line // crosshair y
            x={0}
            y={0}
            points={[0, pixYSnap, containerWidth - widthYAxis, pixYSnap]}
            stroke="black"
            strokeWidth={1}
          />
          <Shape // yaxis marker
            sceneFunc={(context, shape) => {
              const ctx = context._context;
              if (!fullState.pointer.move) return;
              const yStart = pixYSnap as number;
              ctx.moveTo(containerWidth - widthYAxis, yStart);
              ctx.lineTo(containerWidth - widthYAxis + 10, yStart + 10);
              ctx.lineTo(containerWidth, yStart + 10);
              ctx.lineTo(containerWidth, yStart + 1);
              ctx.lineTo(containerWidth, yStart - 10);
              ctx.lineTo(containerWidth - widthYAxis + 10, yStart - 10);
              ctx.lineTo(containerWidth - widthYAxis, yStart);
              // (!) Konva specific method, it is very important
              context.fillStrokeShape(shape);
            }}
            stroke="black"
            fill="rgba(199, 0, 57, 1)"
            strokeWidth={1}
          />
          <Shape // yaxis marker text
            sceneFunc={(context, shape) => {
              const ctx = context._context;
              if (!fullState.pointer.move) return;
              const yStart = pixYSnap as number;
              ctx.font = fullState.yaxis.style.fontSize + "px " + fullState.yaxis.style.fontName;
              ctx.fillStyle = fullState.yaxis.style.fontColor;
              ctx.textAlign = "left";
              ctx.textBaseline = "middle";
              ctx.fillText(
                ySnap as string,
                containerWidth - widthYAxis + fullState.yaxis.widthTickmarkLines + 5,
                yStart // additional 5px distance to end of tickmark
              );
              // (!) Konva specific method, it is very important
              context.fillStrokeShape(shape);
            }}
            // stroke="black"
            // strokeWidth={1}
          />
        </React.Fragment>
      ) : null}

      {fullState.pointer?.chartPointer?.move?.xUnlimited <= fullState.xaxis.xEnd &&
      fullState.pointer?.chartPointer?.move?.xUnlimited >= fullState.xaxis.xStart ? (
        <React.Fragment>
          <Rect // xaxis marker rect
            x={fullState.methods.xToPix(Math.round(fullState.methods.pixToX(fullState.pointer.move?.xy[0]))) - 25}
            y={containerHeight - heightXAxis + heightTickMarkLines + 0.5}
            width={50}
            height={25}
            fill="white"
            stroke="black"
            strokeWidth={1}
          />
          <Shape // xaxis marker text
            sceneFunc={(context, shape) => {
              const ctx = context._context;
              if (!fullState.pointer.move) return;
              ctx.font = fullState.xaxis.style.fontSize + "px " + fullState.xaxis.style.fontName;
              ctx.fillStyle = fullState.xaxis.style.fontColor;
              ctx.textAlign = "center";
              ctx.textBaseline = "top";

              ctx.fillText(
                !!fullState.pointer?.chartPointer?.move?.snapDatasets?.[0]
                  ? (fullState.pointer.chartPointer.move.snapDatasets[0].dateString as string)
                  : Math.round(fullState.methods.pixToX(fullState.pointer.move.xy[0])).toString(),
                fullState.methods.xToPix(Math.round(fullState.methods.pixToX(fullState.pointer.move.xy[0]))),
                containerHeight - heightXAxis + heightTickMarkLines + 5 + 0.5 // additional 5px distance to end of tickmark
              );
              // (!) Konva specific method, it is very important
              context.fillStrokeShape(shape);
            }}
            // stroke="black"
            // strokeWidth={1}
          />
        </React.Fragment>
      ) : null}

      {/* ) : null} */}
    </React.Fragment>
  ) : null;
};
