import React from "react";
import {
  createStyles,
  Drawer,
  Grid,
  IconButton,
  makeStyles,
  Theme,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import { mdiClose, mdiFullscreen, mdiHome } from "@mdi/js";
import Icon from "@mdi/react";
import { CalculatedChartStateType, ChartStateHookType } from "../Types/CChartStateTypes";
import { ChartMenuHome } from "./ChartMenuHome";
import { ChartMenuIndicators } from "./ChartMenuIndicators";
import { ChartMenuTools } from "./ChartMenuTools";
import { ChartMenuSettings } from "./ChartMenuSettings";
import { ChartMenuQuickys } from "./ChartMenuQuickys";
import { ChartMenuChart } from "./ChartMenuChart";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      minHeight: "33%",
      border: "1px solid #666",
      borderTopLeftRadius: 10,
      borderTopRightRadius: 10,
    },
    paperDesktop: {
      minHeight: "33%",
      border: "1px solid #666",
      borderBottomRightRadius: 10,
      borderTopRightRadius: 10,
    },
  })
);

export type CChartMenuStateType = {
  location: "menu" | "chart" | "indicators" | "tools" | "settings" | "quickys" | "editIndicator" | "editTool" | null;
  expandedSetting: string[];
};

export const CChartMenu2 = (props: {
  ChartMenuState: CChartMenuStateType;
  onClose: () => void;
  ChartState: CalculatedChartStateType;
  onNavigate: (target: CChartMenuStateType["location"]) => void;
  ChartStateDispatch: ChartStateHookType[1];
  onSettingsExpand: (id: string) => void;
}) => {
  const { onClose, ChartMenuState, ChartState, onNavigate, ChartStateDispatch, onSettingsExpand } = props;
  const theme = useTheme();
  const isMinSm = useMediaQuery("(min-width:600px)");
  const classes = useStyles();

  return (
    <Drawer
      anchor={!!isMinSm ? "left" : "bottom"}
      open={!!ChartMenuState.location}
      classes={{ paper: !!isMinSm ? classes.paperDesktop : classes.paper }}
      ModalProps={{ hideBackdrop: true }}
    >
      <div style={{ background: theme.palette.primary.main, color: theme.palette.primary.contrastText, padding: 10 }}>
        <Grid container alignItems="center" justify="center">
          <Grid item>
            <Typography component="div" variant="h4">
              {ChartMenuState.location === "quickys"
                ? "Quick Tasks"
                : ChartMenuState.location === "editIndicator"
                ? "Edit Indicator"
                : ChartMenuState.location === "indicators"
                ? "Add Indicator"
                : ChartMenuState.location === "editTool"
                ? "Edit Tool"
                : ChartMenuState.location === "tools"
                ? "Add Tool"
                : ChartMenuState.location === "settings"
                ? "Settings"
                : "Chart Menu"}
            </Typography>
          </Grid>
          <Grid item xs></Grid>
          <Grid item>
            <div style={{ height: "2.6rem" }}>
              {!!ChartMenuState.location && ChartMenuState.location !== "menu" ? (
                <IconButton
                  size="small"
                  onClick={() => {
                    onNavigate("menu");
                  }}
                >
                  <Icon path={mdiHome} size={1.75} color={theme.palette.primary.contrastText}></Icon>
                </IconButton>
              ) : null}

              <IconButton
                size="small"
                onClick={() => {
                  //   onClose?.();
                }}
              >
                <Icon path={mdiFullscreen} size={1.75} color={theme.palette.primary.contrastText}></Icon>
              </IconButton>
              <IconButton
                size="small"
                onClick={() => {
                  onClose?.();
                }}
              >
                <Icon path={mdiClose} size={1.75} color={theme.palette.primary.contrastText}></Icon>
              </IconButton>
            </div>
          </Grid>
        </Grid>
      </div>
      <div style={{ padding: 10 }}>
        {ChartMenuState.location === "menu" ? (
          <ChartMenuHome ChartState={ChartState} onNavigate={onNavigate} />
        ) : ChartMenuState.location === "indicators" || ChartMenuState.location === "editIndicator" ? (
          <ChartMenuIndicators
            ChartState={ChartState}
            onNavigate={onNavigate}
            open={ChartMenuState.location}
            ChartStateDispatch={ChartStateDispatch}
          />
        ) : ChartMenuState.location === "tools" || ChartMenuState.location === "editTool" ? (
          <ChartMenuTools
            ChartState={ChartState}
            ChartStateDispatch={ChartStateDispatch}
            onNavigate={onNavigate}
            open={ChartMenuState.location}
          />
        ) : ChartMenuState.location === "settings" ? (
          <ChartMenuSettings
            ChartMenuState={ChartMenuState}
            ChartState={ChartState}
            ChartStateDispatch={ChartStateDispatch}
            onNavigate={onNavigate}
            onSettingsExpand={onSettingsExpand}
          />
        ) : ChartMenuState.location === "quickys" ? (
          <ChartMenuQuickys ChartState={ChartState} onNavigate={onNavigate} ChartStateDispatch={ChartStateDispatch} />
        ) : ChartMenuState.location === "chart" ? (
          <ChartMenuChart ChartState={ChartState} ChartStateDispatch={ChartStateDispatch} />
        ) : null}
      </div>
    </Drawer>
  );
};
