import { Icon } from "@mdi/react";
import { ToggleButton, ToggleButtonGroup, TreeView } from "@material-ui/lab";
import {
  mdiMenuDown,
  mdiMenuRight,
  mdiDiameterVariant,
  mdiChartBellCurve,
  mdiArrowExpandVertical,
  mdiPoll,
  mdiHome,
  mdiFilter,
  mdiMagnify,
  mdiAlphabetical,
  mdiSortAlphabeticalAscending,
  mdiRuler,
  mdiRulerSquareCompass,
  mdiArrowSplitHorizontal,
  mdiBorderColor,
  mdiBullseye,
  mdiHelp,
  mdiMinus,
} from "@mdi/js";
import React from "react";

import { CalculatedChartStateType, ChartStateHookType } from "../Types/CChartStateTypes";
import { ChartDataSeries } from "../Types/CChartTypes";
import { Grid, IconButton, TextField, Typography, useTheme } from "@material-ui/core";
import { CTreeItem } from "../CTreeItem";
import { ReducerActionType } from "../ChartState/CChartStateReducer";
import { Colorpicker } from "../Colorpicker";
import { CChartMenuStateType } from "./ChartMenu";

const onToolLabelClick = (
  ChartStateDispatch: ChartStateHookType[1],
  type: NonNullable<CalculatedChartStateType["draw"]["type"]>
) => {
  const subchartIdx = 0;
  const action: ReducerActionType<"draw"> = { task: "draw", params: { type, subchartIdx } };

  (ChartStateDispatch as React.Dispatch<ReducerActionType<"draw">>)(action);
};

export const CToolTreeItem = (props: {
  subchartIdx: number;
  yaxisIdx: number;
  toolIdx: number;
  ChartState: CalculatedChartStateType;
  ChartStateDispatch: ChartStateHookType[1];
  handleToggleExpanded?: (id: string) => void;
}) => {
  const { subchartIdx, yaxisIdx, toolIdx, ChartState, ChartStateDispatch, handleToggleExpanded } = props;
  const toolIn = ChartState.subCharts?.[subchartIdx]?.yaxis?.[yaxisIdx]?.tools?.[toolIdx];
  const tool = !!toolIn ? toolIn : null;
  if (!tool) return null;

  return (
    <CTreeItem
      key={`tool-${toolIdx}`}
      nodeId={`tool-${subchartIdx}-${toolIdx}`}
      labelText={
        tool.type === "hline"
          ? "horizontal line"
          : tool.type === "vline"
          ? "vertical line"
          : tool.type === "trendline"
          ? "Trendline"
          : "Tool"
      }
      labelIcon={
        <Icon
          path={tool.type === "hline" || tool.type === "vline" || tool.type === "trendline" ? mdiMinus : mdiHelp}
          size={1}
          color="#333"
          rotate={tool.type === "trendline" ? -45 : tool.type === "vline" ? 90 : 0}
        />
      }
      onLabelClick={() => {
        const id = `tool-${subchartIdx}-${toolIdx}`;
        handleToggleExpanded?.(id);
      }}
    >
      {tool.type === "hline" ? (
        <CTreeItem
          key={`tool-${subchartIdx}-y0-${toolIdx}-hlinelevel`}
          nodeId={`tool-${subchartIdx}-y0-${toolIdx}-hlinelevel`}
          labelText={"Y-Level"}
          labelIcon={<Icon path={mdiArrowSplitHorizontal} size={1} color="#333" />}
          labelInfo={
            <TextField
              variant="outlined"
              margin="none"
              size="small"
              inputProps={{ style: { padding: 5, width: 50 } }}
              defaultValue={tool.xy[0][1]}
              onChange={(e) => {
                const val = parseFloat(e.target.value);
                if (isNaN(val)) return;
                const action: ReducerActionType<"setHlineYlevel"> = {
                  task: "setHlineYlevel",
                  params: { val, subchartIdx, yaxisIdx: 0, toolIdx },
                };
                (ChartStateDispatch as React.Dispatch<ReducerActionType<"setHlineYlevel">>)(action);
              }}
            />
          }
        />
      ) : null}
      <CTreeItem
        key={`tool-${subchartIdx}-${toolIdx}-stroke`}
        nodeId={`tool-${subchartIdx}-${toolIdx}-stroke`}
        labelText={"stroke color"}
        labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
        labelInfo={
          <Colorpicker
            color={tool.style.strokeColor}
            onColorSelected={(color) => {
              const action: ReducerActionType<"setToolStrokeColor"> = {
                task: "setToolStrokeColor",
                params: { color: color, subchartIdx, yaxisIdx: 0, toolIdx },
              };
              (ChartStateDispatch as React.Dispatch<ReducerActionType<"setToolStrokeColor">>)(action);
            }}
          />
        }
      />
      <CTreeItem
        key={`tool-${subchartIdx}-${toolIdx}-anchorColor`}
        nodeId={`tool-${subchartIdx}-${toolIdx}-anchorColor`}
        labelText={"anchor color"}
        labelIcon={<Icon path={mdiBullseye} size={1} color="#333" />}
        labelInfo={
          <Colorpicker
            color={tool.style.anchorColor}
            onColorSelected={(color) => {
              const action: ReducerActionType<"setToolAnchorColor"> = {
                task: "setToolAnchorColor",
                params: { color: color, subchartIdx, yaxisIdx: 0, toolIdx },
              };
              (ChartStateDispatch as React.Dispatch<ReducerActionType<"setToolAnchorColor">>)(action);
            }}
          />
        }
      />
    </CTreeItem>
  );
};

export type ToolCategoryType = "Lines" | "Upcoming";

export const ChartMenuTools = (props: {
  ChartState: CalculatedChartStateType;
  onNavigate: (target: CChartMenuStateType["location"]) => void;
  ChartStateDispatch: ChartStateHookType[1];
  open: CChartMenuStateType["location"];
}) => {
  const { ChartState, onNavigate, ChartStateDispatch, open } = props;
  const theme = useTheme();
  const [FilterCat, setFilterCat] = React.useState(true);

  const categorys: { name: ToolCategoryType; icon: string }[] = [
    { name: "Lines", icon: mdiRuler },
    { name: "Upcoming", icon: mdiRulerSquareCompass },
  ];
  const tools: {
    name: string;
    type: NonNullable<CalculatedChartStateType["draw"]["type"]>;
    category: ToolCategoryType;
  }[] = [
    { name: "trendline", type: "trendline", category: "Lines" },
    { name: "horizontal", type: "hline", category: "Lines" },
    { name: "vertical", type: "vline", category: "Lines" },
  ];

  const amtTools = ChartState.subCharts
    .map((subchart) => subchart.yaxis.map((yaxis) => yaxis.tools.length))
    .flat()
    .reduce((acc, cur) => acc + cur);

  return (
    <React.Fragment>
      <div
        style={{
          position: "absolute",
          right: 24, //see padding title bar
          zIndex: 2,

          // padding
        }}
      >
        {amtTools > 0 ? (
          <div style={{ paddingBottom: 5 }}>
            <ToggleButtonGroup value={"web"} exclusive style={{ borderRadius: 500 }} size="small">
              <ToggleButton
                style={{
                  borderBottomLeftRadius: 50,
                  textTransform: "none",
                  borderTopLeftRadius: 50,
                  background: open === "tools" ? theme.palette.secondary.light : "#fff",
                }}
                onClick={() => {
                  onNavigate?.("tools");
                }}
              >
                <Typography> Add</Typography>
              </ToggleButton>
              <ToggleButton
                value="android"
                style={{
                  borderBottomRightRadius: 50,
                  borderTopRightRadius: 50,
                  background: open === "editTool" ? theme.palette.secondary.light : "#fff",
                }}
                onClick={() => {
                  onNavigate?.("editTool");
                }}
              >
                Edit
              </ToggleButton>
            </ToggleButtonGroup>
          </div>
        ) : null}

        <div
          style={{ background: theme.palette.secondary.light, border: "1px solid #666", borderRadius: 50, padding: 5 }}
        >
          <IconButton
            size="small"
            style={{ border: "1px solid #fff", margin: "0px 5px" }}
            onClick={() => alert("upcoming")}
          >
            <Icon path={mdiMagnify} size={1} color={theme.palette.primary.contrastText} />
          </IconButton>
          <IconButton
            size="small"
            style={{ border: "1px solid #fff", margin: "0px 5px 0px 0px" }}
            onClick={() => setFilterCat((current) => !current)}
          >
            <Icon
              path={FilterCat ? mdiSortAlphabeticalAscending : mdiFilter}
              size={1}
              color={theme.palette.primary.contrastText}
            />
          </IconButton>
        </div>
      </div>
      <TreeView
        //   className={classes.root}
        //   defaultExpanded={["3"]}
        defaultCollapseIcon={<Icon path={mdiMenuDown} size={1} color="#333" />}
        defaultExpandIcon={<Icon path={mdiMenuRight} size={1} color="#333" />}
        defaultEndIcon={<div style={{ width: 24 }} />}
        //   selected="1"
        //   onNodeSelect={() => {}}
        disableSelection
        // style={{height: 100, overflowY: "scroll"}}
      >
        {open === "editTool"
          ? ChartState.subCharts.map((subchart, subchartIdx) =>
              subchart.yaxis.map((yaxis, yaxisIdx) =>
                yaxis.tools.map((tool, toolIdx) => (
                  <CToolTreeItem
                    key={`editIndicator-sub-${subchartIdx}-yaxis-${yaxisIdx}-graph-${toolIdx}`}
                    ChartState={ChartState}
                    ChartStateDispatch={ChartStateDispatch}
                    subchartIdx={subchartIdx}
                    yaxisIdx={yaxisIdx}
                    toolIdx={toolIdx}
                  />
                ))
              )
            )
          : !!FilterCat
          ? categorys.map((cat, catIdx) => (
              <CTreeItem
                key={`cat-${catIdx}`}
                nodeId={catIdx.toString()}
                labelText={cat.name}
                labelIcon={
                  <div
                    style={{
                      border: "1px solid #666",
                      borderRadius: 5,
                      marginRight: 10,
                      background: theme.palette.secondary.main,
                      minWidth: 48,
                      height: 48,
                    }}
                  >
                    <Icon path={cat.icon} size={2} color={theme.palette.secondary.contrastText} />
                  </div>
                }
                bgColor={theme.palette.primary.light}
              >
                {tools
                  .filter((tool) => tool.category === cat.name)
                  .sort((a, b) => (a.name > b.name ? 1 : -1))
                  .map((tool, toolIdx) => (
                    <CTreeItem
                      key={`cat-${catIdx}-tool-${toolIdx}`}
                      nodeId={((catIdx + 1) * 100 + (toolIdx + 1)).toString()}
                      labelText={tool.name}
                      //   labelIcon={<Icon path={mdiFileChartOutline} size={1} color="#333" />}
                      bgColor={theme.palette.primary.light}
                      onLabelClick={() => {
                        onToolLabelClick(ChartStateDispatch, tool.type);
                        onNavigate?.(null);
                      }}
                    />
                  ))}
              </CTreeItem>
            ))
          : tools
              .sort((a, b) => (a.name > b.name ? 1 : -1))
              .map((tool, toolIdx) => (
                <CTreeItem
                  key={`cat-tool-${toolIdx}`}
                  nodeId={(toolIdx + 1).toString()}
                  labelText={tool.name}
                  //   labelIcon={<Icon path={mdiFileChartOutline} size={1} color="#333" />}
                  bgColor={theme.palette.primary.light}
                  onLabelClick={() => {
                    onToolLabelClick(ChartStateDispatch, tool.type);
                    onNavigate?.(null);
                  }}
                />
              ))}
      </TreeView>
    </React.Fragment>
  );
};
