import { getDateObject, getDateSubValue } from "./CChartDateTime";
import { ChartPeriodType, DateStatType, PeriodsConstType } from "../Types/CChartTimeTypes";
import { DataSeries } from "../Types/CChartTypes";
import { ChartGraphStateType } from "../Types/CChartStateTypes";

export const chartPeriods: PeriodsConstType = {
  minutes: { period: 60000, scaleMultiplys: [2, 5, 15, 30], range: [60000, 60000] },
  hours: { period: 3600000, scaleMultiplys: [2, 4, 12], range: [3600000, 3600000] },
  days: { period: 86400000, scaleMultiplys: [2], range: [86400000, 86400000] }, // special threatment!
  weeks: { period: 86400000 * 7, scaleMultiplys: [2], range: [259200000, 950400000] }, // only 1 and 2 wheras 2 means 1/2 month
  months: { period: 86400000 * 31, scaleMultiplys: [2, 3, 6], range: [2419200000, 2678400000] }, // months have 28-31 day :/
  years: { period: 86400000 * 365, scaleMultiplys: [], range: [31536000000, 31622400000] },
};

/** determines chartPeriod (restrictions apply, otherwise return null) based on dataPeriod (dataPeriod repres. period with most occurances) */
export const getChartPeriod = (dataPeriod: number): ChartPeriodType | null => {
  const dataPeriodKeys = Object.keys(chartPeriods) as (keyof PeriodsConstType)[];
  for (let i = 0; i < dataPeriodKeys.length; i++) {
    const chartPeriodKey = dataPeriodKeys[i];
    const chartPeriod = chartPeriods[chartPeriodKey].period;
    const chartPeriodRange = chartPeriods[chartPeriodKey].range;
    if (typeof chartPeriod === "number")
      if (dataPeriod === chartPeriod || (dataPeriod >= chartPeriodRange[0] && dataPeriod <= chartPeriodRange[1]))
        return { chartPeriodName: chartPeriodKey, periodsConstIdx: i, chartPeriod: chartPeriod, multiply: 1 };
  }
  return null;
};

export const getOptimalPeriod = (
  data: DataSeries,
  xStart: number,
  xEnd: number,
  metaData: ChartGraphStateType["meta"],
  noDataPortion: number,
  dateStat: DateStatType
): ChartPeriodType | null => {
  if (!metaData.chartPeriod) return null;
  const { chartPeriod, periodsConstIdx, chartPeriodName } = metaData.chartPeriod;

  // const dateStat = getDateStat(data, xStart, xEnd);
  const nCurrent = xEnd - xStart + 1;

  const curDateStat = Object.assign({}, dateStat); //{ ...dateStat };
  const nInDateStat = curDateStat.accAmt[chartPeriodName];
  Object.keys(curDateStat.accAmt).forEach((key, keyIdx) => {
    curDateStat.accAmt[key as keyof DateStatType["accAmt"]] =
      (curDateStat.accAmt[key as keyof DateStatType["accAmt"]] / nInDateStat) * nCurrent;
  });
  const targetIntervals = 10 * (1 - noDataPortion);
  let opt = null;
  let optPeriod = chartPeriodName;
  let optMultiply = 1;
  let optConstIdx = 0;
  for (let i = periodsConstIdx; i < Object.keys(chartPeriods).length; i++) {
    const key = Object.keys(chartPeriods)[i] as keyof PeriodsConstType;
    // higherPeriods += key + ", ";
    if (key in curDateStat.accAmt) {
      for (let multiply of [1, ...chartPeriods[key].scaleMultiplys]) {
        if (opt !== null) {
          if (
            Math.abs(curDateStat.accAmt[key] / multiply - targetIntervals) <
            Math.abs(opt / optMultiply - targetIntervals)
          ) {
            opt = curDateStat.accAmt[key];
            optMultiply = multiply;
            optPeriod = key;
          }
        } else {
          // opt is just once null multiply is in first iteration always 1
          opt = curDateStat.accAmt[key];
          optMultiply = multiply;
          optPeriod = key;
        }
      }
    }
  }
  return {
    chartPeriodName: optPeriod,
    multiply: optMultiply,
    chartPeriod: chartPeriods[optPeriod].period,
    periodsConstIdx: optConstIdx,
  };
};

export const isNewYear = (date: Date, curYearDateStat: DateStatType["years"], periodMultiply: 1 = 1) => {
  // , periodMultiply?: number to be added as parameter -> multiply currently not supported for years
  if (curYearDateStat.length > 1) return false;
  const { day, week, month, year } = getDateObject(date);
  const thisMonthStat = curYearDateStat[0].months[0];
  if (
    thisMonthStat.weeks[0].days[0].day === day &&
    thisMonthStat.weeks[0].week === week &&
    thisMonthStat.month === month
  )
    return true;
  return false;
};

const isNewMonth = (
  date: Date,
  curYearDateStat: DateStatType["years"],
  curMonthIdx: number,
  periodMultiply: 1 | 2 | 3 | 6 = 1
) => {
  if (curYearDateStat.length > 1) return false;
  const { day, week, month, year } = getDateObject(date);
  if ((curMonthIdx + 1) % periodMultiply !== 0) return false;
  if (
    curYearDateStat[0].months[curMonthIdx].weeks[0].days[0].day === day &&
    curYearDateStat[0].months[curMonthIdx].weeks[0].week === week
  )
    return true;
  return false;
};

const isNewIsoWeek = (
  date: Date,
  curYearDateStat: DateStatType["years"],
  curMonthIdx: number,
  curWeekIdx: number,
  periodMultiply: 1 | 2 = 1
) => {
  if (curYearDateStat.length > 1) return false;
  const { day, week, month, year } = getDateObject(date);
  if (
    curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days[0].day === day &&
    curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].week === week
  ) {
    if (periodMultiply === 2) {
      let daysInMonth = 0;
      curYearDateStat[0].months[curMonthIdx].weeks.forEach((val) => (daysInMonth += val.days.length));
      let curDays = 0;
      let opt = 0;
      let optWeekIdx = 0;
      const optimalDaysTarget = Math.round(daysInMonth / 2);
      for (let i = 0; i < curYearDateStat[0].months[curMonthIdx].weeks.length; i++) {
        const weekInMonth = curYearDateStat[0].months[curMonthIdx].weeks[i];
        if (Math.abs(curDays + weekInMonth.days.length - optimalDaysTarget) < Math.abs(opt - optimalDaysTarget)) {
          opt = curDays + weekInMonth.days.length;
          optWeekIdx = i + 1;
        }
        curDays += weekInMonth.days.length;
      }
      if (curWeekIdx === optWeekIdx) return true;
      return false;
    } else if (periodMultiply === 1) {
      if (curWeekIdx === 1 && curYearDateStat[0].months[curMonthIdx].weeks[0].days.length < 3) return false;
      if (
        curWeekIdx === curYearDateStat[0].months[curMonthIdx].weeks.length - 1 &&
        curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days.length < 3
      )
        return false;
      return true;
    }
  }
  return false;
};

const isNewDay = (
  date: Date,
  curYearDateStat: DateStatType["years"],
  curMonthIdx: number,
  curWeekIdx: number,
  curDayIdx: number,
  periodMultiply: 1 | 2 = 1
) => {
  const day = getDateSubValue(date, "days");
  if (curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days[curDayIdx].day === day && periodMultiply === 1)
    return true;
  if (curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days[curDayIdx].day === day && periodMultiply === 2) {
    if (curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days.length <= 3) return false; // weeks with up to 3 days are not diveded
    if (
      (curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days.length === 4 &&
        (curDayIdx === 0 || curDayIdx === 2)) ||
      (curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days.length === 5 &&
        (curDayIdx === 0 || curDayIdx === 2)) ||
      (curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days.length === 6 &&
        (curDayIdx === 0 || curDayIdx === 2 || curDayIdx === 4)) ||
      (curYearDateStat[0].months[curMonthIdx].weeks[curWeekIdx].days.length === 7 &&
        (curDayIdx === 0 || curDayIdx === 2 || curDayIdx === 5))
    )
      return true;
  }
  return false;
};

export const isNewPeriod = (
  //   data: ChartData,
  date: Date,
  dateStat: DateStatType,
  chartPeriod: ChartPeriodType,
  optPeriod: ChartPeriodType
  //   xStart: number,
  //   xEnd: number
) => {
  if (!chartPeriod || !optPeriod || !dateStat) return null;
  const { chartPeriodName: optPeriodName, multiply: optMultiply } = optPeriod;
  const year = getDateSubValue(date, "years");
  const month = getDateSubValue(date, "months");
  const week = getDateSubValue(date, "weeks");
  const day = getDateSubValue(date, "days");

  const curYearStat = dateStat.years.filter((e) => e.year === year);
  // console.log(month, curYearStat, dateStat);
  const curMonthStat = curYearStat[0].months.filter((e) => e.month === month);
  const curMonthStatIdx = curYearStat[0].months.indexOf(curMonthStat[0]);

  const curWeekStat = curMonthStat[0].weeks.filter((e) => e.week === week);
  const curWeekStatIdx = curYearStat[0].months[curMonthStatIdx].weeks.indexOf(curWeekStat[0]);
  const curDayStat = curWeekStat[0].days.filter((e) => e.day === day);
  const curDayStatIdx = curYearStat[0].months[curMonthStatIdx].weeks[curWeekStatIdx].days.indexOf(curDayStat[0]);

  if (chartPeriod.chartPeriodName === optPeriodName && optMultiply === 1) return optPeriodName;
  // if (chartPeriod.chartPeriodName === optPeriodName && optMultiply !== 1) return optPeriodName; //// TODO
  if (optPeriodName === "days") {
    if (isNewYear(date, curYearStat)) return "years";
    if (isNewMonth(date, curYearStat, curMonthStatIdx, 1)) return "months";
    if (isNewIsoWeek(date, curYearStat, curMonthStatIdx, curWeekStatIdx, optMultiply as 1 | 2)) return "weeks";
    if (isNewDay(date, curYearStat, curMonthStatIdx, curWeekStatIdx, curDayStatIdx, optMultiply as 1 | 2))
      return "days";
    return null;
  }
  if (optPeriodName === "weeks") {
    if (isNewYear(date, curYearStat)) return "years";
    if (isNewMonth(date, curYearStat, curMonthStatIdx, 1)) return "months";
    if (isNewIsoWeek(date, curYearStat, curMonthStatIdx, curWeekStatIdx, optMultiply as 1 | 2)) return optPeriodName;
    return null;
  }
  if (optPeriodName === "months") {
    if (curMonthStat[0].weeks[0].days[0].day === day) return optPeriodName;
    else return null;
  }
  if (optPeriodName === "years") {
    if (curYearStat[0].months[0].weeks[0].days[0].day === day) return optPeriodName;
    return null;
  }
  return null;
};
