import React from "react";
import { Circle, Line } from "react-konva";
import { DrawingTool } from "../Types/CChartStateTypes";

export type CTrendlineProps = {
  style?: DrawingTool["style"];
  pixXy: [[number, number], [number, number]];
  mode: "line" | "trendline" | "infiniteLine";
  subchartTop: number;
  subchartBottom: number;
};

export const CTrendline = (props: CTrendlineProps) => {
  const { style, pixXy, mode, subchartTop, subchartBottom } = props;
  if (pixXy.length !== 2) return null;
  const strokeColor = !!style?.strokeColor ? style.strokeColor : "red";
  const anchorColor = !!style?.anchorColor ? style.anchorColor : "#333";
  const modeInt = ["line", "trendline", "infiniteLine"].includes(mode) ? mode : "trendline";
  const pixXyAdjusted: [number, number][] = [];

  if (pixXy[0][0] > pixXy[1][0]) {
    pixXyAdjusted.push([...pixXy[1]], [...pixXy[0]]);
  } else pixXyAdjusted.push([...pixXy[0]], [...pixXy[1]]);

  if (modeInt === "trendline" || mode === "infiniteLine") {
    const dx = pixXyAdjusted[1][0] - pixXyAdjusted[0][0];
    const dy = pixXyAdjusted[1][1] - pixXyAdjusted[0][1];
    const slope = dy / dx;
    const pixYTarget = dy < 0 ? subchartTop : subchartBottom;
    const b = pixXyAdjusted[0][1] - slope * pixXyAdjusted[0][0];
    const pixXTarget = (pixYTarget - b) / slope;
    pixXyAdjusted[1][0] = pixXTarget;
    pixXyAdjusted[1][1] = pixYTarget;
    if (mode === "infiniteLine") {
      const pixYSrc = dy < 0 ? subchartBottom : subchartTop;
      const pixXSrc = (pixYSrc - b) / slope;
      pixXyAdjusted[0][0] = pixXSrc;
      pixXyAdjusted[0][1] = pixYSrc;
    }
  }

  return (
    <React.Fragment>
      <Line
        x={0}
        y={0}
        points={[pixXyAdjusted[0][0], pixXyAdjusted[0][1], pixXyAdjusted[1][0], pixXyAdjusted[1][1]]}
        stroke={strokeColor}
        strokeWidth={1}
      />
      <Circle x={pixXy[0][0]} y={pixXy[0][1]} radius={5} fill={anchorColor} />
      <Circle x={pixXy[1][0]} y={pixXy[1][1]} radius={5} fill={anchorColor} />
    </React.Fragment>
  );
};
