import {
  Button,
  Grid,
  ListItem,
  List,
  Paper,
  Typography,
  useMediaQuery,
  ListItemIcon,
  ListItemText,
  useTheme,
} from "@material-ui/core";
import { mdiChartLine, mdiChartBellCurve, mdiPencilRuler, mdiTune, mdiStarShooting, mdiBroom } from "@mdi/js";
import Icon, { Stack } from "@mdi/react";
import React from "react";
import { CalculatedChartStateType, ChartStateHookType, TypedDispatcher } from "../Types/CChartStateTypes";
import { CChartMenuStateType } from "./ChartMenu";

export const ChartMenuQuickys = (props: {
  ChartState: CalculatedChartStateType;
  ChartStateDispatch: ChartStateHookType[1];
  onNavigate: (target: CChartMenuStateType["location"]) => void;
}) => {
  const { ChartState, onNavigate, ChartStateDispatch } = props;

  const isMinSm = useMediaQuery("(min-width:600px)");
  const theme = useTheme();

  return (
    <React.Fragment>
      {/* <Grid container>
        <Grid item> */}
      <List>
        <ListItem
          button
          onClick={() => {
            ChartStateDispatch({
              task: "clearChart",
              params: { mode: "indicators" },
            }) as unknown as TypedDispatcher<"clearChart">;
          }}
        >
          <ListItemIcon
            style={{
              border: "1px solid #666",
              borderRadius: 5,
              marginRight: 10,
              background: theme.palette.secondary.main,
              minWidth: 48,
              height: 48,
              position: "relative",
              top: 0,
            }}
          >
            <Icon
              path={mdiBroom}
              size={2}
              color={theme.palette.secondary.contrastText}
              style={{ position: "absolute", top: 0, left: 0 }}
              rotate={5}
            />
            <Icon
              path={mdiChartBellCurve}
              size={0.8}
              color={"#fff"}
              style={{ position: "absolute", bottom: 0, right: 0 }}
            />
          </ListItemIcon>
          <ListItemText
            primary="Clear All Indicators"
            primaryTypographyProps={{ variant: "h6", color: "textSecondary" }}
          />
        </ListItem>
        <ListItem
          button
          onClick={() => {
            ChartStateDispatch({
              task: "clearChart",
              params: { mode: "tools" },
            }) as unknown as TypedDispatcher<"clearChart">;
          }}
        >
          <ListItemIcon
            style={{
              border: "1px solid #666",
              borderRadius: 5,
              marginRight: 10,
              background: theme.palette.secondary.main,
              minWidth: 48,
              height: 48,
              position: "relative",
              top: 0,
            }}
          >
            <Icon
              path={mdiBroom}
              size={2}
              color={theme.palette.secondary.contrastText}
              style={{ position: "absolute", top: 0, left: 0 }}
              rotate={5}
            />
            <Icon
              path={mdiPencilRuler}
              size={0.7}
              color={"#fff"}
              style={{ position: "absolute", bottom: 1, right: 1 }}
            />
          </ListItemIcon>
          <ListItemText primary="Clear All Tools" primaryTypographyProps={{ variant: "h6", color: "textSecondary" }} />
        </ListItem>
        <ListItem
          button
          onClick={() => {
            // onNavigate?.("settings");
          }}
        >
          <ListItemIcon
            style={{
              border: "1px solid #666",
              borderRadius: 5,
              marginRight: 10,
              background: theme.palette.secondary.main,
              width: 48,
              height: 48,
            }}
          >
            <Icon path={mdiTune} size={2} color={theme.palette.secondary.contrastText} />
          </ListItemIcon>
          <ListItemText
            primary="Autoscale Y axis (mode to disable upcoming)"
            primaryTypographyProps={{ variant: "h6", color: "textSecondary" }}
          />
        </ListItem>
      </List>
      {/* </Grid>
      </Grid> */}
    </React.Fragment>
  );
};
