import React from "react";
import { Line, Circle } from "react-konva";
import { DrawingTool } from "../Types/CChartStateTypes";

export type CHlineProps = {
  style?: DrawingTool["style"];
  pixXy: [[number, number]];
  mode: "extendRight" | "extendLeft" | "extendLeftRight" | "*extendToSecondPoint";
  pixXstart: number;
  pixXend: number;
};

export const CHline = (props: CHlineProps) => {
  const { style, pixXy, pixXstart, pixXend } = props;
  if (pixXy.length !== 1) return null;
  const strokeColor = !!style?.strokeColor ? style.strokeColor : "red";
  const anchorColor = !!style?.anchorColor ? style.anchorColor : "#333";
  // TODO
  // const modeInt = ["extendRight", "extendLeft", "extendLeftRight", "*extendToSecondPoint"].includes(mode)
  //   ? mode
  //   : "extendLeftRight";
  const pixXyAdjusted: [number, number][] = [];
  pixXyAdjusted.push([pixXstart, pixXy[0][1]], [pixXend, pixXy[0][1]]);

  return (
    <React.Fragment>
      <Line
        x={0}
        y={0}
        points={[pixXyAdjusted[0][0], pixXyAdjusted[0][1], pixXyAdjusted[1][0], pixXyAdjusted[1][1]]}
        stroke={strokeColor}
        strokeWidth={1}
      />
      <Circle x={pixXy[0][0]} y={pixXy[0][1]} radius={5} fill={anchorColor} />
    </React.Fragment>
  );
};
