import React from "react";
import { Line } from "react-konva";
import { isIndicatorDataset } from "../Types/CChartTypes";
import { CalculatedChartStateType, isIndicatorGraph } from "../Types/CChartStateTypes";
import { isNullish } from "../utils";

export type ClineChartType = {
  fullState: CalculatedChartStateType;
  subchartIdx: number;
  yaxisIdx: number;
  graphIdx: number;
  indSeriesIdx?: number;
};
export const CLineChart = (props: ClineChartType) => {
  const { fullState, subchartIdx, yaxisIdx, graphIdx, indSeriesIdx: indSeriesIdxIn } = props;
  const indSeriesIdx = isNullish(indSeriesIdxIn) ? 0 : indSeriesIdxIn;
  const yaxis = fullState.subCharts[subchartIdx].yaxis[yaxisIdx];
  const graph = fullState.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx];
  const graphVals: (number | null)[] = [];
  if (Array.isArray(graph.curData) && graph.curData.length > 1) {
    graphVals.push(
      ...(graph.curData
        .map((val, idx) => {
          let price;
          if (isIndicatorDataset(val)) price = val.prices[indSeriesIdx];
          else price = val.close;
          if (price === null) return [null, null];
          return [fullState.xaxis.widthPerTick * idx, fullState.methods.yToPix(price, subchartIdx, 0, 0)];
        })
        .flat()
        .filter((val) => val !== null) as number[])
    );
  }
  const graphValsNN = graphVals.filter((val) => val !== null) as number[];

  return (
    <Line
      key={`sub${subchartIdx}_graph${graphIdx}`}
      x={fullState.xaxis.pixXStart}
      y={fullState.methods.yToPix(0, subchartIdx, 0) - fullState.subCharts[subchartIdx].bottom}
      points={[...graphValsNN]}
      stroke={isIndicatorGraph(graph) ? graph.style.strokeColor[indSeriesIdx] : graph.style.strokeColor[0]}
      strokeWidth={1}
    />
  );
};
