import React from "react";
import { Shape } from "react-konva";
import { LineChartDataset } from "../Types/CChartTypes";
import { CalculatedChartStateType, isIndicatorGraph } from "../Types/CChartStateTypes";

export type CBarChartType = {
  fullState: CalculatedChartStateType;
  subchartIdx: number;
  yaxisIdx: number;
  graphIdx: number;
  // stroke: string;
  // strokeWidth: number;
};
export const CBarChart = (props: CBarChartType) => {
  const { fullState, subchartIdx, yaxisIdx, graphIdx } = props;
  const strokeWidth = 1;
  const graph = fullState.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx];
  return (
    <Shape
      sceneFunc={(context, shape) => {
        context.beginPath();
        (fullState.subCharts[0].yaxis[0].graphs[0].curData as LineChartDataset[]).forEach((dataset, datasetIdx) => {
          if (!dataset.volume) return;
          const xPix = fullState.methods.xToPix(datasetIdx, 0) + fullState.xaxis.pixXStart;
          const yPixVol = Math.round(fullState.methods.yToPix(dataset.volume, subchartIdx, yaxisIdx, 0));
          const yPix0 = Math.round(fullState.methods.yToPix(0, subchartIdx, yaxisIdx, 0));

          context.moveTo(xPix + 0.5, yPix0 + 0.5);
          context.lineTo(xPix + 0.5, yPixVol + 0.5);
        });
        // (!) Konva specific method, it is very important
        context.closePath();
        context.fillStrokeShape(shape);
      }}
    
      stroke={isIndicatorGraph(graph) ? graph.style.strokeColor[0] : graph.style.strokeColor[0]}
      strokeWidth={strokeWidth}
    />
  );
};
