import { createChartGraphModel } from "./CChartModel";
import {
  DataSeries,
  ChartDataset,
  IndicatorDataset,
  isCandleChartDataset,
  isVolumeDataset,
  IndicatorDataSeries,
  ChartDataSeries,
  isLineChartDataset,
  isIndicatorDataset,
} from "./Types/CChartTypes";
import { AdditionalIndicatorGraphStateType, isChartGraph } from "./Types/CChartStateTypes";

export const iSMACalc = (
  srcChartData: DataSeries,
  srcChartName: string,
  period: number = 20,
  applyOn: "open" | "close" | "high" | "low" = "close"
): IndicatorDataSeries => {
  const indicatorData: IndicatorDataSeries =
    srcChartData.map((srcDataset, srcDatasetIdx) => {
      if (srcDatasetIdx < period - 1) return { prices: [null], date: srcDataset.date };
      let singleSmaAcc = 0;
      srcChartData.slice(srcDatasetIdx - period + 1, srcDatasetIdx + 1).forEach((selSrcDataset) => {
        if (isLineChartDataset(selSrcDataset)) singleSmaAcc += selSrcDataset.close;
        else if (isIndicatorDataset(selSrcDataset))
          singleSmaAcc += !!selSrcDataset.prices[0] ? selSrcDataset.prices[0] : 0;
      });
      return { prices: [singleSmaAcc / period], date: srcDataset.date };
    }) || [];
  return indicatorData;
  // const indicatorData: IndicatorDataset[] = [];
  // for (let i = 0; i < srcChartData.length; i++) {
  //   if (i < period - 1) {
  //     indicatorData.push({ prices: [null], date: srcChartData[i].date });
  //     continue;
  //   }
  //   let singleSmaAcc = 0;
  //   for (let j = i - period + 1; j <= i; j++) {
  //     singleSmaAcc += srcChartData[j].close;
  //   }
  //   indicatorData.push({ prices: [singleSmaAcc / period], date: srcChartData[i].date });
  // }
  // return indicatorData;
};

export const iSMA: AdditionalIndicatorGraphStateType["indicator"] = {
  indicatorFn: iSMACalc,
  name: "SMA",
  params: [{ name: "period", val: 10 }],
  default: { params: [{ name: "period", val: 10 }], newSubchart: false },
  type: "line",
};

export const iEMACalc = (
  srcChartData: DataSeries,
  srcChartName: string,
  period: number = 20,
  applyOn: "open" | "close" | "high" | "low" = "close"
): IndicatorDataSeries => {
  const indicatorData: IndicatorDataSeries = [];
  for (let i = 0; i < srcChartData.length; i++) {
    if (i < period - 1) {
      indicatorData.push({ prices: [null], date: srcChartData[i].date });
      continue;
    } else if (i === period - 1) {
      let initSMA = 0;
      for (let j = 0; j < period; j++) {
        const dataset = srcChartData[j];
        const price = isLineChartDataset(dataset)
          ? dataset.close
          : isIndicatorDataset(dataset)
          ? dataset.prices[0] || 0
          : 0;
        initSMA += price;
      }
      initSMA /= period;
      indicatorData.push({ prices: [initSMA], date: srcChartData[i].date });
    } else {
      const multiplier = 2 / (period + 1);
      const dataset = srcChartData[i];
      const price = isLineChartDataset(dataset)
        ? dataset.close
        : isIndicatorDataset(dataset)
        ? dataset.prices[0] || 0
        : 0;
      const ema =
        (price - (indicatorData[i - 1].prices[0] as number)) * multiplier + (indicatorData[i - 1].prices[0] as number);
      indicatorData.push({ prices: [ema], date: srcChartData[i].date });
    }
  }
  return indicatorData;
};
export const iEMA: AdditionalIndicatorGraphStateType["indicator"] = {
  indicatorFn: iEMACalc,
  name: "EMA",
  params: [{ name: "period", val: 10 }],
  default: { params: [{ name: "period", val: 10 }], newSubchart: false },
  type: "line",
};

export const iATRCalc = (
  srcChartData: ChartDataset[],
  srcChartName: string,
  period: number = 20,
  applyOn: "open" | "close" | "high" | "low" = "close"
): IndicatorDataSeries => {
  const indicatorData: IndicatorDataset[] = [];
  for (let i = 0; i < srcChartData.length; i++) {
    if (i < period) {
      indicatorData.push({ prices: [null], date: srcChartData[i].date });
      continue;
    }
    let singleValAcc = 0;
    for (let j = i - period + 1; j <= i; j++) {
      const dataset = srcChartData[j];
      const dataset1 = srcChartData[j - 1];
      let singleAtr = 0;
      if (isCandleChartDataset(dataset) && isCandleChartDataset(dataset1)) {
        const hl = dataset.high - dataset.low;
        const hc1 = Math.abs(dataset.high - dataset1.close);
        const lc1 = Math.abs(dataset.low - dataset1.close);
        singleAtr = Math.max(hl, hc1, lc1);
      } else {
        const dc = Math.abs(dataset.close - dataset1.close);
      }
      singleValAcc += singleAtr;
    }
    indicatorData.push({ prices: [singleValAcc / period], date: srcChartData[i].date });
  }
  return indicatorData;
};
export const iATR: AdditionalIndicatorGraphStateType["indicator"] = {
  indicatorFn: iATRCalc,
  name: "ATR",
  params: [{ name: "period", val: 14 }],
  default: { params: [{ name: "period", val: 14 }], newSubchart: true },
  type: "line",
};

export const iRSICalc = (
  srcChartData: ChartDataset[],
  srcChartName: string,
  period: number = 20,
  applyOn: "open" | "close" | "high" | "low" = "close"
): IndicatorDataSeries => {
  const indicatorData: IndicatorDataset[] = [];
  let calcData: { lastUpEma: number; lastDwnEma: number } | null = null;
  for (let i = 0; i < srcChartData.length; i++) {
    if (i < period) {
      indicatorData.push({ prices: [null], date: srcChartData[i].date });
      if (i === 0) continue;
    }
    const dataset = srcChartData[i];
    const dataset1 = srcChartData[i - 1];
    const dClose = dataset.close - dataset1.close;
    const up = dClose > 0 ? dClose : 0;
    const dwn = dClose < 0 ? -dClose : 0;
    const w = 1 / period;
    if (i === 1) {
      calcData = { lastUpEma: up, lastDwnEma: dwn };
    }
    if (!calcData || i < period) {
      continue;
    }
    const upSmoothed = up * w + (1 - w) * calcData.lastUpEma;
    calcData.lastUpEma = upSmoothed;
    const dwnSmoothed = dwn * w + (1 - w) * calcData.lastDwnEma;
    calcData.lastDwnEma = dwnSmoothed;
    const rsi = dwnSmoothed === 0 ? 100 : 100 - 100 / (1 + upSmoothed / dwnSmoothed);
    if (i < period) indicatorData.push({ prices: [null], date: srcChartData[i].date });
    else indicatorData.push({ prices: [rsi], date: srcChartData[i].date });
  }
  return indicatorData;
};
export const iRSI: AdditionalIndicatorGraphStateType["indicator"] = {
  indicatorFn: iRSICalc,
  name: "RSI",
  params: [{ name: "period", val: 14 }],
  default: { params: [{ name: "period", val: 14 }], newSubchart: true, decimals: 2, fixedYScale: [0, 100] },
  type: "line",
};

export const iOBVCalc = (srcChartData: ChartDataset[]): IndicatorDataSeries => {
  const indicatorData: IndicatorDataset[] = [];
  for (let i = 0; i < srcChartData.length; i++) {
    const dataset = srcChartData[i];
    if (i === 0 && isVolumeDataset(dataset)) {
      indicatorData.push({ prices: [dataset.volume], date: dataset.date });
      continue;
    }
    const dataset1 = srcChartData[i - 1];
    if (!isVolumeDataset(dataset) || !isVolumeDataset(dataset1)) {
      indicatorData.push({ prices: [null], date: srcChartData[i].date });
      continue;
    }
    const obv = dataset.volume + dataset1.volume;
    indicatorData.push({ prices: [obv], date: srcChartData[i].date });
  }
  return indicatorData;
};
export const iOBV: AdditionalIndicatorGraphStateType["indicator"] = {
  name: "OBV",
  type: "line",
  params: [],
  default: { params: [], newSubchart: true, decimals: 0 },
  indicatorFn: iOBVCalc,
};

export const iVolumeCalc = (srcChartData: ChartDataset[]): IndicatorDataSeries => {
  const indicatorData: IndicatorDataset[] = [];
  for (let i = 0; i < srcChartData.length; i++) {
    const dataset = srcChartData[i];
    if (isVolumeDataset(dataset)) {
      indicatorData.push({ prices: [dataset.volume], date: dataset.date });
    } else {
      indicatorData.push({ prices: [0], date: dataset.date });
    }
  }
  return indicatorData;
};
export const iVolume: AdditionalIndicatorGraphStateType["indicator"] = {
  indicatorFn: iVolumeCalc,
  name: "Volume",
  params: [],
  default: { params: [], newSubchart: true, decimals: 0 },
  type: "bars",
};

export const iMACDCalc = (
  srcChartData: ChartDataset[],
  srcChartName: string,
  periodEmaShort: number = 12,
  periodEmaLong: number = 26,
  periodSignal: number = 9,
  applyOn: "open" | "close" | "high" | "low" = "close"
): IndicatorDataSeries => {
  const indicatorData: IndicatorDataSeries = [];
  const emaShort = iEMACalc(srcChartData, srcChartName, periodEmaShort);
  const emaLong = iEMACalc(srcChartData, srcChartName, periodEmaLong);

  const macd: IndicatorDataSeries = emaShort.map((val, valIdx) => {
    const emaLongVal = emaLong[valIdx].prices[0];
    const emaShortVal = val.prices[0];
    const macd = emaShortVal === null || emaLongVal === null ? null : emaShortVal - emaLongVal;
    return {
      prices: [macd],
      date: srcChartData[valIdx].date,
      priceLabels: ["EMA"],
    };
  });
  //   // const emaIn = ema.map((ema1, ema1Idx) => ({ close: ema1.prices[0], date: ema1.date }));
  const signal = iEMACalc(macd, srcChartName, periodSignal);
  const macdComplete = macd.map((macdVal, macdIdx) => {
    const macdValPrice = macdVal.prices[0];
    const signalVal = signal[macdIdx].prices[0];
    const macdHistogramm = macdValPrice === null || signalVal === null ? null : macdValPrice-signalVal; 
    return { ...macdVal, prices: [macdValPrice, signalVal, macdHistogramm] };
  });
  return macdComplete;
};

export const iMACD: AdditionalIndicatorGraphStateType["indicator"] = {
  indicatorFn: iMACDCalc,
  name: "MACD",
  params: [
    { name: "period short", val: 12 },
    { name: "period long", val: 26 },
    { name: "signal period", val: 9 },
  ],
  default: {
    params: [
      { name: "period short", val: 12 },
      { name: "period long", val: 26 },
      { name: "signal period", val: 9 },
    ],
    newSubchart: true,
    // decimals: 0,
  },
  type: "line",
};
