/** Data types */

import {
  CommonGestureState,
  Coordinates,
  DragState,
  FullGestureState,
  SharedGestureState,
} from "react-use-gesture/dist/types";
import { ExcludeEmpty, ReducerActionTaskType, ReducerActionType } from "../ChartState/CChartStateReducer";
import { ChartPeriodType, DateStatType } from "./CChartTimeTypes";
import { ChartDataSeries, ChartDataset, DataSeries, IndicatorDataSeries, IndicatorDataset } from "./CChartTypes";

export type UseGestureStateType = {
  drag: CommonGestureState & Coordinates & DragState & SharedGestureState;
  move: Omit<FullGestureState<"move">, "event"> & {
    event: React.PointerEvent<Element> | PointerEvent;
  };
  hover: Omit<FullGestureState<"move">, "event"> & {
    event: React.PointerEvent<Element> | PointerEvent;
  };
  wheel: CommonGestureState & Coordinates;
  dragEnd: CommonGestureState & Coordinates & DragState & SharedGestureState;
};

/** Interactive State */
export type InteractiveContainerSizeType = {
  top: number;
  left: number;
  width: number;
  height: number;
  init: boolean;
};

export type InteractivePointerType = {
  isHovering: boolean;
  move: { isMoving: boolean; xy: [number, number] };
  drag: {
    isDragging: boolean;
    xy: [number, number];
    initial: [number, number];
    delta: [number, number];
    first: boolean;
    last: boolean;
    movementInitial: [number, number];
  };
  dragPointerUp: {
    isDragPointerUp: boolean;
    xy: [number, number];
    initial: [number, number];
    first: boolean;
    last: boolean;
    movementInitial: [number, number];
  };
  wheel: { isWheeling: boolean; delta: [number, number] };
  chartPointer: {
    move: {
      pixX: number;
      pixXSnap: number;
      pixY: number;
      x: number|null;
      xUnlimited : number;
      xDateString: string;
      subchartIdx: number;
      snapDatasets: {
        yaxisIdx: number;
        graphIdx: number;
        y: number;
        ySnap: string | null;
        pixYSnap: number | null;
        data: ChartDataset | IndicatorDataset;
        dateString: string;
      }[];
    };
    click: {
      clickedSubchartIdx: number | null;
    };
  };
};

export type InteractiveXaxisCalcs = {
  xStart: number;
  pixXStart: number;
  xEnd: number;
  pixXEnd: number;
  widthPerTick: number;
  startNoDataPeriod: number;
  endNoDataPeriod: number;
  noDataPeriodPortion: number;
  xAxisTicks: { x: number; dateString: string }[];
  optPeriodRes: ChartPeriodType | null;
};

export type InteractiveXaxisType = {
  totalTranslatedX: number;
  totalScaleX: number; //only int
  scaledWidthPerTick: number;
} & InteractiveXaxisCalcs;

export type InteractiveStateType = {
  pointer: InteractivePointerType;
  containerSize: InteractiveContainerSizeType;
  xaxis: InteractiveXaxisType;
};

///////////////////////////////////////

export type ChartPreStateRefType = InteractiveStateType & {
  lastUpdate: Date;
  shallUpdate: ("drag" | "pointerMove" | "wheel" | "dragEnd" | "containerResize" | "deps")[];
  tmp: {
    scaleInitWidthPerTick: number | null;
    scaleInitTranslatedX: number;
    amtLastSubcharts: number;
    lastMainChartData: ChartDataSeries;
    dragActionType:
      | null
      | { type: "translate" | "scale" }
      | { type: "resizeSubchart"; res: number; initBottom: number }
      | { type: "editTool"; res: [number, number, number, number][]; init: [number, number] }
      | { type: "drawTool"; init: [number, number] };
  };
  userInteractedChartState: any;
};

/** SubCharts State */
export type GraphStateType<AdditionalGraphProps = {}> = {
  data: DataSeries;
  style: {
    strokeColor: string[];
  };
  name: string;
  type: "chart" | "indicator" | "formula";
  decimals: number;
  curData?: GraphStateType["data"];
  yMax: number;
  yMaxExact: number;
  yMin: number;
  yMinExact: number;
  // drawPixXy: [number, number][];
  datasets: {
    first: {
      x: number;
      pixX: number;
      data: ChartDataset | IndicatorDataset;
      dateString: string;
    } | null;
    last: {
      x: number;
      pixX: number;
      data: ChartDataset | IndicatorDataset;
      dateString: string;
    } | null;
  };
} & AdditionalGraphProps;

export type AdditionalChartGraphStateType = {
  data: ChartDataSeries;
  dateStat: DateStatType;
  meta: {
    nData: number;
    chartPeriod: ChartPeriodType | null;
    dataPeriod: number;
    periodGuessConfidence: number;
    tradingDaysPerWeek: number;
    tradingDaysPerWeekGuessConfidence: number;
    type: "candlechart" | "linechart";
  };
  style: {
    candleStrokeColor: string;
    candleDownColor: string;
    candleUpColor: string;
  };
};

export type AdditionalIndicatorGraphStateType = {
  data: IndicatorDataSeries;
  indicator: {
    name: string;
    params: { name: string; val: number }[];
    type: "line" | "bars";
    default: {
      params: { name: string; val: number }[];
      newSubchart: boolean;
      fixedYScale?: [number, number]; //
      decimals?: number; //
    };
    indicatorFn: (chartData: ChartDataset[], chartName: string, ...other: any[]) => IndicatorDataSeries;
    
  };
};

export type ChartGraphStateType = GraphStateType<AdditionalChartGraphStateType>;
export type IndicatorGraphStateType = Omit<GraphStateType, "data" | "style"> &
  AdditionalIndicatorGraphStateType & {
    style: {
      strokeColor: string[];
    };
  };

export const isIndicatorGraph = (graph: GraphStateType): graph is GraphStateType<AdditionalIndicatorGraphStateType> => {
  if (graph.type === "indicator") return true;
  return false;
};
export const isChartGraph = (graph: GraphStateType): graph is GraphStateType<AdditionalChartGraphStateType> => {
  if (graph.type === "chart") return true;
  return false;
};
export type DrawingTool = {
  xy: [number, number][];
  type: "hline" | "vline" | "trendline";
  style: {
    strokeColor: string;
    anchorColor: string;
  };
  params?: { [key: string]: any };
};

export type userInteractsSubchartStateType = {
  yaxis: {
    graphs: {
      data?: DataSeries;
      style: {
        strokeColor: string[];
      };
      name: string;
      type: "chart" | "indicator" | "formula";
      decimals: number;
    }[];
    tools: Pick<DrawingTool, "params" | "type" | "style">[];
    decimals: number;
  }[];
  // top: number;
  // bottom: number;
}[];

export type SubchartStateType = {
  yaxis: YaxisStateType[];
  top: number;
  bottom: number;
};

export type YaxisStateType<pure = "false"> = {
  graphs: GraphStateType[];
  tools: DrawingTool[];
  decimals: number;

  optIntervalY: number;
  heightPerPt: number; // yaxis
  translatedY: number; // yaxis
  drawTicks: { pixY: number; label: string }[];
};

/** ChartState */

export type ChartStateType = {
  xaxis: {
    heightXAxis: number;
    heightTickMarkLines: number;
    style: {
      fillColor: string;
      strokeColor: string;
      fontColor: string;
      fontSize: number;
      fontName: string;
    };
  };
  yaxis: {
    widthYAxis: number;
    widthTickmarkLines: number;
    style: {
      fillColor: string;
      strokeColor: string;
      fontColor: string;
      fontSize: number;
      fontName: string;
    };
  };
  grid: {
    useGridX: boolean;
    useGridY: boolean;
    style: {
      strokeColor: string;
      strokeStyle: string;
    };
  };
  backgroundColor: string;
  fullscreen: boolean;
  subCharts: SubchartStateType[];
  draw: Partial<Omit<DrawingTool, "xy">> & { isDrawing: boolean; pixXy: [number, number][] };
} & InteractiveStateType;

/** CalculatedChartState and Hook */

export type CalculatedChartStateType = ChartStateType & {
  methods: {
    addSubchart: (
      chartSeries: DataSeries,
      chartName: string,
      indicator?: AdditionalIndicatorGraphStateType["indicator"],
      reset?: boolean,
    ) => void;
    addGraph: (
      chartSeries: ChartDataset[],
      chartName: string,
      subchartIdx: number,
      indicator?: AdditionalIndicatorGraphStateType["indicator"]
    ) => void;
    getSubchart: (subchartIdx: number) => SubchartStateType;
    getYaxis: (subchartIdx: number, yaxisIdx: number) => YaxisStateType;
    getGraph: (subchartIdx: number, yaxisIdx: number, graphIdx: number) => GraphStateType;
    xToPix: (x: number, translatedX?: number) => number;
    pixToX: (pixX: number, translatedX?: number) => number;
    yToPix: (y: number, subchartIdx: number, yaxisIdx: number, translatedY?: number) => number;
    pixToY: (pixY: number, subchartIdx: number, yaxisIdx: number, translatedY?: number) => number;
  };
  // drawProps: { graphs: number[][]; yaxis: { y: number; label: string }[][] }[];
};
export type TypedDispatcher<Action extends ReducerActionTaskType> = React.Dispatch<ReducerActionType<Action>>;
export type ChartStateHookType<Action extends ReducerActionTaskType = ReducerActionTaskType> = [
  CalculatedChartStateType,
  TypedDispatcher<Action>
];
