import React from "react";
import { Shape } from "react-konva";
import { CandleChartDataset } from "../Types/CChartTypes";
import { CalculatedChartStateType, isChartGraph } from "../Types/CChartStateTypes";

export type CCandleChartType = {
  fullState: CalculatedChartStateType;
};

export const CCandleChart = (props: CCandleChartType) => {
  const { fullState } = props;
  const yaxis = fullState.subCharts[0].yaxis[0];
  const graph = fullState.subCharts[0].yaxis[0].graphs[0];
  if (!isChartGraph(graph) || !graph || !graph.curData) return null;

  let curData: (CandleChartDataset & {
    idx: number;
  })[] = [];
  let dxHalfCandle = 0;
  if (0 in fullState.subCharts)
    if (0 in fullState.subCharts[0].yaxis)
      if (0 in fullState.subCharts[0].yaxis[0].graphs) {
        curData = graph.curData as unknown as (CandleChartDataset & {
          idx: number;
        })[];
        curData.forEach((val, valIdx) => (val.idx = valIdx));
        dxHalfCandle =
          fullState.xaxis.widthPerTick <= 2 ? 0 : Math.floor(0.35 * (fullState.xaxis.widthPerTick - 1));
      }

  return (
    <React.Fragment>
      <Shape // candle wicks
        sceneFunc={(context, shape) => {
          context.beginPath();
          (graph.curData as CandleChartDataset[]).forEach((dataset, datasetIdx) => {
            const xPix = fullState.methods.xToPix(datasetIdx, 0) + fullState.xaxis.pixXStart;
            const yPixHigh = Math.round(
              fullState.methods.yToPix(dataset.high, 0, 0)
            );
            const yPixLow = Math.round(
              fullState.methods.yToPix(dataset.low, 0, 0)
            );
            const yPixClose = Math.round(
              fullState.methods.yToPix(dataset.close, 0, 0)
            );
            context.moveTo(xPix + 0.5, yPixLow + 0.5);
            context.lineTo(xPix + 0.5, yPixHigh + 0.5);
            if (dataset.close === dataset.open) {
              context.moveTo(xPix - 2, yPixClose);
              context.lineTo(xPix + 2, yPixClose);
            }
          });
          // (!) Konva specific method, it is very important
          context.closePath();
          context.fillStrokeShape(shape);
        }}
        stroke={graph.style.candleStrokeColor}
        strokeWidth={1}
      />
      <Shape // candle bodys upward
        sceneFunc={(context, shape) => {
          context.beginPath();
          curData
            .filter((val) => val.close >= val.open)
            .forEach((gainDataset, gainDatasetIdx) => {
              const xPix =
                fullState.methods.xToPix(gainDataset.idx, 0) + fullState.xaxis.pixXStart;
              const yPixOpen = Math.round(
                fullState.methods.yToPix(
                  gainDataset.open,
                  0,
                  0,
                )
              );
              const yPixClose = Math.round(
                fullState.methods.yToPix(
                  gainDataset.close,
                  0,
                  0,
                )
              );
              if (gainDataset.close !== gainDataset.open) {
                if (dxHalfCandle !== 0) {
                  const maxWidth =
                    xPix + dxHalfCandle > fullState.xaxis.pixXEnd
                      ? fullState.xaxis.pixXEnd - xPix + dxHalfCandle
                      : dxHalfCandle * 2;
                  context.rect(xPix + 0.5 - dxHalfCandle, yPixOpen + 0.5, maxWidth, yPixClose - yPixOpen);
                } else if (dxHalfCandle === 0) {
                  context.moveTo(xPix + 0.5, yPixClose);
                  context.lineTo(xPix + 0.5, yPixOpen);
                }
              }
              context.closePath();
              context.fillStrokeShape(shape);
            });
        }}
        stroke={graph.style.candleStrokeColor}
        fill={graph.style.candleUpColor}
        strokeWidth={1}
      />
      <Shape // candle bodys downward
        sceneFunc={(context, shape) => {
          context.beginPath();
          curData
            .filter((val) => val.close < val.open)
            .forEach((gainDataset, gainDatasetIdx) => {
              const xPix =
                fullState.methods.xToPix(gainDataset.idx, 0) + fullState.xaxis.pixXStart;
              const yPixOpen = Math.round(
                fullState.methods.yToPix(
                  gainDataset.open,
                  0,
                  0,
                )
              );
              const yPixClose = Math.round(
                fullState.methods.yToPix(
                  gainDataset.close,
                  0,
                  0,
                )
              );
              if (gainDataset.close !== gainDataset.open) {
                if (dxHalfCandle !== 0) {
                  const maxWidth =
                    xPix + dxHalfCandle > fullState.xaxis.pixXEnd
                      ? fullState.xaxis.pixXEnd - xPix + dxHalfCandle
                      : dxHalfCandle * 2;
                  context.rect(xPix + 0.5 - dxHalfCandle, yPixOpen + 0.5, maxWidth, yPixClose - yPixOpen);
                } else if (dxHalfCandle === 0) {
                  context.moveTo(xPix + 0.5, yPixClose);
                  context.lineTo(xPix + 0.5, yPixOpen);
                }
              }
              context.closePath();
              context.fillStrokeShape(shape);
            });
        }}
        stroke={graph.style.candleStrokeColor}
        fill={graph.style.candleDownColor}
        strokeWidth={1}
      />
    </React.Fragment>
  );
};
