import { getDateSubValue } from "./CChartDateTime";
import {
  DateStatType,
  PeriodsConstType,
  DateStatYearType,
  DateStatMonthType,
  DateStatWeekType,
  DateStatDayType,
} from "../Types/CChartTimeTypes";
import { DataSeries } from "../Types/CChartTypes";

export const getDateStat = (data: DataSeries, xStart: number, xEnd: number) => {
  let stat: DateStatType = { years: [], accAmt: { years: 0, months: 0, weeks: 0, days: 0, hours: 0, minutes: 0 } };

  const makeSingleDayStat = (day: number): DateStatDayType => ({ day: day, hours: [] });
  const makeSingleWeekStat = (week: number, day: number): DateStatWeekType => ({
    week: week,
    days: [makeSingleDayStat(day)],
  });
  const makeSingleMonthStat = (month: number, week: number, day: number): DateStatMonthType => ({
    month: month,
    weeks: [makeSingleWeekStat(week, day)],
  });
  const makeSingleYearStat = (year: number, month: number, week: number, day: number): DateStatYearType => ({
    year: year,
    months: [makeSingleMonthStat(month, week, day)],
  });
  const incrementPeriodAccAmt = (period: keyof PeriodsConstType) => {
    if (period === "years") stat.accAmt.years++;
    if (["years", "months"].includes(period)) stat.accAmt.months++;
    if (["years", "months", "weeks"].includes(period)) stat.accAmt.weeks++;
    if (["years", "months", "weeks", "days"].includes(period)) stat.accAmt.days++;
  };

  for (let i = xStart; i <= xEnd; i++) {
    const year = getDateSubValue(data[i].date, "years");
    const month = getDateSubValue(data[i].date, "months");
    const week = getDateSubValue(data[i].date, "weeks");
    const day = getDateSubValue(data[i].date, "days");

    const addPeriod = (period: keyof PeriodsConstType, curStatIdx: number[]) => {
      if (period === "years") stat.years.push(makeSingleYearStat(year, month, week, day));
      if (period === "months") stat.years[curStatIdx[0]].months.push(makeSingleMonthStat(month, week, day));
      if (period === "weeks") stat.years[curStatIdx[0]].months[curStatIdx[1]].weeks.push(makeSingleWeekStat(week, day));
      if (period === "days")
        stat.years[curStatIdx[0]].months[curStatIdx[1]].weeks[curStatIdx[2]].days.push(makeSingleDayStat(day));
    };

    const addOrIncrementPeriod = (
      period: keyof PeriodsConstType,
      periodStat: DateStatType,
      curStatIdx: number[] = []
    ): null | true => {
      // search current Data's date in stat (resp it's sub-period-value)
      let periodStatFilter: DateStatYearType[] | DateStatDayType[] | DateStatWeekType[] | DateStatMonthType[] | null =
        null;
      let filteredStatIdx = -1;
      if (period === "years") {
        periodStatFilter = (periodStat.years as DateStatYearType[]).filter((e) => e.year === year);
        if (0 in periodStatFilter) filteredStatIdx = stat.years.indexOf(periodStatFilter[0]);
      } else if (period === "months") {
        if (curStatIdx.length < 1) return null;
        const monthsStat = periodStat.years[curStatIdx[0]].months as DateStatMonthType[];
        periodStatFilter = monthsStat.filter((e) => e.month === month);
        if (0 in periodStatFilter) filteredStatIdx = stat.years[curStatIdx[0]].months.indexOf(periodStatFilter[0]);
      } else if (period === "weeks") {
        if (curStatIdx.length < 2) return null;
        const weeksStat = periodStat.years[curStatIdx[0]].months[curStatIdx[1]].weeks as DateStatWeekType[];
        periodStatFilter = weeksStat.filter((e) => e.week === week);
        if (0 in periodStatFilter)
          filteredStatIdx = stat.years[curStatIdx[0]].months[curStatIdx[1]].weeks.indexOf(periodStatFilter[0]);
      } else if (period === "days") {
        if (curStatIdx.length < 3) return null;
        const daysStat = periodStat.years[curStatIdx[0]].months[curStatIdx[1]].weeks[curStatIdx[2]]
          .days as DateStatDayType[];
        periodStatFilter = daysStat.filter((e) => e.day === day);
        if (0 in periodStatFilter)
          filteredStatIdx = stat.years[curStatIdx[0]].months[curStatIdx[1]].weeks[curStatIdx[2]].days.indexOf(
            periodStatFilter[0]
          );
      }
      if (!periodStatFilter) return null; // will not occure if period is provided acc. to it's type

      // period doesn't exist yet in stat
      if (periodStatFilter.length === 0) {
        incrementPeriodAccAmt(period);
        const newPeriodStat =
          period === "years"
            ? makeSingleYearStat(year, month, week, day)
            : period === "months"
            ? makeSingleMonthStat(month, week, day)
            : period === "weeks"
            ? makeSingleWeekStat(week, day)
            : period === "days"
            ? makeSingleDayStat(day)
            : null;
        if (!newPeriodStat) return null; // will not occure if period is provided acc. to it's type
        addPeriod(period, curStatIdx);
        return true;
      } else {
        // period already exists in stat -> check next lower subperiod
        const nextLowerPeriod =
          period === "years" ? "months" : period === "months" ? "weeks" : period === "weeks" ? "days" : null;
        if (!nextLowerPeriod) return null;
        if (filteredStatIdx === -1) return null;
        return addOrIncrementPeriod(nextLowerPeriod, periodStat, [...curStatIdx, filteredStatIdx]);
      }
    };
    addOrIncrementPeriod("years", stat);
  }
  return stat;
};
