import React from "react";
import { Circle, Line } from "react-konva";
import { DrawingTool } from "../Types/CChartStateTypes";

export type CVlineProps = {
  style?: DrawingTool["style"];
  pixXy: [[number, number]];
  mode: "extendUp" | "extendDown" | "extendUpDown" | "*extendToSecondPoint";
  subchartTop: number;
  subchartBottom: number;
};

export const CVline = (props: CVlineProps) => {
  const { style, pixXy, subchartTop, subchartBottom } = props;
  if (pixXy.length !== 1) return null;
  const strokeColor = !!style?.strokeColor ? style.strokeColor : "red";
  const anchorColor = !!style?.anchorColor ? style.anchorColor : "#333";
  //  TODO
  // const modeInt = ["extendUp", "extendDown", "extendUpDown", "*extendToSecondPoint"].includes(mode)
  //   ? mode
  //   : "extendUpDown";
  const pixXyAdjusted: [number, number][] = [];
  pixXyAdjusted.push([pixXy[0][0], subchartTop], [pixXy[0][0], subchartBottom]);

  return (
    <React.Fragment>
      <Line
        x={0}
        y={0}
        points={[pixXyAdjusted[0][0], pixXyAdjusted[0][1], pixXyAdjusted[1][0], pixXyAdjusted[1][1]]}
        stroke={strokeColor}
        strokeWidth={1}
      />
      <Circle x={pixXy[0][0]} y={pixXy[0][1]} radius={5} fill={anchorColor} />
    </React.Fragment>
  );
};
