// import { pixToX } from "../CChart";
import { getDateString } from "../Time/CChartDateTime";
import { getOptimalPeriod, isNewPeriod } from "../Time/CChartPeriod";
import { InteractiveXaxisType, ChartGraphStateType, ChartStateType } from "../Types/CChartStateTypes";
import { ChartPeriodType } from "../Types/CChartTimeTypes";

export const pureXToPix = (x: number, translated: number, widthPerTick: number) => {
  return x * widthPerTick + translated;
};
export const purePixToX = (pixX: number, translated: number, widthPerTick: number) => {
  return (pixX - translated) / widthPerTick;
};

export const getInteractiveXaxisState = (
  currentXaxis: InteractiveXaxisType & { scaleInitWidthPerTick: number | null; scaleInitTranslatedX: number },
  onDrag: ChartStateType["pointer"]["drag"],
  wheel: ChartStateType["pointer"]["wheel"],
  onMove: ChartStateType["pointer"]["move"],
  initWidthTickX: number,
  shallUpdateDrag: boolean,
  clickedChart: boolean,
  clickedXaxis: boolean,
  canvasWidth: number,
  nDatasets: number,
): InteractiveXaxisType & { scaleInitWidthPerTick: number | null; scaleInitTranslatedX: number } => {
  const isWheeling = !!wheel?.isWheeling;
  const wheeldY = wheel?.delta[1];
  const initWheelMousePos = onMove?.xy;

  const scaleFn = (pixX: number) => {
    const delta =
      Math.round(
        Math.min(
          (Math.max(pixX + 640, 0) * (initWidthTickX - 0.5)) / 640 - (initWidthTickX - 0.5),
          -initWidthTickX + canvasWidth / 3
        ) / 0.5
      ) * 0.5;
    return delta;
  };

  // console.log(currentXaxis.xEnd - currentXaxis.xStart, currentXaxis.xStart, currentXaxis.xEnd);
  if (!!shallUpdateDrag) {
    const init = onDrag?.initial;
    if (!init) return currentXaxis;
    if (clickedChart && !wheeldY) {
      const totalTranslatedX = !!onDrag?.delta
        ? Math.max(
            Math.min(
              onDrag.delta[0] + currentXaxis.totalTranslatedX,
              canvasWidth - 2 * currentXaxis.scaledWidthPerTick
            ),
            -( (nDatasets - 3) * currentXaxis.scaledWidthPerTick)
          )
        : currentXaxis.totalTranslatedX;
      return { ...currentXaxis, totalTranslatedX };
    } else if (clickedXaxis && !wheeldY) {
      let scaleInitWidthPerTick: number | null = !currentXaxis.scaleInitWidthPerTick
        ? currentXaxis.scaledWidthPerTick
        : currentXaxis.scaleInitWidthPerTick;
      const scaleInitTranslatedX = !currentXaxis.scaleInitTranslatedX
        ? currentXaxis.totalTranslatedX
        : currentXaxis.scaleInitTranslatedX;
      // shall not scale lower than tickWidth of 0.5
      let totalScaleX = !!onDrag?.delta ? currentXaxis.totalScaleX + onDrag?.delta[0] : currentXaxis.totalScaleX;
      totalScaleX = Math.min(
        Math.max(totalScaleX, -640),
        ((-initWidthTickX + canvasWidth / 3 + initWidthTickX - 0.5) * 640) / (initWidthTickX - 0.5) - 640
      );
      const scaledWidthPerTick = initWidthTickX + scaleFn(totalScaleX);
      const translatedXonStart = scaleInitTranslatedX;
      const initPixX = init[0];
      const initXexact = (init[0] - translatedXonStart) / (scaleInitWidthPerTick as number);
      const newPos = initXexact * scaledWidthPerTick + translatedXonStart;

      const totalTranslatedX = scaleInitTranslatedX - newPos + initPixX;
      if (!onDrag?.isDragging) {
        scaleInitWidthPerTick = null;
      }
      return {
        ...currentXaxis,
        scaleInitWidthPerTick,
        scaleInitTranslatedX,
        totalScaleX,
        scaledWidthPerTick,
        totalTranslatedX,
      };
    }
  } else if (!!isWheeling && !!wheeldY) {
    let scaleInitWidthPerTick: number | null = currentXaxis.scaledWidthPerTick;
    const scaleInitTranslatedX = currentXaxis.totalTranslatedX;

    // shall not scale lower than tickWidth of 0.5
    let totalScaleX = currentXaxis.totalScaleX + wheeldY / 10;
    totalScaleX = Math.min(
      Math.max(totalScaleX, -640),
      ((-initWidthTickX + canvasWidth / 3 + initWidthTickX - 0.5) * 640) / (initWidthTickX - 0.5) - 640
    );
    const scaledWidthPerTick = initWidthTickX + scaleFn(totalScaleX);
    const translatedXonStart = scaleInitTranslatedX;
    const initPixX = !!initWheelMousePos ? initWheelMousePos[0] : 0;
    const initXexact =
      ((!!initWheelMousePos ? initWheelMousePos[0] : 0) - translatedXonStart) / (scaleInitWidthPerTick as number);
    const newPos = Math.round(initXexact) * scaledWidthPerTick + currentXaxis.totalTranslatedX; //translatedXonStart;

    const totalTranslatedX = (scaleInitTranslatedX as number) - newPos + initPixX;
    if (!isWheeling) {
      scaleInitWidthPerTick = null;
    }
    return {
      ...currentXaxis,
      scaleInitWidthPerTick,
      scaleInitTranslatedX,
      totalScaleX,
      scaledWidthPerTick,
      totalTranslatedX,
    };
  }
  return currentXaxis;
};

export const calculateCurrentXaxis = (
  widthPerTick: number,
  translatedPixX: number,
  containerWidth: number,
  widthYAxis: number,
  mainGraph: ChartGraphStateType | null
): Omit<
  ChartStateType["xaxis"],
  | "style"
  | "heightXAxis"
  | "heightTickMarkLines"
  | "totalTranslatedX"
  | "totalScaleX"
  | "scaledWidthPerTick"
  | "scaleInitWidthPerTick"
  | "scaleInitTranslatedX"
> => {
  const xAxisTicks: { x: number; dateString: string }[] = [];

  const xStart = Math.max(Math.ceil(-translatedPixX / widthPerTick), 0);
  const nMainData = mainGraph?.data.length;
  const pixXStart = Math.max(Math.ceil(-translatedPixX / widthPerTick), 0) * widthPerTick + translatedPixX;
  let pixXend = 0,
    xEnd = 0;
  const startNoDataPeriod = Math.abs(Math.min(Math.ceil(-translatedPixX / widthPerTick), 0));
  let endNoDataPeriod = 0;
  let noDataPeriodPortion = 0;
  let optPeriodRes = null;
  if (!!containerWidth && !!widthYAxis && !!nMainData) {
    // pixXend = Math.min(containerWidth - widthYAxis, nMainData * widthPerTick + translatedPixX);
    // xEnd = Math.min(Math.floor((pixXend - translatedPixX) / widthPerTick), nMainData - 1);
    // endNoDataPeriod = Math.max(
    //   Math.floor((containerWidth - widthYAxis - translatedPixX) / widthPerTick) - nMainData,
    //   0
    // );
    pixXend = Math.min(containerWidth, nMainData * widthPerTick + translatedPixX);
    xEnd = Math.min(Math.floor((pixXend - translatedPixX) / widthPerTick), nMainData - 1);
    endNoDataPeriod = Math.max(Math.floor((containerWidth - translatedPixX) / widthPerTick) - nMainData, 0);

    noDataPeriodPortion = (startNoDataPeriod + endNoDataPeriod) / (xEnd - xStart + startNoDataPeriod + endNoDataPeriod);
    
    if (!!mainGraph) {
      optPeriodRes = getOptimalPeriod(
        mainGraph.data,
        xStart,
        xEnd,
        mainGraph.meta,
        noDataPeriodPortion,
        Object.assign({}, mainGraph.dateStat)
      );
// console.log(mainGraph, optPeriodRes, mainGraph?.meta.chartPeriod);
      if (!!optPeriodRes && !!mainGraph.meta.chartPeriod) {
        let pixXi = pixXStart;
        let xi = xStart;
        while (pixXi < pixXend) {
          const periodToDraw = isNewPeriod(mainGraph.data[xi].date, mainGraph.dateStat, mainGraph.meta.chartPeriod, {
            ...(optPeriodRes as ChartPeriodType),
          });
          if (!periodToDraw) {
            pixXi += widthPerTick;
            xi++;
            continue;
          }
          const dateString = getDateString(mainGraph.data[xi].date, periodToDraw);
          xAxisTicks.push({ x: pixXi, dateString: dateString.toString() });
          pixXi += widthPerTick;
          xi++;
        }
      }
    }
  }

  return {
    xStart: xStart,
    pixXStart: pixXStart,
    xEnd: xEnd,
    pixXEnd: pixXend,
    widthPerTick,
    startNoDataPeriod,
    endNoDataPeriod,
    noDataPeriodPortion,
    xAxisTicks,
    optPeriodRes,
  };
};
