import { ReducerActionTaskType, ReducerActionType } from "./CChartStateReducer";
import { ChartDataSeries, DataSeries } from "../Types/CChartTypes";

import {
  AdditionalIndicatorGraphStateType,
  ChartStateType,
  CalculatedChartStateType,
  TypedDispatcher,
} from "../Types/CChartStateTypes";
const addSubchart = (
  chartSeries: DataSeries,
  chartName: string,
  containerHeight: number,
  Dispatch: TypedDispatcher<"addSubchart">,
  indicator?: AdditionalIndicatorGraphStateType["indicator"],
  reset?: boolean
) => {
  const action: ReducerActionType<"addSubchart"> = {
    task: "addSubchart",
    params: { chartSeries, chartName, indicator, containerHeight, reset },
  };
  Dispatch(action);
};

const addGraph = (
  chartSeries: DataSeries,
  chartName: string,
  subchartIdx: number,
  Dispatch: TypedDispatcher<"addGraph">,
  indicator?: AdditionalIndicatorGraphStateType["indicator"]
) => {
  const action: ReducerActionType<"addGraph"> = {
    task: "addGraph",
    params: { subchartIdx, chartSeries, indicator, chartName },
  };
  (Dispatch as React.Dispatch<ReducerActionType<"addGraph">>)(action);
};

export const pixToX = (pixX: number, translated: number, widthPerTick: number) => {
  return (pixX - translated) / widthPerTick;
};

export const getChartMethods = (
  ChartState: ChartStateType,
  containerHeight: number,
  Dispatch: TypedDispatcher<ReducerActionTaskType>
): CalculatedChartStateType["methods"] => ({
  addGraph: (
    chartSeries: ChartDataSeries,
    chartName: string,
    subchartIdx: number,
    indicator?: AdditionalIndicatorGraphStateType["indicator"]
  ) => addGraph(chartSeries, chartName, subchartIdx, Dispatch, indicator),
  addSubchart: (
    chartSeries: DataSeries,
    chartName: string,
    indicator?: AdditionalIndicatorGraphStateType["indicator"],
    reset?: boolean
  ) => addSubchart(chartSeries, chartName, containerHeight, Dispatch, indicator, reset),
  getSubchart: (subchartIdx: number) => ChartState.subCharts[subchartIdx],
  getYaxis: (subchartIdx: number, yaxisIdx: number) => ChartState.subCharts[subchartIdx].yaxis[yaxisIdx],
  getGraph: (subchartIdx: number, yaxisIdx: number, graphIdx: number) =>
    ChartState.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx],
  xToPix: (x: number, translatedX?: number) => {
    const { widthPerTick, totalTranslatedX } = ChartState.xaxis;
    const translatedXint = translatedX !== undefined ? translatedX : totalTranslatedX;
    return x * widthPerTick + translatedXint;
  },
  pixToX: (pixX: number, translatedX?: number) => {
    const { widthPerTick, totalTranslatedX } = ChartState.xaxis;
    const translatedXint = translatedX !== undefined ? translatedX : totalTranslatedX;
    return (pixX - translatedXint) / widthPerTick;
  },
  yToPix: (y: number, subchartIdx: number, yaxisIdx: number, translatedY?: number) => {
    const { heightPerPt, decimals, translatedY: stateTranslatedY } = ChartState.subCharts[subchartIdx].yaxis[yaxisIdx];
    const translatedYint = translatedY !== undefined ? translatedY : stateTranslatedY;
    return ChartState.subCharts[subchartIdx].bottom - y * Math.pow(10, decimals) * heightPerPt + translatedYint;
  },
  pixToY: (pixY: number, subchartIdx: number, yaxisIdx: number, translatedY?: number) => {
    const { heightPerPt, decimals, translatedY: stateTranslatedY } = ChartState.subCharts[subchartIdx].yaxis[yaxisIdx];
    const translatedYint = translatedY !== undefined ? translatedY : stateTranslatedY;
    return ((ChartState.subCharts[subchartIdx].bottom + translatedYint - pixY) / heightPerPt) * Math.pow(10, -decimals);
  },
});
