import { getISOWeek } from "date-fns";
import { getDateStat } from "./Time/CChartDateStat";
import { getDateSubValue } from "./Time/CChartDateTime";
import { getChartPeriod } from "./Time/CChartPeriod";
import {
  DataSeries,
  ChartDataset,
  IndicatorDataset,
  isCandleChartDataset,
  IndicatorDataSeries,
} from "./Types/CChartTypes";
import {
  isIndicatorGraph,
  SubchartStateType,
  YaxisStateType,
  AdditionalIndicatorGraphStateType,
  IndicatorGraphStateType,
  ChartGraphStateType,
  GraphStateType,
} from "./Types/CChartStateTypes";
import { getDecimals } from "./utils";
// import { getDataDateStat } from "./CChartDate";
// import { CandleChartDataset, CChartViewerInternalState, ChartDataset, GraphProps } from "./CChartViewer";

export const getGraphMetaData = (
  dataSeries: ChartDataset[]
): ChartGraphStateType["meta"] & { decimals: number; isDescending: boolean } => {
  // get nData
  const nData = dataSeries.length;

  // local function, ts seems not to accept Date types as number
  const getDeltaT = (dataPoint1: ChartDataset, dataPoint0: ChartDataset) =>
    (dataPoint1.date as unknown as number) - (dataPoint0.date as unknown as number);

  // loop data -> guess period, tradingDaysPerWeek (and confidence level=occurences in percent) and get decimals
  const dT1 = getDeltaT(dataSeries[1], dataSeries[0]);
  let deltaPeriods = [{ dT: dT1, amt: 1 }];
  let decimals = 0;
  let curWeek = getDateSubValue(dataSeries[0].date, "weeks"); //  getISOWeek(dataSeries[0].date);
  let curYear = getDateSubValue(dataSeries[0].date, "years"); //dataSeries[0].date.getFullYear();
  let curWeekDays = 0;
  let weekDays: { amtDays: number; occ: number }[] = [];
  for (let i = 0; i < nData; i++) {
    // heuristic guess of period using simple stat of delta Periods (N=n-1)
    if (i >= 2) {
      // start with 2, iteration i=1 is done outside for loop
      const deltaPeriod = getDeltaT(dataSeries[i], dataSeries[i - 1]);
      // loop thru deltaPeriods already determined in prev iterations
      for (let j = 0; j < deltaPeriods.length; j++) {
        const savedDeltaPeriod = deltaPeriods[j];
        if (savedDeltaPeriod.dT === deltaPeriod) {
          deltaPeriods[j].amt++;
          break;
        } else if (j === deltaPeriods.length - 1) deltaPeriods.push({ dT: deltaPeriod, amt: 1 });
      }
    }
    // heuristic guess of tradingDaysPerWeek
    const iDataWeek = getISOWeek(dataSeries[i].date);
    const iyear = getDateSubValue(dataSeries[i].date, "years"); //.getFullYear(); ;
    if (iDataWeek > curWeek || iyear > curYear || i === nData - 1) {
      // eslint-disable-next-line no-loop-func
      const weekDaysFiltered = weekDays.filter((e) => e.amtDays === curWeekDays);
      if (weekDaysFiltered.length > 0) {
        if (weekDaysFiltered.length > 1) console.log("warning - internal error determing GraphMetaData");
        const weekDayIdx = weekDays.indexOf(weekDaysFiltered[0]);
        weekDays[weekDayIdx].occ++;
      } else if (weekDaysFiltered.length === 0) {
        weekDays.push({ amtDays: curWeekDays, occ: 1 });
      }
      curWeek = iDataWeek;
      curWeekDays = 1;
      curYear = iyear;
    } else {
      curWeekDays++;
    }

    if (!dataSeries[i].close) console.log("hier ist was falsch", i);

    // determine max amount of decimals found in data
    const datasetDecimals = [getDecimals(dataSeries[i].close)];
    const curDataset = dataSeries[i]; //ts seems not to cast
    if (isCandleChartDataset(curDataset)) {
      datasetDecimals.push(getDecimals(curDataset.open));
      datasetDecimals.push(getDecimals(curDataset.high));
      datasetDecimals.push(getDecimals(curDataset.low));
    }
    const maxDecimals = Math.max(...datasetDecimals);
    if (maxDecimals > decimals) decimals = maxDecimals;
  }

  // extract desired period data from deltaPeriod stat
  let deltaTs = deltaPeriods.map((item) => item.dT);
  let amts = deltaPeriods.map((item) => item.amt);
  let maxIdx = amts.reduce((maxIdx, val, i, arr) => (val > arr[maxIdx] ? i : maxIdx), 0); //
  // extract desired tradingDaysPerWeek data from weekDays stat
  const maxDaysPerWeekOccurences = weekDays.map((val) => val.occ);
  let totalOcc = 0;
  weekDays.forEach((val) => (totalOcc += val.occ));
  const maxDaysPerWeekOccIdx = maxDaysPerWeekOccurences.reduce(
    (maxIdx, val, i, arr) => (val > arr[maxIdx] ? i : maxIdx),
    0
  );
  const maxDaysPerWeek = weekDays[maxDaysPerWeekOccIdx].amtDays;
  const maxDaysPerWeekGuessConfidence = weekDays[maxDaysPerWeekOccIdx].occ / totalOcc;
  const isDesc = deltaTs[maxIdx] < 0;

  // get chartperiod
  console.log(deltaTs[maxIdx]);
  const dataPeriod = Math.abs(deltaTs[maxIdx]);
  const chartPeriod = getChartPeriod(dataPeriod);
  return {
    nData: nData,
    dataPeriod: dataPeriod,
    chartPeriod: chartPeriod,
    periodGuessConfidence: amts[maxIdx] / nData,
    tradingDaysPerWeek: maxDaysPerWeek,
    tradingDaysPerWeekGuessConfidence: maxDaysPerWeekGuessConfidence,
    type: "candlechart",
    decimals: decimals,
    isDescending: isDesc,
  };
};

export const createSubChartModel = (
  top: number,
  bottom: number,
  chartData: ChartDataset[],
  chartName: string,
  style?: ChartGraphStateType["style"] | IndicatorGraphStateType["style"],
  indicator?: AdditionalIndicatorGraphStateType["indicator"]
): SubchartStateType => {
  return {
    yaxis: [createYaxisModel(chartData, chartName, style, indicator)],
    top: top,
    bottom: bottom,
  };
};

export const createYaxisModel = (
  chartData: ChartDataset[],
  chartName: string,
  style?: ChartGraphStateType["style"] | IndicatorGraphStateType["style"],
  indicator?: AdditionalIndicatorGraphStateType["indicator"]
): YaxisStateType => {
  const newGraph = !!indicator
    ? createIndicatorGraphModel(chartData, chartName, indicator, style as IndicatorGraphStateType["style"])
    : createChartGraphModel(chartData, chartName, style as ChartGraphStateType["style"]);
  return {
    graphs: [{ ...(newGraph as any) }],
    decimals: newGraph.decimals,
    tools: [],
    translatedY: 0,
    optIntervalY: 0,
    heightPerPt: 0,
    drawTicks: [],
  };
};

export const createChartGraphModel = (
  chartData: ChartDataset[],
  chartName: string,
  style?: GraphStateType["style"]
): ChartGraphStateType => {
  const { decimals, isDescending, ...graphProps } = getGraphMetaData(chartData);
  if (isDescending) chartData.reverse();
  const dateStat = getDateStat(chartData, 0, chartData.length - 1);
  return {
    data: chartData,
    meta: graphProps,
    // dateStat: dateStat,
    style: {
      strokeColor: ["#000"],
      candleStrokeColor: "#666",
      candleDownColor: "rgb(199, 0, 57)",
      candleUpColor: "#009688",
      ...style,
    },
    yMax: 0,
    yMaxExact: 0,
    yMin: 0,
    yMinExact: 0,
    // drawPixXy: [],
    datasets: { first: null, last: null },
    dateStat: dateStat,
    type: "chart",
    name: chartName,
    decimals: decimals,
  };
};
export const createIndicatorGraphModel = (
  chartData: ChartDataset[],
  chartName: string,
  indicator: AdditionalIndicatorGraphStateType["indicator"],
  style?: IndicatorGraphStateType["style"]
): IndicatorGraphStateType => {
  const indicatorData: IndicatorDataSeries = indicator.indicatorFn?.(
    chartData,
    chartName,
    ...indicator.params.map((val) => val.val)
  );
  const { decimals: decimalsData, ...graphProps } = getGraphMetaData(chartData); //only decimals necessary!
  const decimals = !!indicator.default.decimals ? indicator.default.decimals : decimalsData;
  // const dateStat = getDateStat(chartData, 0, chartData.length - 1);
  return {
    data: indicatorData,
    type: "indicator",
    name: indicator.name, //chartName,
    decimals: decimals,
    style: {
      ...style,
      strokeColor: style?.strokeColor || ["#00ffff"],
    },
    yMax: 0,
    yMaxExact: 0,
    yMin: 0,
    yMinExact: 0,
    // drawPixXy: [],
    datasets: { first: null, last: null },
    indicator,
  };
};
