import React, { DOMElement } from "react";
import { useGesture } from "react-use-gesture";
import { chartStateReducer, ReducerActionTaskType, ReducerActionType } from "./CChartStateReducer";

import { getChartMethods } from "./CChartStateMethods";
import { calculateCurrentXaxis, getInteractiveXaxisState } from "./CChartStateXaxis";
import { calculateCurrentGraphs, purePixToY, pureYToPix } from "./CChartStateYaxis";
import { calculateXPointer, calculateCurrentPointerDataset } from "./CChartStateInteractions";
import { purePixToX, pureXToPix } from "./CChartStateXaxis";
import {
  CalculatedChartStateType,
  ChartPreStateRefType,
  ChartStateType,
  InteractiveXaxisType,
  isChartGraph,
  InteractivePointerType,
  userInteractsSubchartStateType,
  UseGestureStateType,
  isIndicatorGraph,
} from "../Types/CChartStateTypes";
import { addTool, addToolAnchor, editToolPosition, getDragActionType, resizeSubchart } from "./CChartStateInteractions";
import { includesOne } from "../utils";
import { ChartDataSeries } from "../Types/CChartTypes";
var dequal = require("fast-deep-equal");

export const defaultState: ChartStateType = {
  xaxis: {
    heightXAxis: 50,
    heightTickMarkLines: 20,
    totalScaleX: 0,
    totalTranslatedX: 0,
    scaledWidthPerTick: 20,
    xStart: 0,
    xEnd: 0,
    pixXStart: 0,
    pixXEnd: 0,
    startNoDataPeriod: 0,
    endNoDataPeriod: 0,
    noDataPeriodPortion: 0,
    optPeriodRes: null,
    widthPerTick: 20,
    xAxisTicks: [],
    style: {
      fillColor: "#fff",
      strokeColor: "#000",
      fontColor: "#000",
      fontSize: 16,
      fontName: "Arial",
    },
  },
  grid: { useGridX: true, useGridY: true, style: { strokeColor: "rgba(51,51,51, 0.33)", strokeStyle: "" } },
  yaxis: {
    widthYAxis: 80,
    widthTickmarkLines: 20,
    style: {
      fillColor: "#fff",
      strokeColor: "#000",
      fontColor: "#000",
      fontSize: 16,
      fontName: "Arial",
    },
  },
  backgroundColor: "#fff",
  fullscreen: false,
  subCharts: [],
  draw: {
    isDrawing: false,
    pixXy: [],
  },
  containerSize: { top: 0, left: 0, width: 300, height: 300, init: false },
  pointer: {
    isHovering: false,
    move: { isMoving: false, xy: [0, 0] },
    wheel: { isWheeling: false, delta: [0, 0] },
    dragPointerUp: {
      isDragPointerUp: false,
      xy: [0, 0],
      first: false,
      last: false,
      initial: [0, 0],
      movementInitial: [0, 0],
    },
    drag: {
      isDragging: false,
      xy: [0, 0],
      first: false,
      last: false,
      initial: [0, 0],
      movementInitial: [0, 0],
      delta: [0, 0],
    },
    chartPointer: {
      move: {
        x: 0,
        pixX: 0,
        pixY: 0,
        snapDatasets: [],
        subchartIdx: 0,
        xDateString: "",
        pixXSnap: 0,
        xUnlimited: 0,
      },
      click: { clickedSubchartIdx: null },
    },
  },
};

export const useChartState = (
  HtmlElementRef: React.RefObject<HTMLElement>,
  settings: {
    maxUpdatesPerSec?: number;
    initWidthPerTick?: number;
    width?: number;
    height?: number;
    viewMode?: "static" | "responsive" | "fullscreen";
  }
): [CalculatedChartStateType, React.Dispatch<ReducerActionType<ReducerActionTaskType>>] => {
  const maxUpdatesPerSec = !!settings.maxUpdatesPerSec ? settings.maxUpdatesPerSec : 15;
  const initWidthPerTick = !!settings.initWidthPerTick ? settings.initWidthPerTick : 20;
  const viewModeInternal =
    settings.viewMode === "static" && (!settings.height || !settings.width)
      ? "responsive"
      : !!settings.viewMode
      ? settings.viewMode
      : "responsive";
  // const isResponsive = settings.width === undefined && settings.height === undefined ? true : false;
  const [ChartState, Dispatch] = React.useReducer<
    (prevState: ChartStateType, action: ReducerActionType<ReducerActionTaskType>) => ChartStateType
  >(chartStateReducer, defaultState);

  const PreStateRef = React.useRef<ChartPreStateRefType>({
    containerSize: defaultState.containerSize,
    pointer: defaultState.pointer,
    xaxis: {
      totalScaleX: 0,
      totalTranslatedX: 0,
      scaledWidthPerTick: 20,
      xStart: 0,
      xEnd: 0,
      pixXStart: 0,
      pixXEnd: 0,
      startNoDataPeriod: 0,
      endNoDataPeriod: 0,
      noDataPeriodPortion: 0,
      optPeriodRes: null,
      widthPerTick: 20,
      xAxisTicks: [],
    },
    tmp: {
      scaleInitWidthPerTick: null,
      scaleInitTranslatedX: 0,
      dragActionType: null,
      amtLastSubcharts: 0,
      lastMainChartData: [],
    },
    lastUpdate: new Date(),
    shallUpdate: [],
    userInteractedChartState: null,
  });

  const containerHeight = ChartState.containerSize.height - 1;

  /** Pointer Event Handler defined using react-use-gesture -> pointer state is stored in PreStateRef */
  useGesture(
    {
      onDrag: (dragState: UseGestureStateType["drag"]) => {
        const deltaX =
          !!PreStateRef.current.pointer.drag?.delta && !!PreStateRef.current.shallUpdate.includes("drag")
            ? PreStateRef.current.pointer.drag?.delta[0] + dragState.delta[0]
            : dragState.delta[0];
        const deltaY =
          !!PreStateRef.current.pointer.drag?.delta && !!PreStateRef.current.shallUpdate.includes("drag")
            ? PreStateRef.current.pointer.drag?.delta[1] + dragState.delta[1]
            : dragState.delta[1];

        const { xy, initial, movement: movementInitial, first, last, active: isDragging } = dragState;
        PreStateRef.current = {
          ...PreStateRef.current,
          pointer: {
            ...PreStateRef.current.pointer,
            drag: { isDragging, xy, initial, movementInitial, last, first, delta: [deltaX, deltaY] },
          },
          shallUpdate: [...PreStateRef.current.shallUpdate, "drag"],
          lastUpdate: PreStateRef.current.lastUpdate,
        };
      },
      onMove: (moveState: UseGestureStateType["move"]) => {
        PreStateRef.current = {
          ...PreStateRef.current,
          pointer: {
            ...PreStateRef.current.pointer,
            move: { isMoving: moveState.active, xy: moveState.xy },
          },
          shallUpdate: [...PreStateRef.current.shallUpdate, "pointerMove"],
        };
      },
      onHover: (hoverState: UseGestureStateType["hover"]) => {
        PreStateRef.current = {
          ...PreStateRef.current,
          pointer: {
            ...PreStateRef.current.pointer,
            isHovering: hoverState.active,
          },
          shallUpdate: [...PreStateRef.current.shallUpdate, "pointerMove"],
        };
      },
      onWheel: (wheelState: UseGestureStateType["wheel"]) => {
        const deltaX =
          !!PreStateRef.current.pointer.wheel?.delta && !!PreStateRef.current.shallUpdate.includes("wheel")
            ? PreStateRef.current.pointer.wheel?.delta[0] + wheelState.delta[0]
            : wheelState.delta[0];
        const deltaY =
          !!PreStateRef.current.pointer.wheel?.delta && !!PreStateRef.current.shallUpdate.includes("wheel")
            ? PreStateRef.current.pointer.wheel?.delta[1] + wheelState.delta[1]
            : wheelState.delta[1];
        PreStateRef.current = {
          ...PreStateRef.current,
          pointer: {
            ...PreStateRef.current.pointer,
            wheel: { delta: [deltaX, deltaY], isWheeling: wheelState.active },
          },
          shallUpdate: [...PreStateRef.current.shallUpdate, "wheel"],
        };
      },
      onDragEnd: (dragState: UseGestureStateType["dragEnd"]) => {
        const { xy, initial, first, last, movement: movementInitial } = dragState;
        PreStateRef.current = {
          ...PreStateRef.current,
          pointer: {
            ...PreStateRef.current.pointer,
            dragPointerUp: { isDragPointerUp: true, xy, initial, movementInitial, last, first },
          },
          shallUpdate: [...PreStateRef.current.shallUpdate, "dragEnd"],
        };
      },
    },
    {
      domTarget: HtmlElementRef,
      transform: ([x, y]) => [x - ChartState.containerSize.top, y - ChartState.containerSize.left],
      // eventOptions: { capture: true,  },
      drag: { useTouch: true, experimental_preventWindowScrollY: true },
      move: {},
    }
  );

  /** window resize event handler  */
  React.useEffect(() => {
    // console.log("HI!");
    if (!HtmlElementRef || !HtmlElementRef.current) return;
    // console.log("noch da!");
    if (ChartState.fullscreen || viewModeInternal === "fullscreen") {
      // console.log("immrt noch!");
      HtmlElementRef.current.style.position = "fixed";
      HtmlElementRef.current.style.width = "100%";
      HtmlElementRef.current.style.height = "100%";
      HtmlElementRef.current.style.top = "0px";
      const { clientWidth: width, clientHeight: height } = HtmlElementRef.current;
      PreStateRef.current = {
        ...PreStateRef.current,
        shallUpdate: [...PreStateRef.current.shallUpdate, "containerResize"],
        containerSize: { top: 0, left: 0, width, height, init: true },
      };
      // return;
    } else {
      const { top, left } = HtmlElementRef.current.getBoundingClientRect();
      const { clientWidth: width, clientHeight: height } = HtmlElementRef.current;
      HtmlElementRef.current.style.position = "relative";
      HtmlElementRef.current.style.width =
        !!settings.width && viewModeInternal === "static" ? settings.width + "px" : "100%";
      HtmlElementRef.current.style.height =
        !!settings.height && viewModeInternal === "static" ? settings.height + "px" : "100%";
      HtmlElementRef.current.style.top = "0px";
      PreStateRef.current = {
        ...PreStateRef.current,
        shallUpdate: [...PreStateRef.current.shallUpdate, "containerResize"],
        containerSize: { top, left, width, height, init: true },
      };
    }
    // listen only when container shall be responsive -> neither width or height are set by CChart
    if (!!settings.height && !!settings.width && viewModeInternal === "static") return;
    const handleWindowResize = (e: Event) => {
      if (!HtmlElementRef || !HtmlElementRef.current) return;
      if (ChartState.fullscreen || viewModeInternal === "fullscreen") {
        const { clientWidth: width, clientHeight: height } = HtmlElementRef.current;
        PreStateRef.current = {
          ...PreStateRef.current,
          shallUpdate: [...PreStateRef.current.shallUpdate, "containerResize"],
          containerSize: { top: 0, left: 0, width, height, init: true },
        };
        // return;
      } else {
        const { top, left } = HtmlElementRef.current.getBoundingClientRect();
        const { clientWidth: width, clientHeight: height } = HtmlElementRef.current;
        // HtmlElementRef.current.style.position = "relative";
        // HtmlElementRef.current.style.width = width + "px";
        // HtmlElementRef.current.style.height = height + "px";
        // HtmlElementRef.current.style.top = "0px";
        PreStateRef.current = {
          ...PreStateRef.current,
          shallUpdate: [...PreStateRef.current.shallUpdate, "containerResize"],
          containerSize: { top, left, width, height, init: true },
        };
      }
    };
    // console.log("updated! onResize E handler");
    if (!!HtmlElementRef.current.parentElement)
      HtmlElementRef.current.parentElement.addEventListener("resize", () => {
        "HI!!!!!";
      });
    window.addEventListener("resize", handleWindowResize);
    window.addEventListener("orientationchange", handleWindowResize);

    return () => {
      window.removeEventListener("resize", handleWindowResize);
      window.removeEventListener("orientationchange", handleWindowResize);
    };
  }, [
    HtmlElementRef,
    settings.width,
    settings.height,
    ChartState.fullscreen,
    viewModeInternal,
    HtmlElementRef?.current?.clientWidth,
    HtmlElementRef?.current?.clientHeight,
  ]);

  const userInteractedSubcharts: userInteractsSubchartStateType = ChartState.subCharts.map((subchart) => ({
    // top: subchart.top,
    // bottom: subchart.bottom,
    yaxis: subchart.yaxis.map((singleYaxis) => ({
      decimals: singleYaxis.decimals,
      graphs: singleYaxis.graphs.map((graph) => {
        const addIndicatorDeps = isIndicatorGraph(graph) ? { ...graph.indicator } : {};

        return {
          // data: graph.data,
          decimals: graph.decimals,
          name: graph.name,
          style: graph.style,
          type: graph.type,
          ...addIndicatorDeps,
        };
      }),
      tools: singleYaxis.tools.map((tool) => ({ type: tool.type, params: tool.params, style: tool.style })),
    })),
  }));
  const userInteractedXaxis = {
    heightXAxis: ChartState.xaxis.heightXAxis,
    heightTickMarkLines: ChartState.xaxis.heightTickMarkLines,
    style: ChartState.xaxis.style,
  };
  const userInteractedChartState = {
    xaxis: userInteractedXaxis,
    subcharts: userInteractedSubcharts,
  };

  if (!dequal(PreStateRef.current.userInteractedChartState, userInteractedChartState)) {
    PreStateRef.current.userInteractedChartState = userInteractedChartState;
  }

  React.useEffect(() => {
    const onTimer = () => {
      const PreState = PreStateRef.current;
      const DragEndState = PreState.pointer.dragPointerUp;
      const DragState = PreState.pointer.drag;
      if (PreState.shallUpdate.length > 0) {
        const widthYaxis = ChartState.yaxis.widthYAxis;
        const widthChart = PreState.containerSize.width - 1;
        const heightChart = PreState.containerSize.height - 1;
        const heightXaxis = ChartState.xaxis.heightXAxis;
        let calcSubcharts = ChartState.subCharts;
        let calcXaxis: InteractiveXaxisType = PreState.xaxis;
        let currentPointer: InteractivePointerType["chartPointer"] | undefined;
        let calcDraw = ChartState.draw;
        let dragActionType;
        if (!PreState.shallUpdate.includes("dragEnd")) PreState.pointer.dragPointerUp.isDragPointerUp = false;
        if (includesOne(PreState.shallUpdate, "containerResize") && ChartState.subCharts.length > 0) {
          const dHeight =
            PreState.containerSize.height -
            1 -
            ChartState.xaxis.heightXAxis -
            ChartState.subCharts[ChartState.subCharts.length - 1].bottom;
          const minMainHeight = 250;
          const dHproportionalTmp = Math.floor(dHeight / ChartState.subCharts.length);
          const newMainSizeTmp = ChartState.subCharts[0].bottom - ChartState.subCharts[0].top + dHproportionalTmp;
          const restMainLimit = newMainSizeTmp < minMainHeight ? minMainHeight - newMainSizeTmp : 0;
          const dHeightMain = dHproportionalTmp + restMainLimit;
          const dHeightSingleSubchart =
            ChartState.subCharts.length > 1
              ? Math.floor((dHeight - dHeightMain) / (ChartState.subCharts.length - 1))
              : 0;
          const rest =
            ChartState.subCharts.length > 1 ? (dHeight - dHeightMain) % (ChartState.subCharts.length - 1) : 0;
          let currentRest = rest;
          let lastBottom: number = 0;
          let correctRest = 0;
          calcSubcharts = calcSubcharts.map((subchart, subchartIdx) => {
            if (subchartIdx === 0) {
              lastBottom = subchart.bottom + 1 * dHeightMain;
              return { ...subchart, bottom: subchart.bottom + 1 * dHeightMain };
            } else {
              const tempRest =
                Math.sign(currentRest) > 0 && currentRest > 0 && currentRest !== 0
                  ? -1
                  : Math.sign(currentRest) < 0 && currentRest < 0 && currentRest !== 0
                  ? +1
                  : 0;
              currentRest += tempRest;
              correctRest += tempRest;
              const newTop = lastBottom;
              // why math.abs -> rest of Math.floor(x/n)
              lastBottom = subchart.bottom + subchartIdx * dHeightSingleSubchart + dHeightMain + Math.abs(correctRest);

              return {
                ...subchart,
                top: newTop,
                bottom: subchart.bottom + subchartIdx * dHeightSingleSubchart + dHeightMain + Math.abs(correctRest),
              };
            }
          });

          // console.log(dHeight, dHeightMain, dHeightSingleSubchart, rest, PreState.containerSize.height);
          PreStateRef.current.userInteractedChartState = null;
        }
        if (
          includesOne(PreState.shallUpdate, "drag", "wheel", "containerResize", "deps") ||
          !!PreState.tmp.dragActionType
        ) {
          // drag actions

          const dragInit = DragState?.initial;
          // if (!PreState.shallUpdate.includes("drag")) {
          dragActionType = getDragActionType(
            dragInit,
            calcSubcharts,
            calcXaxis,
            !!PreState.tmp.dragActionType,
            ChartState.draw.isDrawing,
            widthChart - widthYaxis,
            heightChart - heightXaxis
          );
          if (
            !PreState.tmp.dragActionType &&
            !!dragActionType &&
            ["resizeSubchart"].includes(dragActionType.type) // "editTool", "drawTool"
          ) {
            PreState.tmp.dragActionType = dragActionType;
          }
          if (!!PreState.tmp.dragActionType) {
            if (!!PreState.pointer.drag.isDragging) {
              if (PreState.tmp.dragActionType?.type === "resizeSubchart") {
                calcSubcharts = resizeSubchart(PreState.tmp.dragActionType, calcSubcharts, PreState.pointer.drag);
              }
            }

            if (!PreState.pointer.drag.isDragging) {
              if (!["drawTool", "editTool"].includes(PreState.tmp.dragActionType?.type)) {
                if (PreState.tmp.dragActionType?.type === "resizeSubchart") {
                  // resize with ChartState.subcharts[i].bottom (when resizing began!) for the last time

                  calcSubcharts = resizeSubchart(PreState.tmp.dragActionType, calcSubcharts, PreState.pointer.drag);
                  // and trigger update of ChartState.subCharts for the effect hook
                  PreState.userInteractedChartState = null;
                  PreState.tmp.dragActionType = null;
                }
              }
            }
            // }
          }

          if (
            (!!dragActionType && ["scale", "translate"].includes(dragActionType.type)) ||
            includesOne(PreState.shallUpdate, "containerResize", "deps")
          ) {

            // if (PreState.shallUpdate.includes("deps"))
            //   console.log(
            //     "MAINDATA: ",
            //     PreState.tmp.lastMainChartData ===
            //       (ChartState.subCharts?.[0]?.yaxis?.[0]?.graphs?.[0]?.data as ChartDataSeries) ?? null
            //   );

            // if new chart is added or replacing current chart or chart is reset/cleared -> initial translatedX to end of chart is calculated in reducer 
            if (
              ((PreState.tmp.amtLastSubcharts !== ChartState.subCharts.length && ChartState.subCharts.length === 1) ||
                PreState.tmp.lastMainChartData !==
                  (ChartState.subCharts?.[0]?.yaxis?.[0]?.graphs?.[0]?.data as ChartDataSeries)) ??
              null
            ) {
              // console.log("DIFFERENZ: ", PreState.tmp.amtLastSubcharts, ChartState.subCharts.length);

              PreState.xaxis.totalTranslatedX = ChartState.xaxis.totalTranslatedX;
            }
            const { scaleInitTranslatedX, scaleInitWidthPerTick } = PreState.tmp;
            let mainGraph = !!calcSubcharts?.[0]?.yaxis?.[0]?.graphs?.[0]
              ? calcSubcharts?.[0].yaxis[0].graphs[0]
              : null;
            if (!!mainGraph) if (!isChartGraph(mainGraph)) mainGraph = null;

            PreState.xaxis = getInteractiveXaxisState(
              { ...PreState.xaxis, scaleInitTranslatedX, scaleInitWidthPerTick }, // + temp vars in ref! therefore ref not state
              PreState.pointer.drag,
              PreState.pointer.wheel,
              PreState.pointer.move,
              initWidthPerTick,
              PreState.shallUpdate.includes("drag"),
              dragActionType?.type === "translate",
              dragActionType?.type === "scale",
              widthChart, //- widthYaxis
              mainGraph?.data.length || 0
            );
            // const { scaleInitTranslatedX, scaleInitWidthPerTick, ...xaxisInteractiveState } = PreState.xaxis;

            calcXaxis = {
              ...calcXaxis,
              ...PreState.xaxis,
              ...calculateCurrentXaxis(
                PreState.xaxis.scaledWidthPerTick,
                PreState.xaxis.totalTranslatedX,
                PreState.containerSize.width,
                ChartState.yaxis.widthYAxis,
                mainGraph
              ),
            };
          }
          if (
            (!!dragActionType && ["scale", "translate", "resizeSubchart"].includes(dragActionType.type)) ||
            !!PreState.tmp.dragActionType ||
            includesOne(PreState.shallUpdate, "containerResize", "deps")
          ) {
            // graphs (and yaxis) - depends on drag actions (translate, scale, resizeSubchart)
            const calcCurrentSubchart = calculateCurrentGraphs(
              calcSubcharts,
              PreState.xaxis.scaledWidthPerTick,
              calcXaxis.xStart,
              calcXaxis.xEnd,
              calcXaxis.pixXStart,
              calcXaxis.pixXEnd
            );
            if (!!calcCurrentSubchart) calcSubcharts = calcCurrentSubchart;
          }
        }
        if (PreState.shallUpdate.includes("pointerMove")) {
          const xpointerRes = calculateXPointer(
            calcXaxis.scaledWidthPerTick,
            calcXaxis.totalTranslatedX,
            calcXaxis,
            calcSubcharts?.[0]?.yaxis?.[0]?.graphs?.[0] as any,
            PreState.pointer.move
          );

          // if (!!xpointerRes) {
          // console.log(xpointerRes);
          const xpointer = xpointerRes?.xPointer !== undefined ? xpointerRes?.xPointer : null;
          const xPointerUnl = xpointerRes?.xPointerUnlimited !== undefined ? xpointerRes.xPointerUnlimited : -1;
          const xPointerDateString = xpointerRes?.xPointerDateStr || "";
          const pixXPointer = xpointerRes?.pixXPointer || 0;
          currentPointer = calculateCurrentPointerDataset(
            calcSubcharts,
            xpointer,
            xPointerUnl,
            xPointerDateString,
            pixXPointer,
            PreState.pointer.move,
            PreState.pointer.dragPointerUp
          );
          // }
          // ensure xUnlimited is added to chartstate

          // currentPointer = {move: {xUnlimited: Math.round(
          //   purePixToX(
          //     PreState.pointer.move?.xy[0] as any,
          //     PreState.xaxis.totalTranslatedX,
          //     PreState.xaxis.widthPerTick
          //   )
          // );}}
        }
        if (
          PreState.shallUpdate.includes("drag") ||
          (!!PreState.tmp?.dragActionType && ["editTool", "drawTool"].includes(PreState.tmp.dragActionType.type))
        ) {
          if (
            !PreState.tmp.dragActionType &&
            !!dragActionType &&
            ["editTool", "drawTool"].includes(dragActionType.type)
          ) {
            PreState.tmp.dragActionType = dragActionType;
          }
          if (!!PreState.tmp.dragActionType) {
            if (!!PreState.pointer.drag.isDragging) {
              if (PreState.tmp.dragActionType?.type === "editTool") {
                calcSubcharts = editToolPosition(
                  PreState.tmp.dragActionType,
                  calcSubcharts,
                  calcXaxis,
                  PreState.pointer
                );
              }
            }
            if (!PreState.pointer.drag.isDragging) {
              if (PreState.tmp.dragActionType?.type === "editTool") {
                //   console.log(PreState.tmp.dragActionType, PreState.pointer.drag);
                calcSubcharts = editToolPosition(
                  PreState.tmp.dragActionType,
                  calcSubcharts,
                  calcXaxis,
                  PreState.pointer
                );
                // and trigger update of ChartState.subCharts for the effect hook
                PreState.userInteractedChartState = null;
                PreState.tmp.dragActionType = null;
              } else if (DragEndState.isDragPointerUp && PreState.tmp.dragActionType?.type === "drawTool") {
                const nPoints = !ChartState.draw.type
                  ? 0
                  : ["hline", "vline"].includes(ChartState.draw.type)
                  ? 1
                  : ["trendline"].includes(ChartState.draw.type)
                  ? 2
                  : 0;
                // console.log(nPoints, ChartState.draw, ChartState.draw.isDrawing);
                if (!nPoints) calcDraw = { isDrawing: false, pixXy: [] };
                else {
                  if (calcDraw.pixXy.length < nPoints - 1) {
                    calcDraw = addToolAnchor(calcDraw.type, calcDraw, PreState.pointer);
                    // console.log("drawing 0", ChartState.draw, DragEndState);
                  } else {
                    calcDraw = addToolAnchor(calcDraw.type, calcDraw, PreState.pointer);
                    calcSubcharts = addTool(calcSubcharts, calcXaxis, calcDraw, PreState.pointer.drag);
                    calcDraw = { isDrawing: false, pixXy: [] };
                    PreState.tmp.dragActionType = null;
                    // console.log("drawing 1", DragEndState);
                  }
                }
              }
            }
          }
        }

        const { lastUpdate, shallUpdate, userInteractedChartState, xaxis, tmp, ...state } = PreState;
        PreState.xaxis = { ...PreState.xaxis, ...calcXaxis };
        if (!!currentPointer) PreState.pointer.chartPointer = currentPointer;
        // PreState.subcharts = calcSubcharts;

        Dispatch({
          task: "updateInteractionState",
          params: {
            newState: {
              ...state,
              xaxis: calcXaxis as any,
              subCharts: calcSubcharts,
              draw: calcDraw,
            },
            // resizeSubchartObj,
          },
        } as ReducerActionType<"updateInteractionState">);

        PreStateRef.current = {
          ...PreState,
          lastUpdate: new Date(),
          shallUpdate: [],
        };
      }
      PreState.tmp.amtLastSubcharts = ChartState.subCharts.length;
      PreState.tmp.lastMainChartData =
        (ChartState.subCharts?.[0]?.yaxis?.[0]?.graphs?.[0]?.data as ChartDataSeries) ?? null;
    };
    // onTimer(); // exec immmediately
    console.log("updated Timer");
    PreStateRef.current.shallUpdate.push("deps");
    onTimer();
    const handleTimer = window.setInterval(onTimer, 1000 / maxUpdatesPerSec);
    return () => {
      window.clearInterval(handleTimer);
    };
  }, [
    maxUpdatesPerSec,
    initWidthPerTick,
    ChartState.draw.isDrawing,
    ChartState.draw.pixXy,
    ChartState.yaxis.widthYAxis,
    ChartState.xaxis.heightXAxis,
    PreStateRef.current.userInteractedChartState,
    ChartState.fullscreen,
  ]);

  return [
    {
      ...ChartState,
      methods: getChartMethods(ChartState, containerHeight, Dispatch),
    },
    Dispatch,
  ];
};
