import { createChartGraphModel, createIndicatorGraphModel, createSubChartModel } from "../CChartModel";
// import { getGraphColors, graphColorsLight } from "../Archiv/CChartVals";
import { ChartDataSeries, ChartDataset, DataSeries } from "../Types/CChartTypes";

import {
  AdditionalChartGraphStateType,
  AdditionalIndicatorGraphStateType,
  ChartGraphStateType,
  ChartStateType,
  GraphStateType,
  IndicatorGraphStateType,
  isChartGraph,
  isIndicatorGraph,
} from "../Types/CChartStateTypes";
export type AtLeastOne<T, U = { [K in keyof T]: Pick<T, K> }> = Partial<T> & U[keyof U];
export type ExcludeEmpty<T> = T extends AtLeastOne<T> ? T : never;

export const graphColorsLight: string[] = ["#666", "#0693E3", "#f50057", "#00D084", "#FF6900", "#bd10e0", "#bbb"];
export const getGraphColors = (colorArr: string[], idx: number) => colorArr[idx % colorArr.length];
export const getLightGraphColors = (idx: number) => getGraphColors(graphColorsLight, idx);

export type ReducerActionTaskType =
  | "addSubchart"
  | "addGraph"
  | "clearChart"
  // | "draw"
  | "setBackgroundColor"
  | "setAxisFillColor"
  | "setAxisStrokeColor"
  | "setAxisTextColor"
  | "toggleXaxisGrid"
  | "toggleYaxisGrid"
  | "setGridStrokeColor"
  | "setLineChartStrokeColor"
  | "setCandleChartUpColor"
  | "setCandleChartDownColor"
  | "setCandleChartStrokeColor"
  | "setIndicatorParam"
  | "updateInteractionState"
  | "setToolStrokeColor"
  | "setToolchorColor"
  | "setHlineYlevel"
  | "toggleFullscreen"
  | "removeSubchart"
  | "removeGraph"
  | {};

export type ReducerActionType<T extends ReducerActionTaskType = {}> = {
  task: T;
  params: T extends "addSubchart"
    ? {
        chartSeries: DataSeries;
        chartName: string;
        indicator?: AdditionalIndicatorGraphStateType["indicator"];
        containerHeight: number; // should not be
        reset?: boolean;
      }
    : T extends "removeSubchart"
    ? {
        subchartIdx: number;
      }
    : T extends "removeGraph"
    ? {
        subchartIdx: number;
        yaxisIdx: number;
        graphIdx: number;
      }
    : T extends "addGraph"
    ? {
        chartSeries: DataSeries;
        chartName: string;
        subchartIdx: number;
        indicator?: AdditionalIndicatorGraphStateType["indicator"];
      }
    : T extends "draw"
    ? {
        type?: ChartStateType["draw"]["type"];
        xy?: [number, number];
        subchartIdx: number;
      }
    : T extends
        | "setBackgroundColor"
        | "setAxisFillColor"
        | "setAxisStrokeColor"
        | "setAxisTextColor"
        | "setGridStrokeColor"
    ? { color: string }
    : T extends "toggleXaxisGrid" | "toggleYaxisGrid" | "toggleFullscreen"
    ? {}
    : T extends
        | "setLineChartStrokeColor"
        | "setCandleChartUpColor"
        | "setCandleChartDownColor"
        | "setCandleChartStrokeColor"
    ? { color: string; subchartIdx: number; yaxisIdx: number; graphIdx: number }
    : T extends "setToolStrokeColor" | "setToolAnchorColor"
    ? { color: string; subchartIdx: number; yaxisIdx: number; toolIdx: number }
    : T extends "setIndicatorParam"
    ? {
        subchartIdx: number;
        yaxisIdx: number;
        graphIdx: number;
        paramIdx: number;
        val: any;
      }
    : T extends "setHlineYlevel"
    ? { subchartIdx: number; yaxisIdx: number; toolIdx: number; val: number }
    : T extends "updateInteractionState"
    ? {
        newState: ChartStateType;
      }
    : T extends "clearChart"
    ? {
        mode: "all" | "indicators" | "tools";
      }
    : {};
};

export const isAddSubchartAction = (action: ReducerActionType): action is ReducerActionType<"addSubchart"> =>
  action.task === "addSubchart";
export const isAddGraphAction = (action: ReducerActionType): action is ReducerActionType<"addGraph"> =>
  action.task === "addGraph";
// export const isDrawAction = (action: ReducerActionType): action is ReducerActionType<"draw"> => action.task === "draw";
export const isSetBackgroundColorAction = (
  action: ReducerActionType
): action is ReducerActionType<"setBackgroundColor"> => action.task === "setBackgroundColor";
export const isSetAxisFillColorAction = (action: ReducerActionType): action is ReducerActionType<"setAxisFillColor"> =>
  action.task === "setAxisFillColor";
export const isSetAxisStrokeColorAction = (
  action: ReducerActionType
): action is ReducerActionType<"setAxisStrokeColor"> => action.task === "setAxisStrokeColor";
export const isSetAxisTextColorAction = (action: ReducerActionType): action is ReducerActionType<"setAxisTextColor"> =>
  action.task === "setAxisTextColor";
export const isToggleXaxisGridAction = (action: ReducerActionType): action is ReducerActionType<"toggleXaxisGrid"> =>
  action.task === "toggleXaxisGrid";
export const isToggleYaxisGridAction = (action: ReducerActionType): action is ReducerActionType<"toggleYaxisGrid"> =>
  action.task === "toggleYaxisGrid";
export const isSetLineChartStrokeColor = (
  action: ReducerActionType
): action is ReducerActionType<"setLineChartStrokeColor"> => action.task === "setLineChartStrokeColor";
export const isSetCandleChartUpColor = (
  action: ReducerActionType
): action is ReducerActionType<"setCandleChartUpColor"> => action.task === "setCandleChartUpColor";
export const isSetCandleChartDownColor = (
  action: ReducerActionType
): action is ReducerActionType<"setCandleChartDownColor"> => action.task === "setCandleChartDownColor";
export const isSetCandleChartStrokeColor = (
  action: ReducerActionType
): action is ReducerActionType<"setCandleChartStrokeColor"> => action.task === "setCandleChartStrokeColor";
export const isSetIndicatorParam = (action: ReducerActionType): action is ReducerActionType<"setIndicatorParam"> =>
  action.task === "setIndicatorParam";
export const isUpdateInteractionState = (
  action: ReducerActionType
): action is ReducerActionType<"updateInteractionState"> => action.task === "updateInteractionState";
export const isSetToolStrokeColorAction = (
  action: ReducerActionType
): action is ReducerActionType<"setToolStrokeColor"> => action.task === "setToolStrokeColor";
export const isSetToolAnchorColor = (action: ReducerActionType): action is ReducerActionType<"setToolAnchorColor"> =>
  action.task === "setToolAnchorColor";
export const isSetHlineYlevelAction = (action: ReducerActionType): action is ReducerActionType<"setHlineYlevel"> =>
  action.task === "setHlineYlevel";
export const isDrawAction = (action: ReducerActionType): action is ReducerActionType<"draw"> => action.task === "draw";
export const isToggleFullscreenAction = (action: ReducerActionType): action is ReducerActionType<"toggleFullscreen"> =>
  action.task === "toggleFullscreen";
export const isRemoveSubchartAction = (action: ReducerActionType): action is ReducerActionType<"removeSubchart"> =>
  action.task === "removeSubchart";
export const isRemoveGraphAction = (action: ReducerActionType): action is ReducerActionType<"removeGraph"> =>
  action.task === "removeGraph";
export const isSetGridStrokeColorAction = (
  action: ReducerActionType
): action is ReducerActionType<"setGridStrokeColor"> => action.task === "setGridStrokeColor";
export const isClearChartAction = (action: ReducerActionType): action is ReducerActionType<"clearChart"> =>
  action.task === "clearChart";

export const chartStateReducer = <T>(current: ChartStateType, action: ReducerActionType<T>): ChartStateType => {
  // addSubchart
  if (isAddSubchartAction(action)) {
    // currently all new subcharts are indicators!
    const chartSeries = action.params.chartSeries;
    const chartName = action.params.chartName;
    const indicator = action.params.indicator;
    const reset = action.params.reset;
    const containerHeight = action.params.containerHeight;
    const bottom = containerHeight - current.xaxis.heightXAxis;
    const minMainHeight = 250;
    const heightNewSubchart = Math.min(150, (bottom - minMainHeight) / (current.subCharts.length - 1 + 1));
    const top = current.subCharts.length === 0 || reset ? 0 : bottom - Math.round(heightNewSubchart);
    const style = !!indicator
      ? { strokeColor: [getGraphColors(graphColorsLight, 0)] }
      : { strokeColor: getGraphColors(graphColorsLight, 0) };

    if (current.subCharts.length === 0 || !!reset) {
      console.log(chartSeries.length);
      return {
        ...current,
        xaxis: {
          ...current.xaxis,
          totalTranslatedX:
            -(chartSeries.length +5  - (current.containerSize.width - 1) / current.xaxis.scaledWidthPerTick) *
            current.xaxis.scaledWidthPerTick,
        },
        subCharts: [
          createSubChartModel(
            top,
            bottom,
            chartSeries as ChartDataset[],
            chartName,
            style as ChartGraphStateType["style"] | IndicatorGraphStateType["style"]
          ),
        ],
      };
    } else {
      const mainHeight = current.subCharts[0].bottom - current.subCharts[0].top;
      const maxHeight =
        mainHeight - heightNewSubchart < minMainHeight
          ? (bottom - minMainHeight - heightNewSubchart) / (current.subCharts.length - 1)
          : null;

      const subchart = createSubChartModel(
        top,
        bottom,
        chartSeries as ChartDataset[],
        chartName,
        style as ChartGraphStateType["style"] | IndicatorGraphStateType["style"],
        indicator
      );
      const newGraph = subchart.yaxis[0].graphs[0];

      if (isIndicatorGraph(newGraph) && newGraph.data[newGraph.data.length - 1].prices.length > 1)
        (newGraph as unknown as IndicatorGraphStateType).style.strokeColor = newGraph.data[
          newGraph.data.length - 1
        ].prices.map((x, idx) => getGraphColors(graphColorsLight, idx));

      return {
        ...current,
        subCharts: [
          ...current.subCharts.map((subChart, subChartIdx) => {
            if (subChartIdx === 0) {
              subChart.bottom = Math.max(subChart.bottom - heightNewSubchart, minMainHeight);
              subChart.top = 0;
            } else if (subChartIdx === current.subCharts.length - 1) {
              subChart.top = current.subCharts[subChartIdx - 1].bottom;
              subChart.bottom = top;
            } else {
              subChart.top = current.subCharts[subChartIdx - 1].bottom;
              subChart.bottom = !!maxHeight
                ? Math.round(subChart.top + maxHeight)
                : subChart.bottom - heightNewSubchart;
            }
            return subChart;
          }),
          subchart,
        ],
      };
    }
  } else if (isRemoveSubchartAction(action)) {
    const subchartIdx = action.params.subchartIdx;
    const dHeight = current.subCharts[subchartIdx].bottom - current.subCharts[subchartIdx].top;
    const dHeightEach = Math.floor(dHeight / (current.subCharts.length - 1));
    const dHeightRest = dHeight % (current.subCharts.length - 1);
    return {
      ...current,
      subCharts: [
        ...current.subCharts.slice(0, subchartIdx).map((subchartSliced, subchartSlicedIdx) => ({
          ...subchartSliced,
          top: subchartSliced.top === 0 ? 0 : subchartSliced.top + dHeightRest + (subchartIdx - 2) * dHeightEach,
          bottom:
            subchartSlicedIdx === 0
              ? subchartSliced.bottom + dHeightEach + dHeightRest
              : subchartSliced.bottom + dHeightEach + dHeightRest + (subchartIdx - 1) * dHeightEach,
        })),
        ...current.subCharts.slice(subchartIdx + 1).map((subchartSliced, subchartSlicedIdx) => ({
          ...subchartSliced,
          top: subchartSliced.top - (subchartSlicedIdx + 1) * dHeightEach,
          bottom:
            subchartSlicedIdx === current.subCharts.slice(subchartIdx + 1).length - 1
              ? subchartSliced.bottom
              : subchartSliced.bottom - (subchartSlicedIdx + 1) * dHeightEach,
        })),
      ],
    };
  } else if (isRemoveGraphAction(action)) {
    const { subchartIdx, yaxisIdx, graphIdx } = action.params;
    if (graphIdx === 0 && current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.length === 1) {
      // do same like removeSubchart
      const dHeight = current.subCharts[subchartIdx].bottom - current.subCharts[subchartIdx].top;
      const dHeightEach = Math.floor(dHeight / (current.subCharts.length - 1));
      const dHeightRest = dHeight % (current.subCharts.length - 1);
      return {
        ...current,
        subCharts: [
          ...current.subCharts.slice(0, subchartIdx).map((subchartSliced, subchartSlicedIdx) => ({
            ...subchartSliced,
            top: subchartSliced.top === 0 ? 0 : subchartSliced.top + dHeightRest + (subchartIdx - 2) * dHeightEach,
            bottom:
              subchartSlicedIdx === 0
                ? subchartSliced.bottom + dHeightEach + dHeightRest
                : subchartSliced.bottom + dHeightEach + dHeightRest + (subchartIdx - 1) * dHeightEach,
          })),
          ...current.subCharts.slice(subchartIdx + 1).map((subchartSliced, subchartSlicedIdx) => ({
            ...subchartSliced,
            top: subchartSliced.top - (subchartSlicedIdx + 1) * dHeightEach,
            bottom:
              subchartSlicedIdx === current.subCharts.slice(subchartIdx + 1).length - 1
                ? subchartSliced.bottom
                : subchartSliced.bottom - (subchartSlicedIdx + 1) * dHeightEach,
          })),
        ],
      };
    } else {
      return {
        ...current,
        subCharts: [
          ...current.subCharts.slice(0, subchartIdx),
          {
            ...current.subCharts[subchartIdx],
            yaxis: [
              ...current.subCharts[subchartIdx].yaxis.slice(0, yaxisIdx),
              {
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx],
                graphs: [
                  ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(0, graphIdx),
                  // { ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx] },
                  ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(graphIdx + 1),
                ],
              },
              ...current.subCharts[subchartIdx].yaxis.slice(yaxisIdx + 1),
            ],
          },
          ...current.subCharts.slice(subchartIdx + 1),
        ],
      };
    }
  } else if (isClearChartAction(action)) {
    const mode = action.params.mode;
    if (mode === "all") {
      return {
        ...current,
        subCharts: [
          {
            ...current.subCharts[0],
            yaxis: [{ ...current.subCharts[0].yaxis[0], graphs: [current.subCharts[0].yaxis[0].graphs[0]], tools: [] }],
          },
        ],
      };
    } else if (mode === "indicators") {
      return {
        ...current,
        subCharts: [
          {
            ...current.subCharts[0],
            yaxis: [{ ...current.subCharts[0].yaxis[0], graphs: [current.subCharts[0].yaxis[0].graphs[0]] }],
            top: 0,
            bottom: current.containerSize.height - 1 - current.xaxis.heightXAxis,
          },
        ],
      };
    } else if (mode === "tools") {
      return {
        ...current,
        subCharts: current.subCharts.map((subchart, subchartIdx) => ({
          ...subchart,
          yaxis: subchart.yaxis.map((yaxis, yaxisIdx) => ({ ...yaxis, tools: [] })),
        })),
      };
    }
  } else if (isAddGraphAction(action)) {
    const chartSeries = action.params.chartSeries;
    const chartName = action.params.chartName;
    const indicator = !!action.params.indicator ? action.params.indicator : null;
    const subchartIdx = action.params.subchartIdx;
    // const style = {
    //   strokeColor: getGraphColors(graphColorsLight, current.subCharts[subchartIdx].yaxis[0].graphs.length),
    // };
    const style = !!indicator
      ? { strokeColor: [getGraphColors(graphColorsLight, current.subCharts[subchartIdx].yaxis[0].graphs.length)] }
      : { strokeColor: getGraphColors(graphColorsLight, current.subCharts[subchartIdx].yaxis[0].graphs.length) };

    const newGraph = !!indicator
      ? createIndicatorGraphModel(
          chartSeries as ChartDataSeries,
          chartName,
          indicator,
          style as unknown as IndicatorGraphStateType["style"]
        )
      : createChartGraphModel(chartSeries as ChartDataSeries, chartName, style as ChartGraphStateType["style"]);
    const graph = newGraph as GraphStateType;
    if (isIndicatorGraph(graph) && graph.data[newGraph.data.length - 1].prices.length > 1)
      (graph as unknown as IndicatorGraphStateType).style.strokeColor = graph.data[newGraph.data.length - 1].prices.map(
        (x, idx) => getGraphColors(graphColorsLight, idx)
      );

    return {
      ...current,
      subCharts: [
        ...current.subCharts.map((subChart, subChartIdx) => {
          if (subChartIdx === subchartIdx)
            return {
              ...subChart,
              yaxis: [{ ...subChart.yaxis[0], graphs: [...subChart.yaxis[0].graphs, { ...(newGraph as any) }] }],
            };
          else return subChart;
        }),
      ],
    };
  } else if (isDrawAction(action)) {
    const xy = action.params.xy;
    const subchartIdx = action.params.subchartIdx;
    const defaultDrawState: ChartStateType["draw"] = { isDrawing: false, pixXy: [] };
    const nPoints = !action.params.type
      ? 0
      : ["hline", "vline"].includes(action.params.type)
      ? 1
      : ["trendline"].includes(action.params.type)
      ? 2
      : 0;
    // console.log("HI", xy, subchartIdx, nPoints, action);

    if (!nPoints || !action.params.type) {
      return { ...current, draw: defaultDrawState };
    }
    // init draw command -> switch to draw mode
    else if (!xy && !!action.params.type) {
      return { ...current, draw: { isDrawing: true, pixXy: [], type: action.params.type } };
    }
    return current;
  } else if (isSetBackgroundColorAction(action)) {
    const color = action.params.color;
    return { ...current, backgroundColor: color };
  } else if (isSetGridStrokeColorAction(action)) {
    const color = action.params.color;
    return {
      ...current,
      grid: { ...current.grid, style: { ...current.grid.style, strokeColor: color } },
    };
  } else if (isSetAxisFillColorAction(action)) {
    const color = action.params.color;
    return {
      ...current,
      xaxis: {
        ...current.xaxis,
        style: { ...current.xaxis.style, fillColor: color },
      },
      yaxis: { ...current.yaxis, style: { ...current.yaxis.style, fillColor: color } },
    };
  } else if (isSetAxisStrokeColorAction(action)) {
    const color = action.params.color;
    return {
      ...current,
      xaxis: {
        ...current.xaxis,
        style: { ...current.xaxis.style, strokeColor: color },
      },
      yaxis: { ...current.yaxis, style: { ...current.yaxis.style, strokeColor: color } },
    };
  } else if (isSetAxisTextColorAction(action)) {
    const color = action.params.color;
    return {
      ...current,
      xaxis: {
        ...current.xaxis,
        style: { ...current.xaxis.style, fontColor: color },
      },
      yaxis: { ...current.yaxis, style: { ...current.yaxis.style, fontColor: color } },
    };
  } else if (isToggleXaxisGridAction(action)) {
    return {
      ...current,
      grid: { ...current.grid, useGridX: !current.grid.useGridX },
    };
  } else if (isToggleYaxisGridAction(action)) {
    return {
      ...current,
      grid: { ...current.grid, useGridY: !current.grid.useGridY },
    };
  } else if (isToggleFullscreenAction(action)) {
    return { ...current, fullscreen: !current.fullscreen };
  } else if (isSetLineChartStrokeColor(action)) {
    const subchartIdx = action.params.subchartIdx;
    const yaxisIdx = action.params.yaxisIdx;
    const graphIdx = action.params.graphIdx;
    const color = action.params.color;
    const graph = current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx];
    return {
      ...current,
      subCharts: [
        ...current.subCharts.slice(0, subchartIdx),
        {
          ...current.subCharts[subchartIdx],
          yaxis: [
            {
              ...current.subCharts[subchartIdx].yaxis[yaxisIdx],
              graphs: [
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(0, graphIdx),
                {
                  ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx],
                  style: {
                    ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx].style,
                    strokeColor: [color],
                  },
                },
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(graphIdx + 1),
              ],
            },
          ],
        },
        ...current.subCharts.slice(subchartIdx + 1),
      ],
    };
  } else if (
    isSetCandleChartUpColor(action) ||
    isSetCandleChartDownColor(action) ||
    isSetCandleChartStrokeColor(action)
  ) {
    const subchartIdx = action.params.subchartIdx;
    const yaxisIdx = action.params.yaxisIdx;
    const graphIdx = action.params.graphIdx;
    const color = action.params.color;
    const prop = isSetCandleChartUpColor(action)
      ? "candleUpColor"
      : isSetCandleChartDownColor(action)
      ? "candleDownColor"
      : isSetCandleChartStrokeColor(action)
      ? "candleStrokeColor"
      : null;
    if (!prop) return current;
    return {
      ...current,
      subCharts: [
        ...current.subCharts.slice(0, subchartIdx),
        {
          ...current.subCharts[subchartIdx],
          yaxis: [
            {
              ...current.subCharts[subchartIdx].yaxis[yaxisIdx],
              graphs: [
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(0, graphIdx),
                {
                  ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx],
                  style: {
                    ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx].style,
                    [prop]: color,
                  },
                },
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(graphIdx + 1),
              ],
            },
          ],
        },
        ...current.subCharts.slice(subchartIdx + 1),
      ],
    };
  } else if (isSetToolStrokeColorAction(action) || isSetToolAnchorColor(action)) {
    const subchartIdx = action.params.subchartIdx;
    const yaxisIdx = action.params.yaxisIdx;
    const graphIdx = action.params.toolIdx;
    const color = action.params.color;
    const prop = isSetToolStrokeColorAction(action)
      ? "strokeColor"
      : isSetToolAnchorColor(action)
      ? "anchorColor"
      : null;
    if (!prop) return current;
    return {
      ...current,
      subCharts: [
        ...current.subCharts.slice(0, subchartIdx),
        {
          ...current.subCharts[subchartIdx],
          yaxis: [
            {
              ...current.subCharts[subchartIdx].yaxis[yaxisIdx],
              tools: [
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].tools.slice(0, graphIdx),
                {
                  ...current.subCharts[subchartIdx].yaxis[yaxisIdx].tools[graphIdx],
                  style: {
                    ...current.subCharts[subchartIdx].yaxis[yaxisIdx].tools[graphIdx].style,
                    [prop]: color,
                  },
                },
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].tools.slice(graphIdx + 1),
              ],
            },
          ],
        },
        ...current.subCharts.slice(subchartIdx + 1),
      ],
    };
  } else if (isSetIndicatorParam(action)) {
    const subchartIdx = action.params.subchartIdx;
    const yaxisIdx = action.params.yaxisIdx;
    const graphIdx = action.params.graphIdx;
    const paramIdx = action.params.paramIdx;
    const val = action.params.val;

    const indicatorGraph = current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs[graphIdx];

    if (!isIndicatorGraph(indicatorGraph)) return current;
    const fn = indicatorGraph.indicator.indicatorFn;
    const srcData = current.subCharts[0].yaxis[0].graphs[0].data as ChartDataSeries;
    let params = indicatorGraph.indicator.params.map((val) => val.val);
    params[paramIdx] = val;
    const indicatorData = fn(srcData, indicatorGraph.name, ...params);
    // console.log(
    //   "HI!! HII!",
    //   val,
    //   subchartIdx,
    //   yaxisIdx,
    //   graphIdx,
    //   paramIdx,
    //   isIndicatorGraph(indicatorGraph),
    //   params,
    //   indicatorData,
    //   {
    //     ...indicatorGraph.indicator,
    //     params: [
    //       ...indicatorGraph.indicator.params.slice(0, paramIdx),
    //       {
    //         ...indicatorGraph.indicator.params[paramIdx],
    //         val: val,
    //       },
    //       ...indicatorGraph.indicator.params.slice(paramIdx + 1),
    //     ],
    //   }
    // );
    // const newIndicatorGraph = { ...indicatorGraph };
    // newIndicatorGraph.data = indicatorData;
    // newIndicatorGraph.indicator.params[paramIdx].val = val;
    return {
      ...current,
      subCharts: [
        ...current.subCharts.slice(0, subchartIdx),
        {
          ...current.subCharts[subchartIdx],
          yaxis: [
            {
              ...current.subCharts[subchartIdx].yaxis[yaxisIdx],
              graphs: [
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(0, graphIdx),
                {
                  ...indicatorGraph,
                  data: indicatorData,
                  indicator: {
                    ...indicatorGraph.indicator,
                    params: [
                      ...indicatorGraph.indicator.params.slice(0, paramIdx),
                      {
                        ...indicatorGraph.indicator.params[paramIdx],
                        val: val,
                      },
                      ...indicatorGraph.indicator.params.slice(paramIdx + 1),
                    ],
                  },
                } as any,
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].graphs.slice(graphIdx + 1),
              ],
            },
          ],
        },
        ...current.subCharts.slice(subchartIdx + 1),
      ],
    };
  } else if (isSetHlineYlevelAction(action)) {
    const subchartIdx = action.params.subchartIdx;
    const yaxisIdx = action.params.yaxisIdx;
    const toolIdx = action.params.toolIdx;
    const val = action.params.val as number;
    const anchorX = current.subCharts[subchartIdx].yaxis[yaxisIdx].tools[toolIdx].xy[0][0];
    const newAnchor: [number, number] = [anchorX, val];
    return {
      ...current,
      subCharts: [
        ...current.subCharts.slice(0, subchartIdx),
        {
          ...current.subCharts[subchartIdx],
          yaxis: [
            {
              ...current.subCharts[subchartIdx].yaxis[yaxisIdx],
              tools: [
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].tools.slice(0, toolIdx),
                { ...current.subCharts[subchartIdx].yaxis[yaxisIdx].tools[toolIdx], xy: [newAnchor] },
                ...current.subCharts[subchartIdx].yaxis[yaxisIdx].tools.slice(toolIdx + 1),
              ],
            },
          ],
        },
        ...current.subCharts.slice(subchartIdx + 1),
      ],
    };
  } else if (isUpdateInteractionState(action)) {
    // const nSubcharts = current.subCharts.length;
    return {
      ...current,
      pointer: action.params.newState.pointer,
      containerSize: action.params.newState.containerSize,
      // xaxis: { ...current.xaxis, ...action.params.newState.xaxis },
      subCharts: action.params.newState.subCharts,
      draw: { ...current.draw, ...action.params.newState.draw },
      xaxis: { ...current.xaxis, ...action.params.newState.xaxis },
    };
  }

  return current;
};
