import { Icon } from "@mdi/react";
import { ToggleButton, ToggleButtonGroup, TreeView } from "@material-ui/lab";
import {
  mdiMenuDown,
  mdiMenuRight,
  mdiDiameterVariant,
  mdiChartBellCurve,
  mdiArrowExpandVertical,
  mdiPoll,
  mdiHome,
  mdiFilter,
  mdiMagnify,
  mdiAlphabetical,
  mdiSortAlphabeticalAscending,
  mdiRuler,
  mdiRulerSquareCompass,
  mdiFileChartOutline,
  mdiWater,
  mdiArrowSplitHorizontal,
  mdiBorderColor,
  mdiBullseye,
  mdiChartAreaspline,
  mdiChartLine,
  mdiClose,
  mdiFormatText,
  mdiGrid,
  mdiHelp,
  mdiIframeVariableOutline,
  mdiMinus,
  mdiPencilRuler,
} from "@mdi/js";
import React from "react";

import {
  CalculatedChartStateType,
  ChartStateHookType,
  isChartGraph,
  isIndicatorGraph,
} from "../Types/CChartStateTypes";
import { ChartDataSeries } from "../Types/CChartTypes";
import { Button, Checkbox, Grid, IconButton, Menu, MenuItem, TextField, Typography, useTheme } from "@material-ui/core";
import { CTreeItem } from "../CTreeItem";
import { ReducerActionType } from "../ChartState/CChartStateReducer";
import { Colorpicker } from "../Colorpicker";
import { CChartMenuStateType } from "./ChartMenu";
import { CIndicatorGraphTreeItem } from "./ChartMenuIndicators";
import { CToolTreeItem } from "./ChartMenuTools";

const onToolLabelClick = (
  ChartStateDispatch: ChartStateHookType[1],
  type: NonNullable<CalculatedChartStateType["draw"]["type"]>
) => {
  const subchartIdx = 0;
  const action: ReducerActionType<"draw"> = { task: "draw", params: { type, subchartIdx } };
  return () => {
    (ChartStateDispatch as React.Dispatch<ReducerActionType<"draw">>)(action);
  };
};

export type ToolCategoryType = "Lines" | "Upcoming";

export const ChartMenuSettings = (props: {
  ChartMenuState: CChartMenuStateType;
  ChartState: CalculatedChartStateType;
  onNavigate: (target: CChartMenuStateType["location"]) => void;
  ChartStateDispatch: ChartStateHookType[1];
  onSettingsExpand: (id: string) => void;
}) => {
  const { ChartState, onNavigate, ChartStateDispatch, onSettingsExpand, ChartMenuState } = props;
  const theme = useTheme();
  const [FilterCat, setFilterCat] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

  // const [Expanded, setExpanded] = React.useState<string[]>([]);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (target?: CChartMenuStateType["location"]) => {
    setAnchorEl(null);
    if (!!target) onNavigate?.(target);
  };

  // const handleToggleExpanded = (id: string) => {
  //   if (Expanded.includes(id)) {
  //     setExpanded(Expanded.filter((val) => val !== id));
  //   } else setExpanded([...Expanded, id]);
  // };

  return (
    <React.Fragment>
      <div
        style={{
          position: "absolute",
          right: 24, //see padding title bar
          zIndex: 2,

          // padding
        }}
      >
        {/* <div
          style={{
            background: theme.palette.secondary.light,
            border: "1px solid #666",
            borderRadius: 50,
            padding: 5,
          }}
        >
          <IconButton
            size="small"
            style={{ border: "1px solid #fff", margin: "0px 5px" }}
            // onClick={() => onNavigate("menu")}
          >
            <Icon path={mdiMagnify} size={1} color={theme.palette.primary.contrastText} />
          </IconButton>
          <IconButton
            size="small"
            style={{ border: "1px solid #fff", margin: "0px 5px 0px 0px" }}
            onClick={() => setFilterCat((current) => !current)}
          >
            <Icon
              path={FilterCat ? mdiSortAlphabeticalAscending : mdiFilter}
              size={1}
              color={theme.palette.primary.contrastText}
            />
          </IconButton>
        </div> */}
        <div style={{ paddingBottom: 5 }}>
          <Button
            // value="web"
            style={{
              border: "1px solid #666",
              borderRadius: 50,
              padding: 5,
              // marginTop: 5,
              textTransform: "none",
              background: theme.palette.secondary.light,
              width: "100%",
              color: theme.palette.secondary.contrastText,
            }}
            onClick={handleClick}
          >
            <Typography> Add...</Typography>
          </Button>
          <Menu
            id="quick-add"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={() => handleClose(undefined)}
            anchorOrigin={{
              vertical: "top",
              horizontal: "center",
            }}
            transformOrigin={{
              vertical: "bottom",
              horizontal: "center",
            }}
          >
            <MenuItem onClick={() => handleClose("indicators")}>Indicator</MenuItem>
            <MenuItem onClick={() => handleClose("tools")}>Tool</MenuItem>
            {/* <MenuItem onClick={handleClose}></MenuItem> */}
          </Menu>
        </div>
      </div>

      <TreeView
        //   className={classes.root}
        //   defaultExpanded={["3"]}
        defaultCollapseIcon={<Icon path={mdiMenuDown} size={1} color="#333" />}
        defaultExpandIcon={<Icon path={mdiMenuRight} size={1} color="#333" />}
        defaultEndIcon={<div style={{ width: 24 }} />}
        //   selected="1"
        //   onNodeSelect={() => {}}
        disableSelection
        // style={{height: 100, overflowY: "scroll"}}
        expanded={ChartMenuState.expandedSetting}
      >
        <CTreeItem
          nodeId="1"
          labelText="General"
          labelIcon={
            <div
              style={{
                background: theme.palette.secondary.main,
                width: 48,
                height: 48,
                border: "1px solid #666",
                borderRadius: 5,
                marginRight: 10,
              }}
            >
              <Icon path={mdiFileChartOutline} size={2} color={theme.palette.secondary.contrastText} />
            </div>
          }
          bgColor={theme.palette.primary.light}
          labelTypographyVariant="h6"
          onLabelClick={() => {
            const id = "1";
            onSettingsExpand?.(id);
          }}
        >
          <CTreeItem
            nodeId="11"
            labelText="background color"
            labelIcon={<Icon path={mdiWater} size={1} color="#333" />}
            labelInfo={
              <Colorpicker
                color={ChartState.backgroundColor}
                onColorSelected={(color) => {
                  // const action: ReducerActionType<"setBackgroundColor"> = {
                  //   task: "setBackgroundColor",
                  //   params: { color: color },
                  // };
                  ChartStateDispatch({
                    task: "setBackgroundColor",
                    params: { color },
                  } as ReducerActionType<"setBackgroundColor">);
                }}
              />
            }
            //   color="#fff"
            bgColor="#eee"
          />
          <CTreeItem
            nodeId="12"
            labelText="axis fill color"
            labelIcon={<Icon path={mdiWater} size={1} color="#333" />}
            labelInfo={
              <Colorpicker
                color={ChartState.xaxis.style.fillColor}
                onColorSelected={(color) => {
                  const action: ReducerActionType<"setAxisFillColor"> = {
                    task: "setAxisFillColor",
                    params: { color: color },
                  };
                  (ChartStateDispatch as React.Dispatch<ReducerActionType<"setAxisFillColor">>)(action);
                }}
              />
            }
            //   color="#e3742f"
            bgColor="#eee"
          />
          <CTreeItem
            nodeId="13"
            labelText="axis stroke color"
            labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
            labelInfo={
              <Colorpicker
                color={ChartState.xaxis.style.strokeColor}
                onColorSelected={(color) => {
                  const action: ReducerActionType<"setAxisStrokeColor"> = {
                    task: "setAxisStrokeColor",
                    params: { color: color },
                  };
                  (ChartStateDispatch as React.Dispatch<ReducerActionType<"setAxisStrokeColor">>)(action);
                }}
              />
            }
            //   color="#a250f5"
            bgColor="#eee"
          />
          <CTreeItem
            nodeId="14"
            labelText="axis text color"
            labelIcon={<Icon path={mdiFormatText} size={1} color="#333" />}
            labelInfo={
              <Colorpicker
                color={ChartState.xaxis.style.fontColor}
                onColorSelected={(color) => {
                  const action: ReducerActionType<"setAxisTextColor"> = {
                    task: "setAxisTextColor",
                    params: { color: color },
                  };
                  (ChartStateDispatch as React.Dispatch<ReducerActionType<"setAxisTextColor">>)(action);
                }}
              />
            }
            color="#3c8039"
            bgColor="#eee"
          />
          <CTreeItem
            nodeId="15"
            labelText="use x-axis grid"
            labelIcon={<Icon path={mdiGrid} size={1} color="#333" />}
            labelInfo={
              <Checkbox
                checked={ChartState.grid.useGridX}
                size="small"
                style={{ padding: 0, width: 24, height: 24 }}
                onChange={() => {
                  const action: ReducerActionType<"toggleXaxisGrid"> = {
                    task: "toggleXaxisGrid",
                    params: {},
                  };
                  (ChartStateDispatch as React.Dispatch<ReducerActionType<"toggleXaxisGrid">>)(action);
                }}
              />
            }
            color="#3c8039"
            bgColor="#eee"
          />
          <CTreeItem
            nodeId="16"
            labelText="use y-axis grid"
            labelIcon={<Icon path={mdiGrid} size={1} color="#333" />}
            labelInfo={
              <Checkbox
                checked={ChartState.grid.useGridY}
                size="small"
                style={{ padding: 0, width: 24, height: 24 }}
                onChange={() => {
                  const action: ReducerActionType<"toggleYaxisGrid"> = {
                    task: "toggleYaxisGrid",
                    params: {},
                  };
                  (ChartStateDispatch as React.Dispatch<ReducerActionType<"toggleYaxisGrid">>)(action);
                }}
              />
            }
            color="#3c8039"
            bgColor="#eee"
          />
          <CTreeItem
            nodeId="17"
            labelText="grid stroke color"
            labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
            labelInfo={
              <Colorpicker
                color={ChartState.grid.style.strokeColor}
                onColorSelected={(color) => {
                  const action: ReducerActionType<"setGridStrokeColor"> = {
                    task: "setGridStrokeColor",
                    params: { color: color },
                  };
                  (ChartStateDispatch as React.Dispatch<ReducerActionType<"setGridStrokeColor">>)(action);
                }}
              />
            }
            color="#3c8039"
            bgColor="#eee"
          />
        </CTreeItem>

        {ChartState.subCharts.map((subchart, subchartIdx) => (
          <CTreeItem
            labelTypographyVariant="h6"
            key={`sub-${subchartIdx}`}
            nodeId={`sub-${subchartIdx}`}
            labelText={subchartIdx === 0 ? "Mainchart" : `Subchart ${subchartIdx}`}
            bgColor={theme.palette.primary.light}
            onLabelClick={() => {
              const id = `sub-${subchartIdx}`;
              onSettingsExpand?.(id);
            }}
            labelIcon={
              <div
                style={{
                  background: theme.palette.secondary.main,
                  width: 48,
                  height: 48,
                  border: "1px solid #666",
                  borderRadius: 5,
                  marginRight: 10,
                }}
              >
                <Icon path={mdiChartAreaspline} size={2} color={theme.palette.secondary.contrastText} />
              </div>
            }
            labelInfo={
              subchartIdx !== 0 ? (
                <IconButton
                  size="small"
                  onClick={(e) => {
                    e.preventDefault();
                    const action: ReducerActionType<"removeSubchart"> = {
                      task: "removeSubchart",
                      params: { subchartIdx },
                    };
                    (ChartStateDispatch as React.Dispatch<ReducerActionType<"removeSubchart">>)(action);
                  }}
                >
                  <Icon path={mdiClose} size={1} color="#333" />
                </IconButton>
              ) : undefined
            }
          >
            {subchart.yaxis[0].graphs.map((graph, graphIdx) =>
              isIndicatorGraph(graph) ? (
                <CIndicatorGraphTreeItem
                  key={`editIndicator-sub-${subchartIdx}-yaxis-${0}-graph-${graphIdx}`}
                  ChartState={ChartState}
                  ChartStateDispatch={ChartStateDispatch}
                  subchartIdx={subchartIdx}
                  yaxisIdx={0}
                  graphIdx={graphIdx}
                  handleToggleExpanded={onSettingsExpand}
                />
              ) : isChartGraph(graph) ? (
                <React.Fragment>
                  <CTreeItem
                    onLabelClick={() => {
                      const id = `graph-sub-${subchartIdx}-yaxis-${0}-graph-${graphIdx}`;
                      onSettingsExpand?.(id);
                    }}
                    key={`graph-sub-${subchartIdx}-y-${0}-graph-${graphIdx}`}
                    nodeId={`graph-sub-${subchartIdx}-y-${0}-graph-${graphIdx}`}
                    labelText={graph.name}
                    bgColor="#eee"
                    labelIcon={
                      <div
                        style={{
                          background: theme.palette.primary.main,
                          width: 32,
                          height: 32,
                          border: "1px solid #666",
                          borderRadius: 5,
                          marginRight: 10,
                        }}
                      >
                        <Icon path={mdiChartLine} size={"32px"} color={theme.palette.secondary.contrastText} />
                      </div>
                    }
                    labelInfo={
                      subchartIdx !== 0 || graphIdx !== 0 ? (
                        <IconButton
                          size="small"
                          onClick={(e) => {
                            e.preventDefault();
                            const action: ReducerActionType<"removeGraph"> = {
                              task: "removeGraph",
                              params: { subchartIdx, yaxisIdx: 0, graphIdx },
                            };
                            (ChartStateDispatch as React.Dispatch<ReducerActionType<"removeGraph">>)(action);
                          }}
                        >
                          <Icon path={mdiClose} size={1} color="#333" />
                        </IconButton>
                      ) : undefined
                    }
                  >
                    {graph.meta.type === "linechart" ? (
                      <CTreeItem
                        nodeId={`${2 + subchartIdx}00-${graphIdx}`}
                        labelText="line stroke color"
                        labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
                        labelInfo={
                          <Colorpicker
                            color={ChartState.subCharts[subchartIdx].yaxis[0].graphs[graphIdx].style?.strokeColor?.[0]}
                            onColorSelected={(color) => {
                              const action: ReducerActionType<"setLineChartStrokeColor"> = {
                                task: "setLineChartStrokeColor",
                                params: { color: color, subchartIdx, yaxisIdx: 0, graphIdx },
                              };
                              (ChartStateDispatch as React.Dispatch<ReducerActionType<"setLineChartStrokeColor">>)(
                                action
                              );
                            }}
                          />
                        }
                      />
                    ) : graph.meta.type === "candlechart" ? (
                      <React.Fragment>
                        <CTreeItem
                          nodeId={`${2 + subchartIdx}00-${graphIdx}`}
                          labelText="candle up color"
                          labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
                          labelInfo={
                            <Colorpicker
                              color={graph.style.candleUpColor}
                              onColorSelected={(color) => {
                                const action: ReducerActionType<"setCandleChartUpColor"> = {
                                  task: "setCandleChartUpColor",
                                  params: { color: color, subchartIdx, yaxisIdx: 0, graphIdx },
                                };
                                (ChartStateDispatch as React.Dispatch<ReducerActionType<"setCandleChartUpColor">>)(
                                  action
                                );
                              }}
                            />
                          }
                        />
                        <CTreeItem
                          nodeId={`${2 + subchartIdx}00-${graphIdx}`}
                          labelText="candle down color"
                          labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
                          labelInfo={
                            <Colorpicker
                              color={graph.style.candleDownColor}
                              onColorSelected={(color) => {
                                const action: ReducerActionType<"setCandleChartDownColor"> = {
                                  task: "setCandleChartDownColor",
                                  params: { color: color, subchartIdx, yaxisIdx: 0, graphIdx },
                                };
                                (ChartStateDispatch as React.Dispatch<ReducerActionType<"setCandleChartDownColor">>)(
                                  action
                                );
                              }}
                            />
                          }
                        />
                        <CTreeItem
                          nodeId={`${2 + subchartIdx}00-${graphIdx}`}
                          labelText="candle stroke color"
                          labelIcon={<Icon path={mdiBorderColor} size={1} color="#333" />}
                          labelInfo={
                            <Colorpicker
                              color={graph.style.candleStrokeColor}
                              onColorSelected={(color) => {
                                const action: ReducerActionType<"setCandleChartStrokeColor"> = {
                                  task: "setCandleChartStrokeColor",
                                  params: { color: color, subchartIdx, yaxisIdx: 0, graphIdx },
                                };
                                (ChartStateDispatch as React.Dispatch<ReducerActionType<"setCandleChartStrokeColor">>)(
                                  action
                                );
                              }}
                            />
                          }
                        />
                      </React.Fragment>
                    ) : null}
                  </CTreeItem>
                </React.Fragment>
              ) : null
            )}

            {subchart.yaxis[0].tools.length > 0 ? (
              <CTreeItem
                key={`tools-${subchartIdx}-y0`}
                nodeId={`tools-${subchartIdx}-y0`}
                labelIcon={
                  <div
                    style={{
                      background: theme.palette.primary.main,
                      width: 32,
                      height: 32,
                      border: "1px solid #666",
                      borderRadius: 5,
                      marginRight: 10,
                    }}
                  >
                    <Icon path={mdiPencilRuler} size={"32px"} color={theme.palette.secondary.contrastText} />
                  </div>
                }
                labelText="Tools"
                onLabelClick={() => {
                  const id = `tools-${subchartIdx}-y0`;
                  onSettingsExpand?.(id);
                }}
                bgColor="#eee"
              >
                {subchart.yaxis[0].tools.map((tool, toolIdx) => (
                  <CToolTreeItem
                    key={`editIndicator-sub-${subchartIdx}-yaxis-${0}-tool-${toolIdx}`}
                    ChartState={ChartState}
                    ChartStateDispatch={ChartStateDispatch}
                    subchartIdx={subchartIdx}
                    yaxisIdx={0}
                    toolIdx={toolIdx}
                    handleToggleExpanded={onSettingsExpand}
                  />
                ))}
              </CTreeItem>
            ) : null}
          </CTreeItem>
        ))}
      </TreeView>
    </React.Fragment>
  );
};
